import React, { FC, useEffect, useReducer, useRef } from "react";
import { useHistory, RouteComponentProps } from "react-router-dom";
import { useQuery } from "react-query";
import ReactModal from "react-modal";

// styles
import {
  Form,
  CopyContainer,
  CopyIconWrapper,
  OTPCopyTitle,
  OTPCopyBtn,
  Warning,
  CreditLegend,
  FetchingSpinnerContainer,
  UpperOTPInput,
  ErrorMsg,
  LowerOTPWrapper,
  NewOTPWrapper,
} from "./FormOTP.styled";
import { theme } from "../../styles/theme";
import {
  FormHeader,
  FormTitle,
  TitleIconWrapper,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";

// components
import ContinueBtn from "../ui/PrimaryBtn";
import OTPInput from "../ui/OTPInput";
import MobileOTPModal from "../MobileOTPModal";
import SmallSpinner from "../ui/SmallSpinner";
import RejectionMessage from "../RejectionMensage";
import BoureauLegend from "../ui/BoureauLegend";
import NewOTP from "../ui/SecondaryBtn";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// icons
import { ReactComponent as CellphoneIcon } from "../../assets/icons/CellphoneIcon.svg";
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";
import { ReactComponent as CopyIcon } from "../../assets/icons/CopyIcon.svg";

// hooks
import useMediaQuery from "../../hooks/useMedia";

// utils
import { defaultState, validationReducer } from "./validation.reducer";
import { flasher } from "../../utils/Flasher";
import {
  verifyOTPEndpoint,
  requestOTP,
  validateOTP,
  validateLowerOTP,
  validateUpperOTP,
} from "./FormOTP.utils";

ReactModal.setAppElement("body");

const FormOTP: FC<RouteComponentProps> = ({ match }) => {
  const isMobile = useMediaQuery(theme.mediaquerys.mobile);
  const otpCopyRef = useRef<null | HTMLInputElement>(null);
  const [
    {
      isMobileModalOpen,
      showOTPExceed,
      numberOfTrials,
      upperOTP,
      upperOTPValidation,
      lowerOTPValidation,
      bureauLegendOpen,
      secsUntilOtp,
      disableOtpBtn,
    },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);
  const {
    step,
    partner,
    ctePhone,
    idProdType,
    currentStep,
    otpValue,
  } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const history = useHistory();
  useQuery("verifyOTPEndpoint", verifyOTPEndpoint, {
    refetchOnWindowFocus: false,
    onError: (error) => {
      console.error(error);
      flasher("Algo sali mal, intente ms tarde.", "error");
    },
    onSuccess: (isAvailable) => {
      if (!isAvailable) {
        flasher(
          "El servicio de SMS no se encuentra disponible, intente ms tarde.",
          "warning"
        );
      }
    },
  });
  const { refetch: requestNewOTP } = useQuery(
    ["requestOTP", ctePhone, idProdType, partner],
    requestOTP,
    {
      retry: false,
      enabled: false,
      onError: (error) => {
        console.error(error);
        flasher("Algo sali mal, intente ms tarde.", "error");
      },
      onSuccess: (resCode) => {
        if (resCode !== 200) {
          flasher(
            "El servicio de SMS no se encuentra disponible, intente ms tarde.",
            "warning"
          );
          return;
        }
        flasher("Mensaje enviado a tu celular con xito.", "success");
      },
    }
  );
  const { refetch: validateOTPInput, isLoading } = useQuery(
    ["validateOTP", otpValue],
    validateOTP,
    {
      enabled: false,
      retry: false,
      onError: (error) => {
        console.error(error);
        flasher("Algo sali mal, intente ms tarde.", "error");
      },
      onSuccess: (validationRes: { celular: string; vigente: string }) => {
        if (validationRes.celular !== ctePhone) {
          flasher(
            "El nmero celular no coincide con el que gener el OTP",
            "warning"
          );
          return validationDispatcher({ type: "INCREMENT_NUMBER_TRIALS" });
        } else if (validationRes.vigente === "0") {
          flasher("El nmero OTP ha caducado", "warning");
          return;
        } else if (
          validationRes.celular === ctePhone &&
          validationRes.vigente === "1"
        ) {
          mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.4 });
          history.push(match.url.replace("/otp", "/id"));
        }
      },
    }
  );

  useEffect(() => {
    if (numberOfTrials > 4) {
      validationDispatcher({ type: "DISPLAY_OTP_EXCEDED" });
    }
  }, [numberOfTrials]);

  useEffect(() => {
    if (step < 0.3) {
      history.goBack();
    }
  }, [step, history]);

  useEffect(() => {
    if (currentStep <= 0.3) {
      requestNewOTP();
    }
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.3 });
  }, [mainFormDispatcher]);

  useEffect(() => {
    if (otpCopyRef.current) {
      otpCopyRef.current.focus();
    }
  }, [otpCopyRef]);

  useEffect(() => {
    const otpInterval = setInterval(() => {
      if (secsUntilOtp === 0) {
        validationDispatcher({ type: "TOGGLE_NEWOTP_BTN" });
        clearInterval(otpInterval);
      } else {
        validationDispatcher({ type: "ONE_SEC_PASS" });
      }
    }, 1000);
    return () => clearInterval(otpInterval);
  }, [secsUntilOtp]);

  const verifyOTP = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    validateOTPInput();
  };

  const handleCopyOTP = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    if (otpCopyRef.current) {
      otpCopyRef.current.select();
      otpCopyRef.current.setSelectionRange(0, 99999);
      document.execCommand("copy");
      flasher("OTP Copiado", "info");
    }
  };

  const handleUpperOTPChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;
    const lastChar = value.charAt(value.length - 1);
    if ((!value || /[0-9]/.test(lastChar)) && value.length <= 6) {
      validationDispatcher({ type: "SET_UPPEROTP", otpValue: value });
    }
  };

  const handleBlur = (
    validationString: "SET_UPPEROTP_VALIDATION" | "SET_LOWEROTP_VALIDATION"
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    let validation;

    if (validationString === "SET_UPPEROTP_VALIDATION") {
      validation = validateUpperOTP(upperOTP, otpValue);
    } else {
      validation = validateLowerOTP(upperOTP, otpValue);
    }

    validationDispatcher({ type: validationString, validation });
  };

  const toggleLegendModal = (e: React.MouseEvent<HTMLSpanElement>) => {
    e.preventDefault();
    validationDispatcher({ type: "TOGGLE_MODAL" });
  };

  useEffect(() => {
    if (otpValue.length === 6 && upperOTP.length === 6) {
      const validationUpper = validateUpperOTP(upperOTP, otpValue);
      const validationLower = validateLowerOTP(upperOTP, otpValue);
      validationDispatcher({
        type: "SET_BOTHOTP_VALIDATION",
        validationUpper,
        validationLower,
      });
    }
  }, [otpValue, upperOTP]);

  const handleRequestNewOTP = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    requestNewOTP();
    validationDispatcher({ type: "OTP_REQUESTED" });
  };

  return (
    <>
      {isMobile && isMobileModalOpen && (
        <MobileOTPModal dispatcher={validationDispatcher} />
      )}
      <ReactModal
        isOpen={showOTPExceed}
        className="modal-content"
        overlayClassName="modal-overlay"
        closeTimeoutMS={300}
        style={{ content: { height: isMobile ? "100vh" : 410 } }}
      >
        <RejectionMessage
          msg={"Has superado ms de 4 intentos para ingresar tu OTP."}
          restart
        />
      </ReactModal>
      <ReactModal
        isOpen={bureauLegendOpen}
        className="modal-content"
        overlayClassName="modal-overlay"
        closeTimeoutMS={300}
        onRequestClose={toggleLegendModal}
        style={{
          content: {
            padding: isMobile ? "20px 10px 20px 20px" : 30,
            height: isMobile ? "100vh" : 580,
          },
        }}
      >
        <BoureauLegend toggleLegendModal={toggleLegendModal} />
      </ReactModal>
      <Form onSubmit={(e) => e.preventDefault()}>
        <FormHeader>
          {isMobile && (
            <FormTitle style={{ fontSize: 22 }}>Confirmar el OTP</FormTitle>
          )}
          {!isMobile && (
            <>
              <TitleIconWrapper style={isMobile ? { marginTop: 70 } : {}}>
                <span className="circle" />
                <CellphoneIcon />
              </TitleIconWrapper>
              <FormTitle>Confirmar el OTP</FormTitle>
            </>
          )}
        </FormHeader>
        <FormSubtitle
          style={
            isMobile
              ? {
                  padding: "0 30px",
                  textAlign: "center",
                  width: "100vw",
                  marginBottom: 20,
                  marginTop: 29,
                }
              : { marginBottom: 17 }
          }
        >
          Escribe el OTP (cdigo de validacin) que enviamos a tu celular.
        </FormSubtitle>
        <CopyContainer>
          <UpperOTPInput
            ref={otpCopyRef}
            type="tel"
            value={upperOTP}
            onChange={handleUpperOTPChange}
            onBlur={handleBlur("SET_UPPEROTP_VALIDATION")}
          />
          <OTPCopyTitle>Tu OTP</OTPCopyTitle>
          <OTPCopyBtn onClick={handleCopyOTP}>
            <CopyIconWrapper>
              <CopyIcon />
            </CopyIconWrapper>
            Copiar
          </OTPCopyBtn>
          {upperOTPValidation.validated && !upperOTPValidation.valid && (
            <ErrorMsg>{upperOTPValidation.msg}</ErrorMsg>
          )}
        </CopyContainer>
        <FormSubtitle
          style={
            isMobile
              ? { marginBottom: 6, fontSize: 20, marginTop: 0 }
              : { marginBottom: 6 }
          }
        >
          Confirma el OTP
        </FormSubtitle>
        <Warning>
          Al capturar el OTP que te enviamos, autorizas la{" "}
          <span>Consulta de tu Historial Crediticio</span> a favor de Bradescard
          Mxico S. de R.L.
        </Warning>
        <LowerOTPWrapper>
          <OTPInput />
          {lowerOTPValidation.validated && !lowerOTPValidation.valid && (
            <ErrorMsg lower>{lowerOTPValidation.msg}</ErrorMsg>
          )}
        </LowerOTPWrapper>
        <CreditLegend onClick={toggleLegendModal}>
          Leyenda de Bur de Crdito
        </CreditLegend>
        {isLoading ? (
          <FetchingSpinnerContainer>
            <SmallSpinner />
          </FetchingSpinnerContainer>
        ) : (
          <ContinueBtn
            disabled={
              !lowerOTPValidation.validated ||
              !lowerOTPValidation.valid ||
              !upperOTPValidation.validated ||
              !upperOTPValidation.valid
            }
            onClick={verifyOTP}
          >
            Continuar
            <ContinueIconWrapper>
              <ContinueIcon />
            </ContinueIconWrapper>
          </ContinueBtn>
        )}
        <NewOTPWrapper>
          <p
            style={{ padding: "0 40px" }}
          >{`Hubo algn problema?. Tiempo en volver a enviar ${secsUntilOtp} seg`}</p>
          <NewOTP disabled={disableOtpBtn} onClick={handleRequestNewOTP}>
            Enviar de nuevo el OTP
          </NewOTP>
        </NewOTPWrapper>
      </Form>
    </>
  );
};

export default FormOTP;
