import React, { FC, useEffect, useCallback } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";

// styles
import {
  Container,
  Title,
  Message,
  AnimationWrapper,
} from "./ProcesandoAnim.styled";
import { FormSubtitle } from "../FormClientPhone/FormClientPhone.styled";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// Icons
import { ReactComponent as Globe } from "../../assets/icons/Globe.svg";

const ProcesandoAnim: FC<RouteComponentProps> = ({ match }) => {
  const mainFormDispatcher = useMasterFormDispatch();
  const { step } = useMasterForm();
  const history = useHistory();

  useEffect(() => {
    if (step < 0.5) {
      history.goBack();
    }
  }, [step, history]);

  const goToPersonalData1 = useCallback(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.6 });
    history.push(match.url.replace("/procesando", "/datos-personales"));
  }, [mainFormDispatcher, match.url, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.5 });
  }, [mainFormDispatcher]);

  // this is temporally to fake the API call
  useEffect(() => {
    const timeout = setTimeout(() => {
      goToPersonalData1();
    }, 6000);
    return () => clearTimeout(timeout);
  }, [goToPersonalData1]);

  return (
    <Container>
      <AnimationWrapper>
        <Globe />
      </AnimationWrapper>
      <Title>Tu tarjeta est en camino!</Title>
      <FormSubtitle style={{ marginTop: 10, marginBottom: 5 }}>
        Ahora hblanos de ti.
      </FormSubtitle>
      <Message>
        Captura tus datos que ayudarn
        <br />a obtener tu tarjeta.
      </Message>
    </Container>
  );
};

export default ProcesandoAnim;
