import React, { useEffect, useReducer, useCallback, FC, useState } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import { useQuery } from "react-query";
import ReactModal from "react-modal";

// styles
import {
  Form,
  MiddleText,
  FetchingSpinnerContainer,
} from "./ResumeProcess.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";
import { theme } from "../../styles/theme";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// components
import ContinueBtn from "../ui/PrimaryBtn";
import NumberDataInput from "../ui/NumberDataInput";
import RejectionMessage from "../RejectionMensage";
import TextDataInput from "../ui/TextDataInput";
import SmallSpinner from "../ui/SmallSpinner";

// Context
import { useMasterFormDispatch } from "../../Context/MainForm";

// utils
import {
  validateName,
  validateOTP,
  validatePhoneNonEmpty,
  continueModel,
} from "./ResumeProcess.utils";
import { validationReducer, defaultState } from "./validation.reducer";
import { flasher } from "../../utils/Flasher";
import { validateOTP as validateOTPEndpoint } from "../FormOTP/FormOTP.utils";

// hooks
import mediaQuery from "../../hooks/useMedia";

ReactModal.setAppElement("body");

const ResumeProcess: FC<RouteComponentProps> = ({ match }) => {
  const [{ isOpen, rejectionMessage }, setModalState] = useState({
    isOpen: false,
    rejectionMessage: "",
  });
  const history = useHistory();
  const isMobile = mediaQuery(theme.mediaquerys.mobile);
  const mainFormDispatcher = useMasterFormDispatch();
  const [
    {
      fullName,
      otp,
      tel,
      nameValidation,
      telValidation,
      otpValidation,
      partnerId,
    },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);

  const { refetch, isFetching: fetchingRecovery } = useQuery(
    ["continueModel", otp, fullName, tel, telValidation],
    continueModel,
    {
      enabled: false,
      refetchOnMount: false,
      onError: () => {
        flasher(
          "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
          "error"
        );
      },
      onSettled: (modelRes) => {
        console.log(modelRes);
        if (modelRes && modelRes.name) {
          switch (modelRes.name) {
            case "ShowNotification":
              return setModalState({
                isOpen: true,
                rejectionMessage: `estatus ${modelRes.notificationStatus}, ${modelRes.notificationReason}`,
              });
            case "FullCapture":
              mainFormDispatcher({
                type: "SET_RECOVERY_FULLCAPTURE_RES",
                response: modelRes,
                rfc: modelRes.rfc,
                partner: modelRes.partnerId,
                ctePhone: modelRes.phoneNumber,
                nationality: modelRes.nationality,
                idSelected: modelRes.idType,
                idProdType: modelRes.partnerId === "0" ? "3" : "9",
                finance: modelRes.incomeSource,
                fullName: modelRes.fullName,
                productType: modelRes.productType,
              });
              return goToCapturaCompleta(modelRes.partnerName);
            case "TelephoneCapture":
              mainFormDispatcher({
                type: "SET_RECOVERY_TELCAPTURE_RES",
                response: modelRes,
                partner: modelRes.partnerId,
                ctePhone: modelRes.phoneNumber,
                fullName: modelRes.fullName,
                productType: modelRes.productType,
              });
              return goToReferences(modelRes.partnerName);
            default:
              flasher(
                "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
                "warning"
              );
          }
        } else {
          flasher(
            "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
            "warning"
          );
          history.goBack();
        }
      },
      retry: 1,
    }
  );
  const { refetch: verifyOTP, isFetching: fetchingOTP } = useQuery(
    ["validateOTPEndpoint", otp],
    validateOTPEndpoint,
    {
      enabled: false,
      refetchOnMount: false,
      retry: 1,
      onError: () => {
        flasher(
          "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
          "error"
        );
      },
      onSettled: (OTPRes) => {
        console.log(OTPRes);
        if (OTPRes && OTPRes.id_Socio && OTPRes.celular && +OTPRes.vigente) {
          validationDispatcher({
            type: "SET_OTP_RESPONSE",
            partner: OTPRes.id_Socio,
            tel: OTPRes.celular,
          });
        } else {
          flasher("El OTP ingresado ya no es vlido", "warning");
          history.goBack();
        }
      },
    }
  );

  const goToCapturaCompleta = useCallback(
    (partner) => {
      mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.75 });
      history.push(
        match.url.replace(
          "/retomar/tramite",
          `/${partner}/id/datos-personales/captura-completa`
        )
      );
    },
    [mainFormDispatcher, match.url, history]
  );

  const goToReferences = useCallback(
    (partner) => {
      mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.9 });
      history.push(
        match.url.replace(
          "/retomar/tramite",
          `/${partner}/id/datos-personales/referencias`
        )
      );
    },
    [mainFormDispatcher, match.url, history]
  );

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    if (telValidation.validated && telValidation.valid) {
      refetch();
    } else {
      verifyOTP();
    }
  };

  useEffect(() => {
    if (partnerId) {
      refetch();
    }
  }, [partnerId, refetch]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.7 });
  }, [mainFormDispatcher]);

  const handleChangeOnlyNums = (action: "SET_PHONE" | "SET_OTP") => (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    const limitLength = action === "SET_PHONE" ? 10 : 6;
    const value = e.target.value;
    if (
      value.length <= limitLength &&
      (!value || /[0-9]/gi.test(value.substr(-1)))
    ) {
      validationDispatcher({ type: action, value });
    }
  };

  const handleChangeName = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.toUpperCase();
    if (!value || /[a-z ,.]/gi.test(value.substr(-1))) {
      validationDispatcher({ type: "SET_NAME", value });
    }
  };

  const handleBur = (
    action:
      | "SET_PHONE_VALIDATION"
      | "SET_NAME_VALIDATION"
      | "SET_OTP_VALIDATION"
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    let validation;
    switch (action) {
      case "SET_PHONE_VALIDATION":
        validation = validatePhoneNonEmpty(tel);
        break;
      case "SET_NAME_VALIDATION":
        validation = validateName(fullName);
        break;
      case "SET_OTP_VALIDATION":
        validation = validateOTP(otp);
        break;
    }
    validationDispatcher({ type: action, validation });
  };

  const isDisabled = () => {
    if (
      (telValidation.validated && telValidation.valid) ||
      (nameValidation.validated &&
        nameValidation.valid &&
        otpValidation.validated &&
        otpValidation.valid)
    ) {
      return false;
    }
    return true;
  };

  return (
    <>
      <ReactModal
        isOpen={isOpen}
        className="modal-content"
        overlayClassName="modal-overlay"
        closeTimeoutMS={300}
      >
        <RejectionMessage msg={rejectionMessage} />
      </ReactModal>
      <Form onSubmit={(e) => e.preventDefault()}>
        <FormHeader>
          <FormTitle bolder style={{ marginTop: 0 }}>
            Retomar trmite
          </FormTitle>
        </FormHeader>
        <FormSubtitle>
          Ingresa tu nmero de telefono, o tu nombre completo y OTP
        </FormSubtitle>
        <NumberDataInput
          labelTxt="+52 Nmero de telfono mvil"
          first
          onChange={handleChangeOnlyNums("SET_PHONE")}
          onBlur={handleBur("SET_PHONE_VALIDATION")}
          value={tel}
          id={"tel"}
          validation={telValidation}
        />
        <MiddleText>Captura tu nombre completo y tu nmero OTP</MiddleText>
        <TextDataInput
          labelTxt="Nombre completo"
          onChange={handleChangeName}
          onBlur={handleBur("SET_NAME_VALIDATION")}
          value={fullName}
          id={"fullName"}
          validation={nameValidation}
          maxLength={200}
        />
        <NumberDataInput
          labelTxt="Nmero OTP"
          onChange={handleChangeOnlyNums("SET_OTP")}
          onBlur={handleBur("SET_OTP_VALIDATION")}
          value={otp}
          id={"otp"}
          validation={otpValidation}
        />
        {fetchingOTP || fetchingRecovery ? (
          <FetchingSpinnerContainer>
            <SmallSpinner />
          </FetchingSpinnerContainer>
        ) : (
          <ContinueBtn
            disabled={isDisabled()}
            onClick={handleContinue}
            style={{ marginTop: isMobile ? 60 : 90 }}
          >
            Retomar Trmite
            <ContinueIconWrapper>
              <ContinueIcon />
            </ContinueIconWrapper>
          </ContinueBtn>
        )}
      </Form>
    </>
  );
};

export default ResumeProcess;
