import React, { FC, useEffect, useReducer } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import ReactModal from "react-modal";
import { useQuery } from "react-query";

// styles
import {
  Form,
  FieldsContainer,
  HeaderWrapper,
  FetchingInputContainer,
} from "./ReferencesForm.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";
import { FetchingSpinnerContainer } from "../ReferencesFormMobile2/ReferencesFormMobile2.styled";
import { theme } from "../../styles/theme";

// components
import TextDataInput from "../ui/TextDataInput";
import NumberDataInput from "../ui/NumberDataInput";
import SelectorInput from "../ui/SelectorInput";
import FinishBtn from "../ui/PrimaryBtnVariant";
import ContinueBtn from "../ui/PrimaryBtn";
import RejectionMessage from "../RejectionMensage";
import SmallSpinner from "../ui/SmallSpinner";
import AccepMessage from "../ApproveMessage";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// hooks
import mediaQuery from "../../hooks/useMedia";

// utils
import { isNumber } from "../../utils/numberUtils";
import { validationReducer, defaultState } from "./validation.reducer";
import {
  referenceDefaultState,
  referenceReducer,
} from "./ReferemceForm.reducer";
import {
  validatePhoneNonEmpty,
  validateName,
  getRelativeOptions,
  completeDataModel,
  validatePhoneByRing,
} from "./ReferencesForm.utils";
import { flasher } from "../../utils/Flasher";
import { closeDataModel } from "../../utils/closeModel";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

ReactModal.setAppElement("body");

const ReferencesForm: FC<RouteComponentProps> = ({ match }) => {
  const history = useHistory();
  const isMobile = mediaQuery(theme.mediaquerys.mobile);
  const {
    currentStep,
    step,
    ctePhone,
    reference1Par,
    reference2Par,
    reference1Name,
    reference2Name,
    ref1Phone,
    ref2Phone,
    startModelResponse,
    partner,
    name,
    secondName,
    fatherSurname,
    motherSurname,
  } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const [
    {
      name1Validation,
      name2Validation,
      ref1PhoneValidation,
      ref2PhoneValidation,
      finalModalOpen,
      message,
      rejection,
    },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);
  const [{ referenceParOptons }, referenceDispatcher] = useReducer(
    referenceReducer,
    referenceDefaultState
  );
  useQuery("getRelativeOptions", getRelativeOptions, {
    keepPreviousData: true,
    onSuccess: (referenceRes) => {
      referenceDispatcher({
        type: "SET_RELATIVES_OPTIONS",
        value: referenceRes[0].content.map((ref: { key: any; value: any }) => ({
          value: ref.key,
          text: ref.value,
        })),
      });
    },
  });
  const { refetch, isLoading } = useQuery(
    [
      "completeDataModel",
      startModelResponse,
      ctePhone,
      reference1Name,
      ref1Phone,
      reference1Par,
      reference2Name,
      ref2Phone,
      reference2Par,
      partner,
      name,
      secondName,
      fatherSurname,
      motherSurname,
    ],
    completeDataModel,
    {
      enabled: false,
      onError: () => {
        flasher(
          "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
          "warning"
        );
        history.push("/");
      },
      onSettled: (modelRes) => {
        console.log(modelRes);
        if (modelRes && modelRes.name) {
          switch (modelRes.name) {
            case "ShowNotification":
              closeDataModel(modelRes.id, modelRes.version, +partner);
              validationDispatcher({
                type: "TOGGLE_MODAL",
                msg: `estatus ${modelRes.notificationStatus}, ${modelRes.notificationReason}`,
                rejection: true,
              });
              return;
            case "ReceiveDocumentsFlag":
              closeDataModel(modelRes.id, modelRes.version, +partner);
              validationDispatcher({
                type: "TOGGLE_MODAL",
                msg: `Acude a tu tienda ms cercana.`,
                rejection: false,
              });
              return;
            default:
              flasher(
                "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
                "warning"
              );
              history.push("/");
          }
        } else {
          flasher(
            "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
            "warning"
          );
          history.push("/");
        }
      },
      retry: false,
      refetchOnMount: false,
    }
  );
  const { refetch: validateRef1, isFetching: ringingRef1 } = useQuery(
    ["validateRef1PhoneByRing", ref1Phone],
    validatePhoneByRing,
    {
      keepPreviousData: true,
      enabled: false,
    }
  );
  const { refetch: validateRef2, isFetching: ringingRef2 } = useQuery(
    ["validateRef2PhoneByRing", ref2Phone],
    validatePhoneByRing,
    {
      keepPreviousData: true,
      enabled: false,
    }
  );

  useEffect(() => {
    if (step < 0.9) {
      history.goBack();
    }
  }, [step, history]);

  const handleFinish = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    e.preventDefault();
    refetch();
  };

  const handleContinueMobile = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.95 });
    history.push(`${match.url}/2`);
  };

  useEffect(() => {
    if (currentStep > 0.9) {
      const name1Validation = validateName(reference1Name);
      const ref1PhoneValidation = validatePhoneNonEmpty(
        ref1Phone,
        ref2Phone,
        ctePhone
      );

      validationDispatcher({
        type: "INIT_ALL_VALIDATIONS",
        name1Validation,
        ref1PhoneValidation,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.9 });
  }, [mainFormDispatcher]);

  const handlePhoneChange = (
    action:
      | "SET_REF2_PHONEHOME"
      | "SET_REF2_PHONE"
      | "SET_REF1_PHONEHOME"
      | "SET_REF1_PHONE"
      | "SET_CTEPHONE_HOME"
      | "SET_CTEPHONE_WORK"
      | "SET_CTE_PHONE"
  ) => (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    const value = e.target.value;
    if (
      value.length < 11 &&
      (!value || isNumber(value.charAt(value.length - 1)))
    ) {
      mainFormDispatcher({ type: action, value });
    }
  };

  const handleChange = (action: "SET_REF1_PAR" | "SET_REF2_PAR") => (
    e: React.ChangeEvent<HTMLSelectElement>
  ) => {
    mainFormDispatcher({ type: action, value: e.target.value });
  };

  const handleNameChange = (action: "SET_REF1_NAME" | "SET_REF2_NAME") => (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    const value = e.target.value.toUpperCase();
    if (!value || /[a-z .,-]/gi.test(value.substr(-1))) {
      mainFormDispatcher({ type: action, value });
    }
  };

  const handleBlurName = (
    value: string,
    action: "SET_REF1NAME_VALIDATION" | "SET_REF2NAME_VALIDATION"
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateName(value);
    validationDispatcher({ type: action, validation });
  };

  const handleBlurRefPhones = (
    value: string,
    action: "SET_REF1PHONE_VALIDATION" | "SET_REF2PHONE_VALIDATION"
  ) => async (e: React.FocusEvent<HTMLInputElement>) => {
    switch (action) {
      case "SET_REF1PHONE_VALIDATION":
        let ref1PhoneVal = validatePhoneNonEmpty(value, ref2Phone, ctePhone);
        if (ref1PhoneVal.valid) {
          const { data } = await validateRef1();
          if (data) {
            ref1PhoneVal = data;
          }
        }
        if (ref2Phone.length === 10) {
          const ref2PhoneVal = validatePhoneNonEmpty(
            ref2Phone,
            ref1Phone,
            ctePhone
          );
          validationDispatcher({
            type: "SET_PHONE_VALIDATIONS",
            ref1PhoneValidation: ref1PhoneVal,
            ref2PhoneValidation: ref2PhoneVal,
          });
        } else {
          validationDispatcher({
            type: action,
            validation: ref1PhoneVal,
          });
        }

        break;
      case "SET_REF2PHONE_VALIDATION":
        let ref2PhoneVal = validatePhoneNonEmpty(value, ref1Phone, ctePhone);
        if (ref2PhoneVal.valid) {
          const { data } = await validateRef2();
          if (data) {
            ref2PhoneVal = data;
          }
        }
        if (ref1Phone.length === 10) {
          const ref1PhoneVal = validatePhoneNonEmpty(
            ref1Phone,
            ref2Phone,
            ctePhone
          );
          validationDispatcher({
            type: "SET_PHONE_VALIDATIONS",
            ref1PhoneValidation: ref1PhoneVal,
            ref2PhoneValidation: ref2PhoneVal,
          });
        } else {
          validationDispatcher({
            type: action,
            validation: ref2PhoneVal,
          });
        }

        break;
    }
  };

  return (
    <>
      <ReactModal
        isOpen={finalModalOpen}
        className="modal-content"
        overlayClassName="modal-overlay"
        closeTimeoutMS={300}
        style={{ content: { padding: 10 } }}
      >
        {rejection ? (
          <RejectionMessage msg={message} />
        ) : (
          <AccepMessage
            msg={message}
            fullName={startModelResponse?.fullName || ""}
            caseId={startModelResponse?.caseId || ""}
            requestId={startModelResponse?.requestNumber || ""}
          />
        )}
      </ReactModal>
      <Form>
        <FormHeader>
          <FormTitle bolder style={{ marginTop: 0 }}>
            Captura de telfonos
          </FormTitle>
        </FormHeader>
        <FormSubtitle style={{ color: theme.colors.bradescardPink }}>
          {`Tu telfono celular: ${ctePhone}`}
        </FormSubtitle>
        {!isMobile && (
          <FormSubtitle style={{ marginBottom: 22 }}>
            Tus telfonos
          </FormSubtitle>
        )}
        <FieldsContainer>
          <HeaderWrapper>
            <h3>Referencia 1</h3>
            <TextDataInput
              labelTxt="Nombre completo"
              first
              onChange={handleNameChange("SET_REF1_NAME")}
              onBlur={handleBlurName(reference1Name, "SET_REF1NAME_VALIDATION")}
              value={reference1Name}
              id={"reference1Name"}
              validation={name1Validation}
            />
          </HeaderWrapper>
          <SelectorInput
            labelTxt="Parentesco"
            first
            value={reference1Par}
            options={referenceParOptons}
            defaultOption={"Seleccione una opcin"}
            onChange={handleChange("SET_REF1_PAR")}
            id="reference1Par"
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "flex-end",
            }}
          />
          {ringingRef1 ? (
            <FetchingInputContainer>
              <SmallSpinner />
            </FetchingInputContainer>
          ) : (
            <NumberDataInput
              labelTxt="+52 Nmero de telfono mvil"
              first
              onChange={handlePhoneChange("SET_REF1_PHONE")}
              onBlur={handleBlurRefPhones(
                ref1Phone,
                "SET_REF1PHONE_VALIDATION"
              )}
              value={ref1Phone}
              id={"ref1Phone"}
              validation={ref1PhoneValidation}
            />
          )}
          <br />
          {!isMobile && (
            <>
              <HeaderWrapper>
                <h3>Referencia 2</h3>
                <TextDataInput
                  labelTxt="Nombre completo"
                  first
                  onChange={handleNameChange("SET_REF2_NAME")}
                  onBlur={handleBlurName(
                    reference2Name,
                    "SET_REF2NAME_VALIDATION"
                  )}
                  value={reference2Name}
                  id={"reference2Name"}
                  validation={name2Validation}
                />
              </HeaderWrapper>
              <SelectorInput
                labelTxt="Parentesco"
                first
                value={reference2Par}
                options={referenceParOptons}
                defaultOption={"Seleccione una opcin"}
                onChange={handleChange("SET_REF2_PAR")}
                id="reference2Par"
                style={{
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "flex-end",
                }}
              />
              {ringingRef2 ? (
                <FetchingInputContainer>
                  <SmallSpinner />
                </FetchingInputContainer>
              ) : (
                <NumberDataInput
                  labelTxt="+52 Nmero de telfono mvil"
                  first
                  onChange={handlePhoneChange("SET_REF2_PHONE")}
                  onBlur={handleBlurRefPhones(
                    ref2Phone,
                    "SET_REF2PHONE_VALIDATION"
                  )}
                  value={ref2Phone}
                  id={"ref2Phone"}
                  validation={ref2PhoneValidation}
                />
              )}
            </>
          )}
        </FieldsContainer>
        {isMobile ? (
          <ContinueBtn
            disabled={
              ringingRef1 ||
              !name1Validation.validated ||
              !name1Validation.valid ||
              !ref1PhoneValidation.validated ||
              !ref1PhoneValidation.valid ||
              !reference1Par
            }
            onClick={handleContinueMobile}
          >
            Continuar
            <ContinueIconWrapper>
              <ContinueIcon />
            </ContinueIconWrapper>
          </ContinueBtn>
        ) : isLoading ? (
          <FetchingSpinnerContainer>
            <SmallSpinner />
          </FetchingSpinnerContainer>
        ) : (
          <FinishBtn
            disabled={
              ringingRef1 ||
              ringingRef2 ||
              !name1Validation.validated ||
              !name1Validation.valid ||
              !name2Validation.validated ||
              !name2Validation.valid ||
              !ref1PhoneValidation.validated ||
              !ref1PhoneValidation.valid ||
              !ref2PhoneValidation.validated ||
              !ref2PhoneValidation.valid ||
              !reference1Par ||
              !reference2Par
            }
            onClick={handleFinish}
          >
            Tramitar
            <ContinueIconWrapper>
              <ContinueIcon />
            </ContinueIconWrapper>
          </FinishBtn>
        )}
      </Form>
    </>
  );
};

export default ReferencesForm;
