import axios from "axios";

const { REACT_APP_API_PREFIX } = process.env;
interface ocrQueryCtx {
  queryKey: [string, File, File];
}

export const getDataFromINE = async (ctx: ocrQueryCtx) => {
  const { queryKey } = ctx;
  let token = localStorage.getItem("auth_token");
  const ineAnverso = queryKey[1];
  const ineReverso = queryKey[2];

  const formData = new FormData();
  formData.append("Type", "123");
  formData.append("FrontImage", ineAnverso);
  formData.append("BackImage", ineReverso);

  const response = await axios.post(
    // CATALOGS END POINT
    `${REACT_APP_API_PREFIX}/Middleware/OCR/ObtenerOcr`,
    // JSON BODY
    formData,
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );

  return response.data.result;
};
