import React, { FC } from "react";
import { useLocation } from "react-router-dom";

// styled
import { Container } from "./MainContainer.styled";

const { REACT_APP_ROUTER_PREFIX } = process.env;

const MainContainer: FC = ({ children }) => {
  const { pathname } = useLocation();
  const pathnameToCheck =
    pathname.length < 2 ? pathname : pathname.replace(/\/$/, "");
  const isHome = pathnameToCheck === REACT_APP_ROUTER_PREFIX;

  return <Container isHome={isHome}>{children}</Container>;
};

export default MainContainer;
