import React, { FC } from "react";

// styles
import { ArrowButtonStyled } from "./ArrowButton.styled";

interface ArrowButtonProps {
  onClick: (event: React.MouseEvent<HTMLButtonElement>) => void;
  right?: boolean;
}

const ArrowButton: FC<ArrowButtonProps> = ({ right, onClick, children }) => {
  return (
    <ArrowButtonStyled right={right} onClick={onClick}>
      {children}
    </ArrowButtonStyled>
  );
};

export default ArrowButton;
