// types
import { ValidationObject } from "../../Types/validationTypes";

export const validateEqualField = (field: string, otherField: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (field.trim().toLowerCase() !== otherField.trim().toLowerCase()) {
    validation.valid = false;
    validation.msg = "El campo no coincide con el ingresado";
  }

  return validation;
};

export const validateBirthDate = (date: string, otherDate: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (date !== otherDate) {
    validation.valid = false;
    validation.msg = "La fecha no coincide con la ingresada";
  }

  return validation;
};
