export const isLeapYear = (year: number) => {
  return (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0;
};

export const toFullTear = (year: number) => {
  const currentYearStr = new Date().getFullYear().toString();
  if (year < 35) {
    return `${currentYearStr.slice(0, 2)}${year.toString().padStart(2, "0")}`;
  }
  return `${(+currentYearStr - 100).toString().slice(0, 2)}${year}`;
};
