import React, { FC } from "react";

// styles
import {
  StyledLabel,
  InputContainer,
  StyledInput,
} from "./TextDataInput.styled";

// components
import { ValidationIconWrapper } from "../ClientNumInput/ClientNumInput.styled";
import ValidationMsg from "../ValidationErrorMsg";

// types
import { ValidationObject } from "../../../Types/validationTypes";

// icons
import { ReactComponent as CheckIcon } from "../../../assets/icons/CheckMark.svg";
import { ReactComponent as CrossIcon } from "../../../assets/icons/Cross.svg";

interface TextDataInputProps {
  labelTxt: string;
  first?: boolean;
  value: string;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onBlur?: (e: React.FocusEvent<HTMLInputElement>) => void;
  id: string;
  validation: ValidationObject;
  disabled?: boolean;
  maxLength?: number;
}

const TextDataInput: FC<TextDataInputProps> = ({
  labelTxt,
  first,
  value,
  onChange,
  onBlur,
  id,
  validation,
  disabled,
  maxLength,
}) => {
  return (
    <StyledLabel first={first}>
      {labelTxt}
      <InputContainer validated={validation.validated} valid={validation.valid}>
        <StyledInput
          type="text"
          value={value}
          onChange={onChange}
          onBlur={onBlur}
          id={id}
          maxLength={maxLength ? maxLength : 50}
          invalid={validation.validated && !validation.valid}
          disabled={disabled}
        />
        {validation.validated && (
          <ValidationIconWrapper invalid={!validation.valid}>
            {validation.valid ? <CheckIcon /> : <CrossIcon />}
          </ValidationIconWrapper>
        )}
      </InputContainer>
      {validation.validated && !validation.valid && (
        <ValidationMsg>{validation.msg}</ValidationMsg>
      )}
    </StyledLabel>
  );
};

export default TextDataInput;
