import React, { FC } from "react";

// styles
import { Container, CheckboxWrapper } from "./AddressTableRow.styled";

// components
import StyledCheckbox from "../ui/StyledCheckbox";

// types
import { personObject } from "../../Types/api";

// utils
import { maskAddressValidation } from "../../utils/StringUtils";

interface AddressTableRowProps {
  first: boolean;
  address: personObject;
  selected: boolean;
  toggleFn: (e: React.ChangeEvent<HTMLInputElement>) => void;
  keyId: number;
}

const AddressTableRow: FC<AddressTableRowProps> = ({
  first,
  address,
  selected,
  toggleFn,
  keyId,
}) => {
  return (
    <Container first={first}>
      <p>{address.state}</p>
      <p>{maskAddressValidation(address.street)}</p>
      <CheckboxWrapper>
        <StyledCheckbox
          checked={selected}
          toogleFn={toggleFn}
          id={keyId.toString()}
        />
      </CheckboxWrapper>
    </Container>
  );
};

export default AddressTableRow;
