import { ValidationObject } from "../../Types/validationTypes";

export type Action =
  | {
      type: "ONE_SEC_PASS";
    }
  | {
      type: "TOGGLE_NEWOTP_BTN";
    }
  | {
      type: "OTP_REQUESTED";
    }
  | {
      type: "CLOSE_MOBILE_MODAL";
    }
  | {
      type: "TOGGLE_CHECK";
    }
  | {
      type: "INCREMENT_NUMBER_TRIALS";
    }
  | {
      type: "DISPLAY_OTP_EXCEDED";
    }
  | {
      type: "SET_UPPEROTP";
      otpValue: string;
    }
  | {
      type: "SET_UPPEROTP_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_LOWEROTP_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_BOTHOTP_VALIDATION";
      validationUpper: ValidationObject;
      validationLower: ValidationObject;
    }
  | {
      type: "TOGGLE_MODAL";
    };
type State = {
  newOtpRequested: boolean;
  secsUntilOtp: number;
  disableOtpBtn: boolean;
  isMobileModalOpen: boolean;
  showOTPExceed: boolean;
  numberOfTrials: number;
  checked: boolean;
  upperOTP: string;
  upperOTPValidation: ValidationObject;
  lowerOTPValidation: ValidationObject;
  bureauLegendOpen: boolean;
};

export const defaultState: State = {
  newOtpRequested: false,
  disableOtpBtn: true,
  secsUntilOtp: 120,
  isMobileModalOpen: true,
  showOTPExceed: false,
  numberOfTrials: 0,
  checked: false,
  upperOTP: "",
  upperOTPValidation: { validated: false },
  lowerOTPValidation: { validated: false },
  bureauLegendOpen: false,
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "ONE_SEC_PASS":
      return { ...state, secsUntilOtp: state.secsUntilOtp - 1 };
    case "TOGGLE_NEWOTP_BTN":
      return { ...state, disableOtpBtn: !state.disableOtpBtn };
    case "OTP_REQUESTED":
      return {
        ...state,
        disableOtpBtn: true,
        newOtpRequested: true,
        secsUntilOtp: 120,
      };
    case "CLOSE_MOBILE_MODAL":
      return { ...state, isMobileModalOpen: false };
    case "TOGGLE_CHECK":
      return { ...state, checked: !state.checked };
    case "INCREMENT_NUMBER_TRIALS":
      return { ...state, numberOfTrials: state.numberOfTrials + 1 };
    case "DISPLAY_OTP_EXCEDED":
      return { ...state, showOTPExceed: true };
    case "SET_UPPEROTP":
      return { ...state, upperOTP: action.otpValue };
    case "SET_UPPEROTP_VALIDATION":
      return { ...state, upperOTPValidation: action.validation };
    case "SET_LOWEROTP_VALIDATION":
      return { ...state, lowerOTPValidation: action.validation };
    case "SET_BOTHOTP_VALIDATION":
      return {
        ...state,
        upperOTPValidation: action.validationUpper,
        lowerOTPValidation: action.validationLower,
      };
    case "TOGGLE_MODAL":
      return { ...state, bureauLegendOpen: !state.bureauLegendOpen };
    default:
      return state;
  }
};
