// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_NATIONALITY_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_STREET_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_NUMEXT_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_NUMINT_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_ZIPCODE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_COL_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_MUN_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_STATE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_CITY_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "TOGGLE_ZIPCODE_PREVIEWS";
    }
  | {
      type: "SET_IS_ERROR";
      value: boolean;
    }
  | {
      type: "VALIDATE_CP_SELECTION";
      zipVal: ValidationObject;
      colVal: ValidationObject;
      munVal: ValidationObject;
      stateVal: ValidationObject;
      cityVal: ValidationObject;
    }
  | {
      type: "INIT_ALL_VALIDATIONS";
      nacVal: ValidationObject;
      strVal: ValidationObject;
      numExtVal: ValidationObject;
      numIntVal: ValidationObject;
      zipVal: ValidationObject;
      colVal: ValidationObject;
      munVal: ValidationObject;
      stateVal: ValidationObject;
      cityVal: ValidationObject;
    };
type State = {
  nacValidation: ValidationObject;
  streetValidation: ValidationObject;
  numExtValidation: ValidationObject;
  numIntValidation: ValidationObject;
  zipCodeValidation: ValidationObject;
  colValidation: ValidationObject;
  munValidation: ValidationObject;
  stateValidation: ValidationObject;
  cityValidation: ValidationObject;
  zipCodePreviews: boolean;
  isError: boolean;
};

export const defaultState: State = {
  nacValidation: { validated: false },
  streetValidation: { validated: false },
  numExtValidation: { validated: false },
  numIntValidation: { validated: false },
  zipCodeValidation: { validated: false },
  colValidation: { validated: false },
  munValidation: { validated: false },
  stateValidation: { validated: false },
  cityValidation: { validated: false },
  zipCodePreviews: false,
  isError: false,
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_NATIONALITY_VALIDATION":
      return { ...state, nacValidation: action.validation };
    case "SET_STREET_VALIDATION":
      return { ...state, streetValidation: action.validation };
    case "SET_NUMEXT_VALIDATION":
      return { ...state, numExtValidation: action.validation };
    case "SET_NUMINT_VALIDATION":
      return { ...state, numIntValidation: action.validation };
    case "SET_ZIPCODE_VALIDATION":
      return { ...state, zipCodeValidation: action.validation };
    case "SET_COL_VALIDATION":
      return { ...state, colValidation: action.validation };
    case "SET_MUN_VALIDATION":
      return { ...state, munValidation: action.validation };
    case "SET_STATE_VALIDATION":
      return { ...state, stateValidation: action.validation };
    case "SET_CITY_VALIDATION":
      return { ...state, cityValidation: action.validation };
    case "TOGGLE_ZIPCODE_PREVIEWS":
      return { ...state, zipCodePreviews: !state.zipCodePreviews };
    case "SET_IS_ERROR":
      return {
        ...state,
        isError: action.value,
        zipCodeValidation: action.value
          ? { validated: true, valid: false }
          : { validated: true, valid: true },
      };
    case "VALIDATE_CP_SELECTION":
      return {
        ...state,
        zipCodeValidation: action.zipVal,
        colValidation: action.colVal,
        munValidation: action.munVal,
        stateValidation: action.stateVal,
        cityValidation: action.cityVal,
      };
    case "INIT_ALL_VALIDATIONS":
      return {
        ...state,
        nacValidation: action.nacVal,
        streetValidation: action.strVal,
        numExtValidation: action.numExtVal,
        numIntValidation: action.numIntVal,
        zipCodeValidation: action.zipVal,
        colValidation: action.colVal,
        munValidation: action.munVal,
        stateValidation: action.stateVal,
        cityValidation: action.cityVal,
      };
    default:
      return state;
  }
};
