import React, { FC } from "react";

// styles
import { Container } from "./ProductsShowcase.styled";

// components
import ProductCard from "../ProductCard";

import { products } from "../../constants/partnerData";

const ProductsShowcase: FC = () => {
  return (
    <Container>
      {products.map((product) => (
        <ProductCard
          key={product.id}
          url={product.url}
          alt={product.alt}
          title={product.title}
          link={product.link}
        />
      ))}
    </Container>
  );
};

export default ProductsShowcase;
