import React, { FC } from "react";
import { Switch, Route, RouteComponentProps } from "react-router-dom";

// styled
import {
  Container,
  FormMainHeader,
  HeaderTitle,
  FormContent,
} from "./MainForm.styled";
import { CornerDecoration, PurpleLine } from "../Home/Home.styled";
import { Footer, MobileBackDecoration } from "../Partner/Partner.styled";
import { theme } from "../../styles/theme";

// conponents
import ProgressBar from "../../components/ui/ProgressBar";
import FormClientPhone from "../../components/FormClientPhone";
import FormOTP from "../../components/FormOTP";
import FormId from "../../components/FormId";
import ProcesandoAnim from "../../components/ProcesandoAnim";
import PersonalData1 from "../../components/PersonalData1";
import PersonalData2 from "../../components/PersonalData2";
import ConfirmPersonalData from "../../components/ConfirmPersonalData";
import ProcesandoAnim2 from "../../components/ProcesandoAnim2";
import CompleteData from "../../components/CompleteData";
import CompleteData2 from "../../components/CompleteData2";
import ProcesandoAnim3 from "../../components/ProcesandoAnim3";
import ReferencesForm from "../../components/ReferencesForm";
import ReferencesFormMobile2 from "../../components/ReferencesFormMobile2";
import BackButton from "../../components/ui/BackButton";
import PersonalData3 from "../../components/PersonalData3";
import PersonalData4 from "../../components/PersonalData4";
import ResumeProcess from "../../components/ResumeProcess";
import AddressValidation from "../../components/AddressValidarion";

// contexts
import { useMasterForm } from "../../Context/MainForm";

// hooks
import useMediaQuery from "../../hooks/useMedia";

// utils
import { isBackButtonVisible } from "./MainForm.utils";

const MainForm: FC<RouteComponentProps> = ({ match }) => {
  const { step } = useMasterForm();
  const isMobile = useMediaQuery(theme.mediaquerys.mobile);
  const isDesktopLg = useMediaQuery(theme.mediaquerys.desktopLg);

  return (
    <Container>
      {isMobile ? (
        <MobileBackDecoration style={step >= 0.3 ? { height: 124 } : {}} />
      ) : (
        <>
          <CornerDecoration
            style={{
              zIndex: -1,
              bottom: "-500px",
              backgroundColor: "#F8FAFB",
            }}
          />
          <PurpleLine right="-200px" bottom="20%" style={{ zIndex: -1 }} />
          <PurpleLine right="300px" bottom="-30px" style={{ zIndex: -1 }} />
          {isDesktopLg && isBackButtonVisible(step) && <BackButton />}
        </>
      )}
      <FormMainHeader>
        <HeaderTitle>Avance de tu trmite {step * 100}%</HeaderTitle>
        <ProgressBar value={step} />
      </FormMainHeader>
      <FormContent>
        <Switch>
          <Route
            path={`${match.url}/tramite`}
            exact
            component={ResumeProcess}
          />
          <Route path={match.url} exact component={FormClientPhone} />
          <Route path={`${match.url}/otp`} exact component={FormOTP} />
          <Route path={`${match.url}/id`} exact component={FormId} />
          <Route
            path={`${match.url}/id/procesando`}
            exact
            component={ProcesandoAnim}
          />
          <Route
            path={`${match.url}/id/datos-personales`}
            exact
            component={PersonalData1}
          />
          <Route
            path={`${match.url}/id/datos-personales/2`}
            exact
            component={PersonalData2}
          />
          <Route
            path={`${match.url}/id/datos-personales/confirmar`}
            exact
            component={ConfirmPersonalData}
          />
          <Route
            path={`${match.url}/id/datos-personales/valida-domicilio`}
            exact
            component={AddressValidation}
          />
          <Route
            path={`${match.url}/id/datos-personales/procesando`}
            exact
            component={ProcesandoAnim2}
          />
          <Route
            path={`${match.url}/id/datos-personales/captura-completa`}
            exact
            component={CompleteData}
          />
          <Route
            path={`${match.url}/id/datos-personales/captura-completa/2`}
            exact
            component={CompleteData2}
          />
          <Route
            path={`${match.url}/id/datos-personales/captura-completa/3`}
            exact
            component={PersonalData3}
          />
          <Route
            path={`${match.url}/id/datos-personales/captura-completa/4`}
            exact
            component={PersonalData4}
          />
          <Route
            path={`${match.url}/id/datos-personales/captura-completa/procesando`}
            exact
            component={ProcesandoAnim3}
          />
          <Route
            path={`${match.url}/id/datos-personales/referencias`}
            exact
            component={ReferencesForm}
          />
          <Route
            path={`${match.url}/id/datos-personales/referencias/2`}
            exact
            component={ReferencesFormMobile2}
          />
        </Switch>
      </FormContent>
      {!isMobile && (
        <Footer>
          <span>Sujeto a aprobacin de crdito.</span>
          <span>
            Tarjetas emitidas y operadas por Bradescard Mxico S. de R.L.
          </span>
        </Footer>
      )}
    </Container>
  );
};

export default MainForm;
