import styled, { keyframes } from "styled-components";

const cloudAnimation = keyframes`
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
`;
const wingsAnimation = keyframes`
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
`;
const rotationAnimation = keyframes`
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(20deg);
  }
`;
const rotationAnimation2 = keyframes`
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(20deg) translateY(-6px);
  }
`;

export const Container = styled.section`
  padding: 50px;
  text-align: center;
`;

export const Title = styled.h1`
  color: ${({ theme: { colors } }) => colors.bradescardPink};
  font-size: 30px;
  font-weight: 700;
  line-height: 36px;
`;

export const Message = styled.p`
  color: ${({ theme: { colors } }) => colors.bradescardBlue};
  font-size: 20px;
  font-weight: 500;
  line-height: 25px;
`;

export const AnimationWrapper = styled.div`
  margin-top: 50px;
  margin-bottom: 30px;

  #clouds {
    animation: ${cloudAnimation} 3000ms linear infinite;
  }
  #lines {
    animation: ${cloudAnimation} 2000ms linear infinite;
  }
  #wings {
    animation: ${wingsAnimation} 100ms linear infinite;
  }
  #Globo1,
  #Globo2 {
    transform-box: fill-box;
    transform-origin: top;
    animation: ${rotationAnimation} 3000ms ease-in-out infinite alternate;
  }

  #strings,
  #Tarjeta1,
  #Tarjeta2,
  #Tarjeta3 {
    transform-box: view-box;
    transform-origin: top;
    animation: ${rotationAnimation2} 3000ms ease-in-out infinite alternate;
  }
`;
