// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_CITIZENS_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "RESET_CITIZENS_VALIDATION";
    };
type State = {
  citizensVal: ValidationObject;
};

export const defaultState: State = {
  citizensVal: { validated: false },
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_CITIZENS_VALIDATION":
      return { ...state, citizensVal: action.validation };
    case "RESET_CITIZENS_VALIDATION":
      return { ...state, citizensVal: { validated: false } };
    default:
      return state;
  }
};
