// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_NAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_SECONDNAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_FATHER_SURNAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_MOTHER_SURNAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_BIRTHDATE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "INIT_ALL_VALIDATIONS";
      nameVal: ValidationObject;
      secondNameVal: ValidationObject;
      fSurnameVal: ValidationObject;
      mSurnameVal: ValidationObject;
      birthVal: ValidationObject;
    }
  | {
      type: "TOGGLE_MODAL";
    };
type State = {
  nameValidation: ValidationObject;
  secondnameValidation: ValidationObject;
  fatherSurnameValidation: ValidationObject;
  motherSurnameValidation: ValidationObject;
  birthDateValidation: ValidationObject;
  isModalOpen: boolean;
};

export const defaultState: State = {
  nameValidation: { validated: false },
  secondnameValidation: { validated: false },
  fatherSurnameValidation: { validated: false },
  motherSurnameValidation: { validated: false },
  birthDateValidation: { validated: false },
  isModalOpen: false,
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_NAME_VALIDATION":
      return { ...state, nameValidation: action.validation };
    case "SET_SECONDNAME_VALIDATION":
      return { ...state, secondnameValidation: action.validation };
    case "SET_FATHER_SURNAME_VALIDATION":
      return { ...state, fatherSurnameValidation: action.validation };
    case "SET_MOTHER_SURNAME_VALIDATION":
      return { ...state, motherSurnameValidation: action.validation };
    case "SET_BIRTHDATE_VALIDATION":
      return { ...state, birthDateValidation: action.validation };
    case "TOGGLE_MODAL":
      return { ...state, isModalOpen: !state.isModalOpen };
    case "INIT_ALL_VALIDATIONS":
      return {
        ...state,
        nameValidation: action.nameVal,
        secondnameValidation: action.secondNameVal,
        fatherSurnameValidation: action.fSurnameVal,
        motherSurnameValidation: action.mSurnameVal,
        birthDateValidation: action.birthVal,
      };
    default:
      return state;
  }
};
