import axios from "axios";

// types
import { tokenResponse } from "../../Types/api";

const {
  REACT_APP_API_PREFIX,
  REACT_APP_TOKEN_USR,
  REACT_APP_TOKEN_PASS,
} = process.env;

export const renewToken = async () => {
  const res: tokenResponse = await axios.post(
    // TOKEN ENDPOINT
    `${REACT_APP_API_PREFIX}/Identity/Users/Auth`,
    // JSON BODY
    {
      // AUTH CREDENTIALS
      Password: REACT_APP_TOKEN_PASS,
      Username: REACT_APP_TOKEN_USR,
    }
  );
  return res.data.result;
};
