import React, { FC, useReducer, useEffect } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";

// styles
import {
  Form,
  Selector,
  SelectorCell,
  SelectorsContainer,
  InputWrapper,
} from "./PersonalData4.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";

// components
import ContinueBtn from "../ui/PrimaryBtn";
import SelectorTab from "../ui/DocumentTab";
import TextDataInput from "../ui/TextDataInput";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// utils
import { validateCouldBeEmpty, validateNonEmpty } from "./PersonalData4.utils";
import { validationReducer, defaultState } from "./validation.reducer";

const PersonalData4: FC<RouteComponentProps> = ({ match }) => {
  const history = useHistory();
  const {
    step,
    currentStep,
    visaResPer,
    resPer,
    resFisOther,
    resFis,
    waitNumCont,
    waitNum,
    quitedCiti,
    quitedCitizens,
    presentedDocs,
  } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const [
    { quitedVal, resFisVal, resPermVal, waitNumVal },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.85 });
    history.push(match.url.replace("/4", "/procesando"));
  };

  useEffect(() => {
    if (step < 0.84) {
      history.goBack();
    }
  }, [step, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.84 });
  }, [mainFormDispatcher]);

  useEffect(() => {
    if (currentStep > 0.67) {
      const resVal = visaResPer
        ? validateNonEmpty(resPer)
        : { validated: false };
      const fisVal = resFisOther
        ? validateNonEmpty(resFis)
        : { validated: false };
      const waitVal = waitNumCont
        ? validateNonEmpty(waitNum)
        : { validated: false };
      const quitVal = quitedCiti
        ? validateNonEmpty(quitedCitizens)
        : { validated: false };
      validationDispatcher({
        type: "INIT_ALL_VALIDATIONS",
        fisVal,
        quitVal,
        resVal,
        waitVal,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleSelectorChange = (
    value: boolean,
    action:
      | "SET_VISARES_PER"
      | "SET_FIS_OTHER"
      | "SET_WAITNUM_CONT"
      | "SET_QUITED_CITIZEN"
      | "SET_PRESENTED_DOCS",
    resetAction?:
      | "RESET_RESPERM_VALIDATION"
      | "RESET_RESFIS_VALIDATION"
      | "RESET_WAITNUM_VALIDATION"
      | "RESET_QUITED_VALIDATION"
  ) => (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    e.preventDefault();
    mainFormDispatcher({ type: action, value });
    if (!value && resetAction) {
      validationDispatcher({ type: resetAction });
    }
  };

  const handleFieldChange = (
    action: "SET_RES_PERM" | "SET_RES_FIS" | "SET_NUM_CONT" | "SET_QUITED"
  ) => (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    mainFormDispatcher({ type: action, value: e.target.value.toUpperCase() });
  };

  const handleBlur = (
    checked: boolean | null,
    field: string,
    action:
      | "SET_RESPERM_VALIDATION"
      | "SET_RESFIS_VALIDATION"
      | "SET_WAITNUM_VALIDATION"
      | "SET_QUITED_VALIDATION"
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    const validation =
      checked === true ? validateNonEmpty(field) : validateCouldBeEmpty(field);
    validationDispatcher({ type: action, validation });
  };

  return (
    <Form onSubmit={(e) => e.preventDefault()}>
      <FormHeader>
        <FormTitle bolder style={{ marginTop: 0 }}>
          Captura extranjeros
        </FormTitle>
      </FormHeader>
      <FormSubtitle>
        Texto para instrucciones - texto para instrucciones
      </FormSubtitle>

      <SelectorsContainer>
        <SelectorCell>
          <h3>
            Cuentas con visa de residencia permanente, vlida en otros pases,
            con Green Card?
          </h3>
          <Selector>
            <SelectorTab
              selected={visaResPer === true}
              onClick={handleSelectorChange(true, "SET_VISARES_PER")}
            >
              S
            </SelectorTab>
            <SelectorTab
              selected={visaResPer === false}
              onClick={handleSelectorChange(
                false,
                "SET_VISARES_PER",
                "RESET_RESPERM_VALIDATION"
              )}
            >
              No
            </SelectorTab>
          </Selector>
        </SelectorCell>
        <InputWrapper>
          <TextDataInput
            labelTxt="Residencias permanentes"
            first
            onChange={handleFieldChange("SET_RES_PERM")}
            onBlur={handleBlur(visaResPer, resPer, "SET_RESPERM_VALIDATION")}
            value={resPer}
            id={"resPer"}
            validation={resPermVal}
            disabled={visaResPer !== true}
          />
        </InputWrapper>
        <SelectorCell>
          <h3>Es residente fiscal en otros pases?</h3>
          <Selector>
            <SelectorTab
              selected={resFisOther === true}
              onClick={handleSelectorChange(true, "SET_FIS_OTHER")}
            >
              S
            </SelectorTab>
            <SelectorTab
              selected={resFisOther === false}
              onClick={handleSelectorChange(
                false,
                "SET_FIS_OTHER",
                "RESET_RESFIS_VALIDATION"
              )}
            >
              No
            </SelectorTab>
          </Selector>
        </SelectorCell>
        <InputWrapper>
          <TextDataInput
            labelTxt="Pases de residencial fiscal"
            first
            onChange={handleFieldChange("SET_RES_FIS")}
            onBlur={handleBlur(resFisOther, resFis, "SET_RESFIS_VALIDATION")}
            value={resFis}
            id={"resFis"}
            validation={resFisVal}
            disabled={resFisOther !== true}
          />
        </InputWrapper>
        <SelectorCell>
          <h3>Espera de Nm. de contribuyente</h3>
          <Selector>
            <SelectorTab
              selected={waitNumCont === true}
              onClick={handleSelectorChange(true, "SET_WAITNUM_CONT")}
            >
              S
            </SelectorTab>
            <SelectorTab
              selected={waitNumCont === false}
              onClick={handleSelectorChange(
                false,
                "SET_WAITNUM_CONT",
                "RESET_WAITNUM_VALIDATION"
              )}
            >
              No
            </SelectorTab>
          </Selector>
        </SelectorCell>
        <InputWrapper>
          <TextDataInput
            labelTxt="Nmero de contribuyente"
            first
            onChange={handleFieldChange("SET_NUM_CONT")}
            onBlur={handleBlur(waitNumCont, waitNum, "SET_WAITNUM_VALIDATION")}
            value={waitNum}
            id={"waitNum"}
            validation={waitNumVal}
            disabled={waitNumCont !== true}
          />
        </InputWrapper>
        <SelectorCell>
          <h3>Renunci a su ciudadana?</h3>
          <Selector>
            <SelectorTab
              selected={quitedCiti === true}
              onClick={handleSelectorChange(true, "SET_QUITED_CITIZEN")}
            >
              S
            </SelectorTab>
            <SelectorTab
              selected={quitedCiti === false}
              onClick={handleSelectorChange(
                false,
                "SET_QUITED_CITIZEN",
                "RESET_QUITED_VALIDATION"
              )}
            >
              No
            </SelectorTab>
          </Selector>
        </SelectorCell>
        <InputWrapper>
          <TextDataInput
            labelTxt="Pas de renuncia"
            first
            onChange={handleFieldChange("SET_QUITED")}
            onBlur={handleBlur(
              quitedCiti,
              quitedCitizens,
              "SET_QUITED_VALIDATION"
            )}
            value={quitedCitizens}
            id={"quitedCitizens"}
            validation={quitedVal}
            disabled={quitedCiti !== true}
          />
        </InputWrapper>
        <SelectorCell>
          <h3>La documentacin es presentada?</h3>
          <Selector>
            <SelectorTab
              selected={presentedDocs === true}
              onClick={handleSelectorChange(true, "SET_PRESENTED_DOCS")}
            >
              S
            </SelectorTab>
            <SelectorTab
              selected={presentedDocs === false}
              onClick={handleSelectorChange(false, "SET_PRESENTED_DOCS")}
            >
              No
            </SelectorTab>
          </Selector>
        </SelectorCell>
      </SelectorsContainer>
      <ContinueBtn
        disabled={
          visaResPer === null ||
          (visaResPer && (!resPermVal.validated || !resPermVal.valid)) ||
          resFisOther === null ||
          (resFisOther && (!resFisVal.validated || !resFisVal.valid)) ||
          waitNumCont === null ||
          (waitNumCont && (!waitNumVal.validated || !waitNumVal.valid)) ||
          quitedCiti === null ||
          (quitedCiti && (!quitedVal.validated || !quitedVal.valid)) ||
          presentedDocs === null
        }
        onClick={handleContinue}
      >
        Continuar
        <ContinueIconWrapper>
          <ContinueIcon />
        </ContinueIconWrapper>
      </ContinueBtn>
    </Form>
  );
};

export default PersonalData4;
