import React, { FC } from "react";

// styles
import { Button } from "./PromaryBtnVariant.styled";

interface PrimaryBtnProps {
  slim?: boolean;
  disabled?: boolean;
  onClick?: (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

const PrimaryBtnVariant: FC<PrimaryBtnProps> = ({
  children,
  disabled,
  onClick,
  slim,
}) => {
  return (
    <Button isDisabled={disabled} onClick={onClick} slim={slim}>
      {children}
    </Button>
  );
};

export default PrimaryBtnVariant;
