// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_CTEPHONE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_CTEPHONECONF_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_CTEPHONECONF";
      ctePhoneConf: string;
    }
  | {
      type: "TOGGLE_TERMS";
    }
  | {
      type: "VERIFY_RECAPTCHA";
      value: boolean;
    };
type State = {
  ctePhoneValidation: ValidationObject;
  ctePhoneConfValidation: ValidationObject;
  ctePhoneConf: string;
  ctePhonePrxConf: string;
  termsChecked: boolean;
  recaptchaVerified: boolean;
};

export const defaultState: State = {
  ctePhoneValidation: { validated: false },
  ctePhoneConfValidation: { validated: false },
  ctePhoneConf: "",
  ctePhonePrxConf: "52",
  termsChecked: false,
  recaptchaVerified: false,
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_CTEPHONE_VALIDATION":
      return { ...state, ctePhoneValidation: action.validation };
    case "SET_CTEPHONECONF_VALIDATION":
      return { ...state, ctePhoneConfValidation: action.validation };
    case "SET_CTEPHONECONF":
      return { ...state, ctePhoneConf: action.ctePhoneConf };
    case "TOGGLE_TERMS":
      return { ...state, termsChecked: !state.termsChecked };
    case "VERIFY_RECAPTCHA":
      return { ...state, recaptchaVerified: action.value };
    default:
      return state;
  }
};
