import React, { FC, useRef, useState } from "react";

// styles
import { Container, Display, StyledInput } from "./OTPInput.styled";

// Context
import {
  useMasterForm,
  useMasterFormDispatch,
} from "../../../Context/MainForm";

const codeLength = new Array(6).fill(0);

const OTPInput: FC = () => {
  const { otpValue } = useMasterForm();
  const masterFormDispatcher = useMasterFormDispatch();
  const values = otpValue.split("");
  const [focused, setFocused] = useState(false);
  const inputRef = useRef<HTMLInputElement | null>(null);
  const selectedIndex =
    values.length < codeLength.length ? values.length : codeLength.length - 1;

  const handleClick = () => {
    inputRef.current?.focus();
  };

  const handleFocus = () => {
    setFocused(true);
  };

  const handleBlur = () => {
    setFocused(false);
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const val = e.target.value;

    if (!/[0-9]/.test(val) || otpValue.length >= codeLength.length) return;
    masterFormDispatcher({
      type: "SET_OTP",
      value: (otpValue + val).slice(0, codeLength.length),
    });
  };

  const handleKeyUp = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Backspace") {
      masterFormDispatcher({
        type: "SET_OTP",
        value: otpValue.slice(0, otpValue.length - 1),
      });
    }
  };

  return (
    <Container onClick={handleClick}>
      {codeLength.map((v, index) => {
        const selected = values.length === index && focused;

        return (
          <Display
            key={`${v}-${index}`}
            typed={values[index]}
            selected={selected}
          >
            {values[index]}
          </Display>
        );
      })}
      <StyledInput
        type="tel"
        value=""
        ref={inputRef}
        onFocus={handleFocus}
        onBlur={handleBlur}
        selectedIndex={selectedIndex}
        onChange={handleChange}
        onKeyDown={handleKeyUp}
      />
    </Container>
  );
};

export default OTPInput;
