import styled from "styled-components";

interface StyledInputProps {
  selectedIndex: number;
}

interface DisplayProps {
  typed: string;
  selected: boolean;
}

export const Container = styled.section`
  display: flex;
  margin: 30px;
  position: relative;

  ${({ theme: { up, breakpoints } }) => up(breakpoints.desktop)} {
    margin-top: 0;
  }
`;

export const Display = styled.div<DisplayProps>`
  align-items: center;
  background: ${({ theme: { colors } }) => colors.otpBackground};
  border: ${({ theme: { colors }, selected }) =>
    selected ? `1.5px solid ${colors.otpBorder}` : "0"};
  border-bottom: 3px solid
    ${({ theme: { colors }, typed, selected }) =>
      typed || selected ? colors.otpBorder : colors.homeTriangle};
  color: ${({ theme: { colors } }) => colors.bradescardBlue};
  display: flex;
  font-size: 23px;
  font-weight: 600;
  justify-content: center;
  margin: 0 5px;
  height: 37px;
  width: 40px;

  ${({ theme: { up, breakpoints } }) => up(breakpoints.desktop)} {
    font-size: 28px;
    font-weight: 400;
    height: 47px;
    width: 50px;
  }
`;

export const StyledInput = styled.input<StyledInputProps>`
  background: transparent;
  border: none;
  font-size: 23px;
  font-weight: 600;
  left: ${({ selectedIndex }) =>
    `${selectedIndex * 40 + (selectedIndex + 1) * 5}px`};
  outline: none;
  position: absolute;
  text-align: center;
  width: 50px;

  ${({ theme: { up, breakpoints } }) => up(breakpoints.desktop)} {
    font-size: 28px;
    font-weight: 400;
    left: ${({ selectedIndex }) =>
      `${selectedIndex * 50 + (selectedIndex + 1) * 5}px`};
    top: 5px;
  }
`;
