import styled from "styled-components";

interface ContainerProps {
  isHome: boolean;
}

export const Container = styled.main<ContainerProps>`
  display: grid;
  grid-template-columns: 100vw;
  grid-template-rows: ${({ isHome }) => (isHome ? "420px auto" : "110px auto")};

  overflow-x: hidden;

  ${({ theme: { up, breakpoints } }) => up(breakpoints.desktopLg)} {
    grid-template-columns: ${({ isHome }) => (isHome ? "100vw" : "auto 1fr")};
    grid-template-rows: ${({ isHome }) =>
      isHome ? "231px auto" : "minmax(790px, 100vh)"};
  }
`;
