import axios from "axios";

// types
import { ValidationObject } from "../../Types/validationTypes";

const { REACT_APP_API_PREFIX } = process.env;

export const validateEmail = (field: string, confirmation: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (
    !field.trim().length ||
    // eslint-disable-next-line
    !/(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")/gi.test(
      field
    )
  ) {
    validation.valid = false;
    validation.msg = "Ingresa un email vlido";
  } else if (confirmation.length && field !== confirmation) {
    validation.valid = false;
    validation.msg = "Los valores no coinciden";
  }

  return validation;
};

export const validateConfirmation = (confirmation: string, field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  if (confirmation !== field) {
    validation.valid = false;
    validation.msg = "Los valores no coinciden";
  }

  return validation;
};

export const validateIdNum = (idNum: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  if (!idNum.trim().length) {
    validation.valid = false;
    validation.msg = "Ingresa un id vlido";
  }

  return validation;
};

export const validateRfc = (rfc: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  const rfcRegex = /^([A-Z\x26]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1]))((-)?([A-Z\d]{3}))?$/gi;
  if (!rfcRegex.test(rfc.trim())) {
    validation.valid = false;
    validation.msg = "Ingresa un rfc vlido";
  }

  return validation;
};

export const getTypeHouse = async () => {
  let token = localStorage.getItem("auth_token");
  const response = await axios.get(
    // Get House type selector end point
    `${REACT_APP_API_PREFIX}/Commons/Catalogs/Tipovivienda`,
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};

