export const maskAddressValidation = (address: string) => {
  const strs = address.trim().split(" ");
  const finalStrs: string[] = [];
  strs.forEach((str) => {
    if (str.length < 3) {
      finalStrs.push(str);
      return;
    }
    finalStrs.push(str.slice(0, 2).padEnd(str.length, "*"));
  });
  return finalStrs.join(" ");
};
