import styled from "styled-components";

interface InputContainerProps {
  validated: boolean;
  valid: boolean | undefined;
}

export const Container = styled.div`
  position: relative;
  margin: 10px 0 20px 0;

  small {
    margin-left: 10px;
  }
`;

export const SelectStyled = styled.select<InputContainerProps>`
  background-color: ${({ theme: { colors } }) => colors.almostWhite};
  color: ${({ theme: { colors } }) => colors.gray};
  border: 1.6px solid
    ${({ theme: { colors }, valid, validated }) =>
      validated
        ? valid
          ? colors.greenCheck
          : colors.errorRed
        : colors.homeTriangle};
  border-radius: 3px;
  font-size: 13px;
  font-weight: 600;
  height: 35px;
  margin: 0 5px;
  padding-left: 10px;
  width: 80px;

  ${({ theme: { up, breakpoints } }) => up(breakpoints.desktop)} {
    margin: 0 10px;
    width: 115px;
  }
`;
