import React, { FC, useState } from "react";
import { useQuery } from "react-query";

// utils
import { getCountries } from "./CountrySelector.utils";

// components
import StyledSelector from "../SelectorInput";

interface SelectOption {
  text: string;
  value: string;
}
interface CountrySelectorProps {
  labelTxt: string;
  first?: boolean;
  value: string;
  onChange: (e: React.ChangeEvent<HTMLSelectElement>) => void;
}

const CountrySelector: FC<CountrySelectorProps> = ({
  labelTxt,
  first,
  value,
  onChange,
}) => {
  const [options, setOptions] = useState<SelectOption[]>([
    {
      value: "1",
      text: "MEXICO",
    },
  ]);
  useQuery("getCountries", getCountries, {
    keepPreviousData: true,
    onSuccess: (countriesRes) => {
      setOptions(
        countriesRes[0].content.map((country: { key: any; value: any }) => ({
          value: country.key,
          text: country.value,
        }))
      );
    },
  });

  return (
    <StyledSelector
      labelTxt={labelTxt}
      first={first}
      value={value}
      options={options}
      onChange={onChange}
      id={labelTxt}
    />
  );
};

export default CountrySelector;
