// types
import { defaultSelectorOption } from "../../Types/selectorOption";

type Action =
  | {
      type: "SET_RESIDENCE_TYPES";
      value: defaultSelectorOption[];
    }
  | {
      type: "SET_RESIDEMCE_TIMES";
      value: defaultSelectorOption[];
    }
  | {
      type: "SET_ID_TYPES";
      value: defaultSelectorOption[];
    };
type State = {
  livingPlaceOptons: defaultSelectorOption[];
  timeResidenceOptons: defaultSelectorOption[];
  idTypesOptions: defaultSelectorOption[];
};

export const completeDefaultState: State = {
  livingPlaceOptons: [
    { text: "Propia", value: "Propia" },
    { text: "La estoy pagando", value: "La estoy pagando" },
    { text: "La estoy rentando", value: "La estoy rentando" },
    { text: "Vivo con mis familiares", value: "Vivo con mis familiares" },
    { text: "Vivo con mis padres", value: "Vivo con mis padres" },
  ],
  timeResidenceOptons: [
    { text: "0 - 12 Meses", value: "0 - 12 Meses" },
    { text: "2 aos", value: "2 aos" },
    { text: "3 aos", value: "3 aos" },
    { text: "5+ aos", value: "5+ aos" },
  ],
  idTypesOptions: [
    { text: "INE/IFE", value: "1" },
    { text: "PASAPORTE MEXICANO", value: "2" },
    { text: "TARJETA DE RESIDENTE PERMANENTE", value: "3" },
    {
      text: "TARJETA DE RESIDENTE TEMPORAL CON PERMISO DE TRABAJO",
      value: "4",
    },
  ],
};

export const completeDataReducer = (
  state: State = completeDefaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_RESIDENCE_TYPES":
      return { ...state, livingPlaceOptons: action.value };
    case "SET_RESIDEMCE_TIMES":
      return { ...state, timeResidenceOptons: action.value };
    case "SET_ID_TYPES":
      return { ...state, idTypesOptions: action.value };
    default:
      return state;
  }
};
