// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_PHONE";
      value: string;
    }
  | {
      type: "SET_NAME";
      value: string;
    }
  | {
      type: "SET_OTP";
      value: string;
    }
  | {
      type: "SET_PHONE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_NAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_OTP_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_OTP_RESPONSE";
      partner: string;
      tel: string;
    };
type State = {
  tel: string;
  fullName: string;
  otp: string;
  telValidation: ValidationObject;
  nameValidation: ValidationObject;
  otpValidation: ValidationObject;
  partnerId: string;
};

export const defaultState: State = {
  tel: "",
  fullName: "",
  otp: "",
  telValidation: { validated: false },
  nameValidation: { validated: false },
  otpValidation: { validated: false },
  partnerId: "",
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_PHONE":
      return { ...state, tel: action.value };
    case "SET_NAME":
      return { ...state, fullName: action.value };
    case "SET_OTP":
      return { ...state, otp: action.value };
    case "SET_PHONE_VALIDATION":
      return { ...state, telValidation: action.validation };
    case "SET_NAME_VALIDATION":
      return { ...state, nameValidation: action.validation };
    case "SET_OTP_VALIDATION":
      return { ...state, otpValidation: action.validation };
    case "SET_OTP_RESPONSE":
      return { ...state, partnerId: action.partner, tel: action.tel };
    default:
      return state;
  }
};
