export type Action = {
  type: "SET_SELECTED_ADDRESS_INDEX";
  value: number | null;
};
type State = {
  selectedAddress: null | number;
};

export const defaultState: State = {
  selectedAddress: null,
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_SELECTED_ADDRESS_INDEX":
      return { ...state, selectedAddress: action.value };
    default:
      return state;
  }
};
