import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";

// styles
import {
  HomeContainer,
  Header,
  HeaderIconWrapper,
  HomeTitle,
  HomeSubTitle,
  PurpleLine,
  Footer,
  SkewedStripe,
  CornerDecoration,
  ButtonLinkContainer,
  Transaction,
  ButtonText,
  HomeTitleWrapper,
  MainTitleCotainer,
} from "./Home.styled";
import { theme } from "../../styles/theme";

// compoenents
import BannerLink from "../../components/ui/BannerLink";
import ProductsShowcase from "../../components/ProductsShowcase";

// icons
import { ReactComponent as HomeTitleIcon } from "../../assets/icons/HomeTitle.svg";
import { ReactComponent as ContinueIcon } from "../../assets/icons/Continue.svg";

// hooks
import useMediaQuery from "../../hooks/useMedia";

// utils
import { renewToken } from "./Home.utils";
import { getDeviceType } from "../../utils/navigatorUtils";

// Context
import { useMasterFormDispatch } from "../../Context/MainForm";

const { REACT_APP_ROUTER_PREFIX } = process.env;

const Home = () => {
  const history = useHistory();
  const isMobile = useMediaQuery(theme.mediaquerys.mobile);
  const mainFormDispatcher = useMasterFormDispatch();

  const handleGoToResumeProcess = (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    history.push(
      REACT_APP_ROUTER_PREFIX?.length === 1
        ? "new/retomar/tramite"
        : `${REACT_APP_ROUTER_PREFIX}/new/retomar/tramite`
    );
  };

  const newToken = async () => {
    try {
      const token = await renewToken();
      localStorage.setItem("auth_token", token);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    newToken();
    mainFormDispatcher({ type: "RESET_FORM" });
    console.log(`Tipo de dispositivo: ${getDeviceType()}`);
    // eslint-disable-next-line
  }, []);

  return (
    <HomeContainer>
      <Header>
        {isMobile && (
          <ButtonLinkContainer onClick={handleGoToResumeProcess}>
            <Transaction conTrans>
              <ContinueIcon />
            </Transaction>
            <ButtonText>Retomar Trmite</ButtonText>
          </ButtonLinkContainer>
        )}
        <HomeTitleWrapper>
          <MainTitleCotainer>
            <HeaderIconWrapper>
              <span className="circle" />
              <HomeTitleIcon />
            </HeaderIconWrapper>
            <HomeTitle>Qu tarjeta deseas tramitar?</HomeTitle>
          </MainTitleCotainer>
          <HomeSubTitle>
            Texto simulado Texto simulado Texto simulado Texto simulado Texto
            simulado Texto simulado
          </HomeSubTitle>
        </HomeTitleWrapper>
        {!isMobile && (
          <ButtonLinkContainer onClick={handleGoToResumeProcess}>
            <Transaction conTrans>
              <ContinueIcon />
            </Transaction>
            <ButtonText>Retomar Trmite</ButtonText>
          </ButtonLinkContainer>
        )}
      </Header>

      {!isMobile && (
        <>
          <SkewedStripe />
          <CornerDecoration />
          <PurpleLine left={"-50px"} top={"59.5%"} lineWidth={"200px"} blue />
          <PurpleLine right={"30%"} bottom={"70px"} />
          <PurpleLine right={"-50px"} bottom={"200px"} />
        </>
      )}

      <ProductsShowcase />

      <Footer>
        <div style={{ textAlign: "center" }}>
          <BannerLink
            href="https://www.bradescard.com.mx/bradescard.site/aviso_privacidad.html"
            newTab
          >
            Aviso de Privacidad
          </BannerLink>
          {isMobile && (
            <BannerLink href="https://www.bradescard.com.mx/">
              bradescard.com.mx
            </BannerLink>
          )}
        </div>
        <span>Sujeto a aprobacin de crdito.</span>
        <span style={{ padding: isMobile ? "0 40px" : "0" }}>
          Tarjetas emitidas y operadas por Bradescard Mxico S. de R.L.
        </span>
      </Footer>
    </HomeContainer>
  );
};

export default Home;
