import axios from "axios";

// types
import { ValidationObject } from "../../Types/validationTypes";
import { StartModelResponse } from "../../Types/api";

const { REACT_APP_API_PREFIX } = process.env;

export const validatePhoneNonEmpty = (
  field: string,
  otherFiled: string,
  requestandPhone: string
) => {
  const validation: ValidationObject = { validated: true, valid: true };
  const repeatRegex = /([0-9])\1{4}/g;
  const numToCheck = field.slice(2);

  if (
    !field.trim().length ||
    field.trim().length < 10 ||
    repeatRegex.test(numToCheck)
  ) {
    validation.valid = false;
    validation.msg = "Ingresa un nmero a 10 digitos vlido";
  } else if (
    field.trim() === otherFiled.trim() ||
    field.trim() === requestandPhone.trim()
  ) {
    validation.valid = false;
    validation.msg = "Debes ingresar distintos telfonos";
  }

  return validation;
};

interface queryValidatePhone {
  queryKey: [string, string];
}

export const validatePhoneByRing = async (ctx: queryValidatePhone) => {
  const { queryKey } = ctx;
  const field = queryKey[1];

  const validation: ValidationObject = { validated: true, valid: true };

  let token = localStorage.getItem("auth_token");
  const response = await axios.post(
    // Ring reference phone for validation
    `${REACT_APP_API_PREFIX}/Middleware/AvayaValidator/ValidatePhoneNumber`,
    // JSON BODY
    {
      phoneNumber: field,
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  console.log(response.data);
  const { result } = response?.data;
  if (result?.description !== "valid") {
    validation.valid = false;
    validation.msg = "El telefono ingresado no pudo ser validado";
  }

  return validation;
};

export const validatePhoneCouldEmpty = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (field.trim().length && field.trim().length < 10) {
    validation.valid = false;
    validation.msg = "Ingresa un nmero a 10 digitos vlido";
  }

  return validation;
};

export const validateName = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (!field.trim().length || field.trim().length < 4) {
    validation.valid = false;
    validation.msg = "Ingresa un nombre completo vlido";
  }

  return validation;
};

export const validateCouldBeEmptyButAtLeastOne = (
  field: string,
  otherField: string
) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (!otherField.length && !field.length) {
    validation.valid = false;
    validation.msg = "Debes llenar al menos un telfono";
  } else if (field.trim().length && field.trim().length < 10) {
    validation.valid = false;
    validation.msg = "Ingresa un nmero a 10 digitos vlido";
  }

  return validation;
};

export const getRelativeOptions = async () => {
  let token = localStorage.getItem("auth_token");
  const response = await axios.get(
    // Get email domains selector end point
    `${REACT_APP_API_PREFIX}/Commons/Catalogs/Parentesco`,
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};
