import React, { useEffect, FC, useReducer } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import { useQuery } from "react-query";

// styles
import {
  Form,
  FirstRow,
  SecondRow,
  SelectTabContainer,
  Selector,
} from "./CompleteData2.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";

// components
import SelectorInput from "../ui/SelectorInput";
import SelectorTab from "../ui/DocumentTab";
import ContinueBtn from "../ui/PrimaryBtn";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// utils
import {
  completeDataReducer,
  completeDefaultState,
} from "./CompleteData2.reducer";
import {
  getEmployType,
  getEmployArea,
  getFechasPago,
} from "./CompleteData2.utils";

const CompleteData2: FC<RouteComponentProps> = ({ match }) => {
  const history = useHistory();
  const {
    step,
    employArea,
    employType,
    payDate,
    beneCont,
    nationality,
    partner,
    idProdType,
  } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const [
    { employTypeOptions, employAreaOptions, payDateOptions },
    completeDispatcher,
  ] = useReducer(completeDataReducer, completeDefaultState);
  useQuery("getEmployType", getEmployType, {
    keepPreviousData: true,
    onSuccess: (employTypesRes) => {
      completeDispatcher({
        type: "SET_EMPLOY_TYPES",
        value: employTypesRes[0].content.map(
          (type: { key: any; value: any }) => ({
            value: type.key,
            text: type.value,
          })
        ),
      });
    },
  });
  useQuery("getEmployArea", getEmployArea, {
    keepPreviousData: true,
    onSuccess: (employAreasRes) => {
      completeDispatcher({
        type: "SET_EMPLOY_AREAS",
        value: employAreasRes[0].content.map(
          (type: { key: any; value: any }) => ({
            value: type.key,
            text: type.value,
          })
        ),
      });
    },
  });
  useQuery(["getFechasPago", partner, idProdType], getFechasPago, {
    keepPreviousData: true,
    onSuccess: (fechasPagoRes) => {
      completeDispatcher({
        type: "SET_PAY_DAYS",
        value: fechasPagoRes.fechasDePagoPrep.map(
          (type: { id_FechaCorte: any; fechaPago: any }) => ({
            value: type.id_FechaCorte,
            text: `Da ${type.fechaPago} de cada mes`,
          })
        ),
      });
    },
  });

  useEffect(() => {
    if (step < 0.65) {
      history.goBack();
    }
  }, [step, history]);

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    if (nationality === "1") {
      mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.85 });
      history.push(match.url.replace("/2", "/procesando"));
    } else {
      mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.82 });
      history.push(match.url.replace("/2", "/3"));
    }
  };

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.8 });
  }, [mainFormDispatcher]);

  const handleAreaChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    mainFormDispatcher({ type: "SET_EMPLOY_AREA", employArea: e.target.value });
  };
  const handleTypeChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    mainFormDispatcher({ type: "SET_EMPLOY_TYPE", employType: e.target.value });
  };
  const handlePayChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    mainFormDispatcher({ type: "SET_PAY_DATE", payDate: e.target.value });
  };

  const handleContSelection = (beneCont: boolean) => (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    mainFormDispatcher({ type: "SET_BENE_CONT", beneCont });
  };

  return (
    <Form
      onSubmit={(e) => {
        e.preventDefault();
      }}
    >
      <FormHeader>
        <FormTitle bolder style={{ marginTop: 0 }}>
          Captura completa
        </FormTitle>
      </FormHeader>
      <FormSubtitle>
        Texto para instrucciones - texto para instrucciones
      </FormSubtitle>
      <FirstRow>
        <SelectorInput
          labelTxt="rea de Ocupacin"
          first
          value={employArea}
          options={employAreaOptions}
          defaultOption={"Seleccione una opcin"}
          onChange={handleAreaChange}
          id="employArea"
        />
        <SelectorInput
          labelTxt="Tipo de empleo"
          first
          value={employType}
          options={employTypeOptions}
          defaultOption={"Seleccione una opcin"}
          onChange={handleTypeChange}
          id="employType"
        />
      </FirstRow>
      <SecondRow>
        <SelectorInput
          labelTxt="Fecha de pago"
          first
          value={payDate}
          options={payDateOptions}
          defaultOption={"Seleccione una opcin"}
          onChange={handlePayChange}
          id="payDate"
        />
        <SelectTabContainer>
          <h3>Beneficiario Controlador</h3>
          <p>
            Tiene usted conocimiento de la existencia de un beneficiario
            controlador de este crdito?
          </p>
          <Selector>
            <SelectorTab
              selected={beneCont}
              onClick={handleContSelection(true)}
            >
              S
            </SelectorTab>
            <SelectorTab
              selected={!beneCont}
              onClick={handleContSelection(false)}
            >
              No
            </SelectorTab>
          </Selector>
        </SelectTabContainer>
      </SecondRow>
      <ContinueBtn
        disabled={!employArea || !employType || !payDate}
        onClick={handleContinue}
      >
        Continuar
        <ContinueIconWrapper>
          <ContinueIcon />
        </ContinueIconWrapper>
      </ContinueBtn>
    </Form>
  );
};

export default CompleteData2;
