import axios from "axios";
import { QueryFunctionContext } from "react-query";

// types
import { ValidationObject } from "../../Types/validationTypes";

const { REACT_APP_API_PREFIX } = process.env;

export const validateNonEmpty = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  if (field.trim().length < 3) {
    validation.valid = false;
    validation.msg = "Ingresa un valor vlido";
  }

  return validation;
};

export const validateStreetNums = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  if (!field.trim().length) {
    validation.valid = false;
    validation.msg = "Ingresar un valor.";
  }

  return validation;
};

export const validateCouldBeEmpty = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  return validation;
};

export const validateZIPCode = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  if (!/[0-9]{5}/.test(field)) {
    validation.valid = false;
    validation.msg = "Valor invlido";
  }

  return validation;
};

export const getZipCodeData = async (ctx: QueryFunctionContext) => {
  let token = localStorage.getItem("auth_token");
  const response = await axios.post(
    // ZIP CODES END POINT
    `${REACT_APP_API_PREFIX}/Middleware/BusinessWorks/CodigosPostales`,
    // JSON BODY
    {
      codigoPostal: ctx.queryKey[1],
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};
