import styled from "styled-components";

interface StyledProgressProps {
  value: number;
}

interface CheckMarkProps {
  value: number;
}

export const Container = styled.section`
  align-items: center;
  display: flex;
  height: 20px;
  position: relative;
  width: 80%;
  z-index: 2;
`;

export const StyledBar = styled.div`
  background: ${({ theme: { colors } }) => colors.progressGrey};
  border-radius: 30px;
  height: 10px;
  overflow: hidden;
  width: 100%;
`;

export const StyledProgress = styled.div<StyledProgressProps>`
  background: ${({ theme: { colors } }) => colors.greenCheck};
  height: 100%;
  width: ${({ value }) => (value ? `${value}%` : "0%")};
  transition: width 500ms ease;
`;

export const CheckMark = styled.div<CheckMarkProps>`
  align-items: center;
  background: ${({ theme: { colors } }) => colors.white};
  border: 1.5px solid ${({ theme: { colors } }) => colors.greenCheck};
  border-radius: 50%;
  display: flex;
  height: 17px;
  justify-content: center;
  left: ${({ value }) => (value ? `calc(${value}% - 5px)` : "calc(0% - 5px)")};
  position: absolute;
  width: 17px;
  z-index: 3;

  transition: left 500ms ease;

  svg {
    width: 8px;
  }
`;
