import React from "react";

// styles
import { Container, SelectStyled } from "./IdEmitDate.styled";
import { StyledLabel } from "../TextDataInput/TextDataInput.styled";

// Context
import {
  useMasterFormDispatch,
  useMasterForm,
} from "../../../Context/MainForm";

// utils
import { isLeapYear } from "../../../utils/dateUtils";

const days = Array.from(Array(31).keys());
const months = [
  "Enero",
  "Febrero",
  "Marzo",
  "Abril",
  "Mayo",
  "Junio",
  "Julio",
  "Agosto",
  "Septiembre",
  "Octubre",
  "Noviembre",
  "Diciembre",
];
const year = new Date().getFullYear() - 69;
const years = Array.from({ length: 70 }, (_, i) => i + year).reverse();

const IdEmitDate = () => {
  const { idEmitDay, idEmitMonth, idEmityear } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();

  const handleDayChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    let day = e.target.value;

    // edge cases
    if (day && idEmitMonth) {
      switch (idEmitMonth) {
        case "2":
          if (+day > 28) {
            if (idEmityear) {
              isLeapYear(+idEmityear) ? (day = "29") : (day = "28");
            } else {
              day = "29";
            }
          }
          break;
        case "4":
        case "6":
        case "9":
        case "11":
          if (+day > 30) day = "30";
          break;
      }
    }

    mainFormDispatcher({ type: "SET_IDEMIT_DAY", day });
  };

  const handleMonthChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const month = e.target.value;
    // edge cases
    if (idEmitDay && month) {
      switch (month) {
        case "2":
          if (+idEmitDay > 28) {
            if (idEmityear) {
              isLeapYear(+idEmityear)
                ? mainFormDispatcher({
                    type: "SET_IDEMIT_DAY_MONTH",
                    day: "29",
                    month,
                  })
                : mainFormDispatcher({
                    type: "SET_IDEMIT_DAY_MONTH",
                    day: "28",
                    month,
                  });
            } else {
              mainFormDispatcher({
                type: "SET_IDEMIT_DAY_MONTH",
                day: "29",
                month,
              });
            }
            return;
          }
          break;
        case "4":
        case "6":
        case "9":
        case "11":
          if (+idEmitDay > 30) {
            mainFormDispatcher({
              type: "SET_IDEMIT_DAY_MONTH",
              day: "30",
              month,
            });
            return;
          }
          break;
      }
    }

    mainFormDispatcher({ type: "SET_IDEMIT_MONTH", month });
  };

  const handleYearChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const year = e.target.value;
    // edge cases
    if (idEmitDay && idEmitMonth && year) {
      switch (idEmitMonth) {
        case "2":
          if (+idEmitDay > 28) {
            if (!isLeapYear(+year)) {
              mainFormDispatcher({
                type: "SET_IDEMIT_YEAR_DAY",
                day: "28",
                year,
              });
              return;
            }
          }
          break;
      }
    }

    mainFormDispatcher({ type: "SET_IDEMIT_YEAR", year });
  };

  return (
    <>
      <StyledLabel>Fecha de emisin</StyledLabel>
      <Container>
        <SelectStyled value={idEmitDay} onChange={handleDayChange}>
          <option value="">Da</option>
          {days.map((n) => (
            <option key={n} value={n + 1}>
              {n + 1}
            </option>
          ))}
        </SelectStyled>
        <SelectStyled value={idEmitMonth} onChange={handleMonthChange}>
          <option value="">Mes</option>
          {months.map((m, i) => (
            <option key={m} value={i + 1}>
              {m}
            </option>
          ))}
        </SelectStyled>
        <SelectStyled value={idEmityear} onChange={handleYearChange}>
          <option value="">Ao</option>
          {years.map((y) => (
            <option key={y} value={y}>
              {y}
            </option>
          ))}
        </SelectStyled>
        <br />
      </Container>
    </>
  );
};

export default IdEmitDate;
