import React, { FC } from "react";

// styled
import { Container } from "./DocumentTab.styled";

// components
import NotSelected from "../TabNotSelected";
import Selected from "../PrimaryBtn";

interface DocumentTabProps {
  selected: boolean;
  onClick: (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

const DocumentTab: FC<DocumentTabProps> = ({ selected, children, onClick }) => {
  return (
    <Container onClick={onClick}>
      {selected ? (
        <Selected slim style={{ margin: "8px 0" }}>
          {children}
        </Selected>
      ) : (
        <NotSelected>{children}</NotSelected>
      )}
    </Container>
  );
};

export default DocumentTab;
