import React, { FC, useEffect, useReducer } from "react";
import { useHistory, RouteComponentProps } from "react-router-dom";
import ReCaptcha from "react-google-recaptcha";

// styled
import {
  Form,
  TitleIconWrapper,
  FormHeader,
  FormTitle,
  FormSubtitle,
  FormFieldWrapper,
  TermsContainer,
  Terms,
  ContinueIconWrapper,
  RecaptchaWrapper,
} from "./FormClientPhone.styled";
import { theme } from "../../styles/theme";

// components
import CteNumInput from "../ui/ClientNumInput";
import StyledCheckbox from "../ui/StyledCheckbox";
import ContinueBtn from "../ui/PrimaryBtn";

// icons
import { ReactComponent as CellphoneIcon } from "../../assets/icons/CellphoneIcon.svg";
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// utils
import { isNumber } from "../../utils/numberUtils";
import {
  validateCtePhone,
  validateCtePhoneConf,
} from "./FormClientPhone.utils";
import { defaultState, validationReducer } from "./validation.reducer";

// hooks
import useMediaQuery from "../../hooks/useMedia";

const FormClientPhone: FC<RouteComponentProps> = ({ match }) => {
  const isMobile = useMediaQuery(theme.mediaquerys.mobile);
  const [
    {
      ctePhoneConfValidation,
      ctePhoneValidation,
      ctePhoneConf,
      ctePhonePrxConf,
      termsChecked,
      recaptchaVerified,
    },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);
  const mainFormDispatcher = useMasterFormDispatch();
  const { ctePhonePrefix, ctePhone, currentStep } = useMasterForm();
  const phoneValidated =
    ctePhoneValidation.validated &&
    ctePhoneValidation.valid &&
    ctePhoneConfValidation.validated &&
    ctePhoneConfValidation.valid;
  const history = useHistory();

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.2 });
  }, [mainFormDispatcher]);

  useEffect(() => {
    if (currentStep > 0.2) {
      const validation = validateCtePhone(ctePhone);
      validationDispatcher({ type: "SET_CTEPHONE_VALIDATION", validation });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const goToOTP = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.3 });
    history.push(`${match.url}/otp`);
  };

  const handlePhoneChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    if (
      !e.target.value ||
      isNumber(e.target.value.charAt(e.target.value.length - 1))
    ) {
      mainFormDispatcher({
        type: "SET_CTE_PHONE",
        value: e.target.value,
      });
    }
  };

  const handleCtePhoneBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    e.preventDefault();
    const validation = validateCtePhone(ctePhone);
    validationDispatcher({ type: "SET_CTEPHONE_VALIDATION", validation });
    if (ctePhoneConf.length === 10) {
      const confValidation = validateCtePhoneConf(ctePhone, ctePhoneConf);
      validationDispatcher({
        type: "SET_CTEPHONECONF_VALIDATION",
        validation: confValidation,
      });
    }
  };

  const handlePhoneConfChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    if (
      !e.target.value ||
      isNumber(e.target.value.charAt(e.target.value.length - 1))
    ) {
      validationDispatcher({
        type: "SET_CTEPHONECONF",
        ctePhoneConf: e.target.value,
      });
    }
  };

  const handleCtePhoneConfBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    e.preventDefault();
    const validation = validateCtePhoneConf(ctePhone, ctePhoneConf);
    validationDispatcher({ type: "SET_CTEPHONECONF_VALIDATION", validation });
  };

  const verifyCaptcha = (response: string | null) => {
    if (response) {
      validationDispatcher({ type: "VERIFY_RECAPTCHA", value: true });
    } else {
      validationDispatcher({ type: "VERIFY_RECAPTCHA", value: false });
    }
  };

  const handleCheckbox = (e: React.ChangeEvent<HTMLInputElement>) => {
    validationDispatcher({ type: "TOGGLE_TERMS" });
  };

  return (
    <Form onSubmit={(e) => e.preventDefault()}>
      <FormHeader>
        {isMobile && (
          <FormTitle phoneValidated={phoneValidated}>
            {phoneValidated
              ? "Nmero de telfono mvil validado"
              : "Proporciona tu nmero de telfono mvil"}
          </FormTitle>
        )}
        <TitleIconWrapper>
          <span className="circle" />
          <CellphoneIcon />
        </TitleIconWrapper>
        {!isMobile && (
          <FormTitle phoneValidated={phoneValidated}>
            {phoneValidated
              ? "Nmero de telfono mvil validado"
              : "Proporciona tu nmero de telfono mvil"}
          </FormTitle>
        )}
      </FormHeader>
      {!phoneValidated && (
        <FormSubtitle>Nmero de telfono mvil de 10 dgitos</FormSubtitle>
      )}
      <FormFieldWrapper>
        <CteNumInput
          validationObj={ctePhoneValidation}
          value={ctePhone}
          prefix={ctePhonePrefix}
          changeFn={handlePhoneChange}
          blurFn={handleCtePhoneBlur}
          placeholder="Nmero de telfono"
        />
      </FormFieldWrapper>
      <FormFieldWrapper>
        <CteNumInput
          validationObj={ctePhoneConfValidation}
          value={ctePhoneConf}
          prefix={ctePhonePrxConf}
          changeFn={handlePhoneConfChange}
          blurFn={handleCtePhoneConfBlur}
          placeholder="Confirmar nmero de telfono"
          disabled={!ctePhoneValidation.validated || !ctePhoneValidation.valid}
        />
      </FormFieldWrapper>
      <TermsContainer>
        <StyledCheckbox
          id="terms-checkbox"
          checked={termsChecked}
          toogleFn={handleCheckbox}
        />
        <Terms>
          {"He Ledo y acepto los "}
          <a
            href="https://www.bradescard.com.mx/bradescard.site/avisolegal.html"
            target="_blank"
            rel="noopener noreferrer"
          >
            Trminos y condiciones
          </a>
          {" y el "}
          <a
            href="https://www.bradescard.com.mx/bradescard.site/aviso_privacidad.html"
            target="_blank"
            rel="noopener noreferrer"
          >
            Aviso de Privacidad
          </a>
          .
        </Terms>
      </TermsContainer>
      <RecaptchaWrapper>
        <ReCaptcha
          sitekey="6Ld3lOQZAAAAADIbU6qf0Q1yYLu4TPEIavC-I0AP"
          onChange={verifyCaptcha}
          hl="es-419"
        />
      </RecaptchaWrapper>
      <ContinueBtn
        disabled={!phoneValidated || !termsChecked || !recaptchaVerified}
        onClick={goToOTP}
      >
        Continuar
        <ContinueIconWrapper>
          <ContinueIcon />
        </ContinueIconWrapper>
      </ContinueBtn>
    </Form>
  );
};

export default FormClientPhone;
