import axios from "axios";
import { getDeviceType } from "../../utils/navigatorUtils";

// types
import { StartModelResponse } from "../../Types/api";

const { REACT_APP_API_PREFIX } = process.env;
interface queryRfc {
  queryKey: string[];
}

export const calculateRfc = async (ctx: queryRfc) => {
  const specialCharRegx = /[]/gi;
  const { queryKey } = ctx;
  if (queryKey[8]) {
    return null;
  }
  const fechaNacimiento = `${queryKey[5].padStart(
    2,
    "0"
  )}-${queryKey[6].padStart(2, "0")}-${queryKey[7]}`;
  const primerNombre = queryKey[1].trim();
  const segundoNombre = queryKey[2].trim();
  const apellidoPaterno = queryKey[3].trim();
  const apellidoMaterno = queryKey[4].trim();
  const faltaMaterno = apellidoMaterno ? 0 : 1;
  const faltaPaterno = apellidoPaterno ? 0 : 1;
  let banderaCaracterEspecial = 0;
  if (
    specialCharRegx.test(primerNombre) ||
    specialCharRegx.test(segundoNombre) ||
    specialCharRegx.test(apellidoPaterno) ||
    specialCharRegx.test(apellidoMaterno)
  ) {
    banderaCaracterEspecial = 1;
  }

  let token = localStorage.getItem("auth_token");

  const response = await axios.post(
    // CATALOGS END POINT
    `${REACT_APP_API_PREFIX}/Middleware/BusinessWorks/GeneraRFC`,
    // JSON BODY
    {
      banderaCaracterEspecial,
      faltaMaterno,
      faltaPaterno,
      fechaNacimiento,
      apellidoMaterno,
      apellidoPaterno,
      primerNombre,
      segundoNombre,
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};

interface querySaveDocs {
  queryKey: [
    string,
    StartModelResponse,
    string,
    string,
    string,
    string,
    string,
    string,
    string,
    string,
    string,
    File | null,
    File | null,
    File | null
  ];
}

export const saveDocuments = async (ctx: querySaveDocs) => {
  const { queryKey } = ctx;
  const {
    rfc,
    firstName,
    middleName,
    lastName,
    secondLastName,
    caseId: caseNumber,
    requestNumber: applicationFolio,
  } = queryKey[1];
  const address = `${queryKey[2].trim()} ${queryKey[3].trim()} ${queryKey[4].trim()} ${queryKey[5].trim()} ${queryKey[6].trim()} ${queryKey[7].trim()} ${queryKey[8].trim()} ${queryKey[9].trim()}`;
  const idSelected = queryKey[10];
  const ineAnverso = queryKey[11];
  const ineReverso = queryKey[12];
  const otherAnverso = queryKey[13];

  let token = localStorage.getItem("auth_token");

  const formData = new FormData();

  if (idSelected === "1") {
    if (ineAnverso && ineReverso) {
      formData.append(
        "files",
        ineAnverso.name.includes(".jpeg")
          ? new File([ineAnverso], "ineAnverso.jpg")
          : ineAnverso
      );
      formData.append(
        "files",
        ineReverso.name.includes(".jpeg")
          ? new File([ineReverso], "ineReverso.jpg")
          : ineReverso
      );
    }
  } else {
    if (otherAnverso) {
      formData.append(
        "files",
        otherAnverso.name.includes(".jpeg")
          ? new File([otherAnverso], "otroAnverso.jpg")
          : otherAnverso
      );
    }
  }
  formData.append("ApplicationFolio", applicationFolio);
  formData.append("CaseNumber", caseNumber);
  formData.append("FirstName", firstName);
  formData.append("MiddleName", middleName);
  formData.append("LastName", lastName);
  formData.append("SecondLastName", secondLastName);
  formData.append("Address", address);
  formData.append("Rfc", rfc);

  console.log(Object.fromEntries(formData));

  const response = await axios.post(
    // CATALOGS END POINT
    `${REACT_APP_API_PREFIX}/Middleware/ContentManagement/Documents/saveIdentificationDoc`,
    // JSON BODY
    formData,
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data;
};

