export const startModel = async (ctx: queryStartModel) => {
  const specialCharRegx = /[]/gi;
  const { queryKey } = ctx;
  const currentStep = queryKey[26];

  if (currentStep > 0.7 || currentStep === 0.2) return "return";

  const firstName = queryKey[1].trim();
  const middleName = queryKey[2].trim();
  const lastName = queryKey[3].trim();
  const secondLastName = queryKey[4].trim();
  let specialCharacterFlag = 0;
  if (
    specialCharRegx.test(firstName) ||
    specialCharRegx.test(middleName) ||
    specialCharRegx.test(lastName) ||
    specialCharRegx.test(secondLastName)
  ) {
    specialCharacterFlag = 1;
  }
  const dateofBirth = `${queryKey[7]}-${queryKey[6].padStart(
    2,
    "0"
  )}-${queryKey[5].padStart(2, "0")}`;
  const gender = +queryKey[8];
  const nationality = +queryKey[9];
  const rfc = queryKey[10].trim();
  const street = queryKey[11].trim();
  const externalNumber = queryKey[12].trim();
  const zipCode = queryKey[13];
  const neighborhood = queryKey[14].trim();
  const municipality = queryKey[15].trim();
  const city = queryKey[16].trim();
  const state = queryKey[17].trim();
  const partnerId = +queryKey[18];
  const deviceTypeId = +getDeviceType();
  const lastNameFlag = lastName ? 0 : 1;
  const secondLastNameFlag = secondLastName ? 0 : 1;
  const idType = queryKey[19] === "1" ? 1 : 2;
  const studentFlag = queryKey[20];
  const incomeSourceId = queryKey[21];
  const otp = queryKey[22];
  const celular = queryKey[23];
  const {
    data: { ip },
  } = await axios.get("https://api.ipify.org?format=json");
  const requestNumber = queryKey[24];
  const requestDate = queryKey[25];

  let token = localStorage.getItem("auth_token");

  console.log({
    requestNumber,
    requestDate,
    firstName,
    middleName,
    lastName,
    secondLastName,
    dateofBirth,
    gender,
    nationality,
    rfc,
    street,
    externalNumber,
    zipCode,
    neighborhood,
    municipality,
    city,
    state,
    partnerId,
    productName: "VISA",
    deviceTypeId,
    specialCharacterFlag,
    tabletFlag: deviceTypeId === 1 ? 1 : 0,
    lastNameFlag,
    secondLastNameFlag,
    idNumber: null,
    idType,
    studentFlag,
    incomeSourceId,
    otp,
    idTipoDispositivo: deviceTypeId,
    ip,
    celular,
    onlineProcess: true,
    privacyPolicyFlag: "1",
  });

  const response = await axios.post(
    // TOKEN ENDPOINT
    `${REACT_APP_API_PREFIX}/Middleware/Amx/ModeloBr`,
    {
      requestNumber,
      requestDate,
      firstName,
      middleName,
      lastName,
      secondLastName,
      dateofBirth,
      gender,
      nationality,
      rfc,
      street,
      externalNumber,
      zipCode,
      neighborhood,
      municipality,
      city,
      state,
      partnerId,
      productName: "VISA",
      deviceTypeId,
      specialCharacterFlag,
      tabletFlag: deviceTypeId === 1 ? 1 : 0,
      lastNameFlag,
      secondLastNameFlag,
      idNumber: null,
      idType,
      studentFlag,
      incomeSourceId,
      otp,
      idTipoDispositivo: deviceTypeId,
      ip,
      celular,
      onlineProcess: true,
      privacyPolicyFlag: "1",
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};
