import axios from "axios";
import { QueryFunctionContext } from "react-query";

// types
import { ValidationObject } from "../../Types/validationTypes";

// utils
import { getDeviceType } from "../../utils/navigatorUtils";

const { REACT_APP_API_PREFIX } = process.env;

export const validateUpperOTP = (upperOTP: string, lowerOTP: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (upperOTP.trim().length < 6) {
    validation.msg = "ingresa un OTP vlido";
    validation.valid = false;
  } else if (lowerOTP.length && upperOTP !== lowerOTP) {
    validation.msg = "Los valores no coinciden";
    validation.valid = false;
  }

  return validation;
};

export const validateLowerOTP = (upperOTP: string, lowerOTP: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (lowerOTP.length && upperOTP !== lowerOTP) {
    validation.msg = "Los valores no coinciden";
    validation.valid = false;
  }

  return validation;
};

export const verifyOTPEndpoint = async () => {
  let token = localStorage.getItem("auth_token");
  const response = await axios.get(
    // Get email domains selector end point
    `${REACT_APP_API_PREFIX}/Commons/Otp`,
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};

export const requestOTP = async (ctx: QueryFunctionContext) => {
  const { queryKey } = ctx;
  let token = localStorage.getItem("auth_token");
  const {
    data: { ip },
  } = await axios.get("https://api.ipify.org?format=json");
  const deviceType = getDeviceType();
  const phone = queryKey[1];
  const prodId = queryKey[2];
  const partner = queryKey[3];

  const response = await axios.post(
    // CATALOGS END POINT
    `${REACT_APP_API_PREFIX}/Commons/Otp/generarOtp`,
    // JSON BODY
    {
      avisoDePrivacidad: "1",
      celular: phone,
      idProducto: prodId,
      idSocio: partner,
      idTipoDispositivo: deviceType,
      ipDispositivo: ip,
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );

  return response.data.code;
};

export const validateOTP = async (ctx: QueryFunctionContext) => {
  const { queryKey } = ctx;
  let token = localStorage.getItem("auth_token");
  const otp = queryKey[1];

  const response = await axios.post(
    // CATALOGS END POINT
    `${REACT_APP_API_PREFIX}/Commons/Otp/consultarOtp`,
    // JSON BODY
    {
      otp,
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};
