import styled, { keyframes } from "styled-components";

const cloudAnimation = keyframes`
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
`;

const blingAnimation = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;

const rotation = keyframes`
0% {
  transform: rotate(0);
}
100% {
  transform: rotate(360deg);
}
`;

const truckAnimation = keyframes`
0% {
  transform: translateY(0);
}
80% {
  transform: translateY(0);
}
100% {
  transform: translateY(3px);
}
`;

export const AnimationWrapper = styled.div`
  margin-top: 50px;
  margin-bottom: 30px;

  #Clouds1 {
    animation: ${cloudAnimation} 3000ms linear infinite;
  }
  #Clouds2 {
    animation: ${cloudAnimation} 3000ms linear 200ms infinite;
  }
  #Lines1 {
    animation: ${cloudAnimation} 2000ms linear 100ms infinite;
  }
  #Lines2 {
    animation: ${cloudAnimation} 2000ms linear 200ms infinite;
  }
  #Lines3 {
    animation: ${cloudAnimation} 2000ms linear 300ms infinite;
  }
  #Cross1 {
    opacity: 0;
    animation: ${blingAnimation} 2000ms ease infinite alternate;
  }
  #Cross2 {
    opacity: 0;
    animation: ${blingAnimation} 2000ms ease 400ms infinite alternate;
  }
  #Cross3 {
    opacity: 0;
    animation: ${blingAnimation} 2000ms ease 800ms infinite alternate;
  }
  #Cross4 {
    opacity: 0;
    animation: ${blingAnimation} 2000ms ease 1200ms infinite alternate;
  }
  #Tire1 {
    transform-box: fill-box;
    transform-origin: center;
    animation: ${rotation} 1000ms linear infinite;
  }
  #Tire2 {
    transform-box: fill-box;
    transform-origin: center;
    animation: ${rotation} 1000ms linear infinite;
  }
  #Truck {
    animation: ${truckAnimation} 1500ms ease-in-out infinite alternate;
  }
`;
