import axios from "axios";

const { REACT_APP_API_PREFIX } = process.env;

export const closeDataModel = async (
  id: string,
  version: string,
  partnerId: number
) => {
  let token = localStorage.getItem("auth_token");

  console.log({
    id,
    version,
    partnerId,
  });

  const response = await axios.post(
    // TOKEN ENDPOINT
    `${REACT_APP_API_PREFIX}/Middleware/Amx/AmxGateway/cancel`,
    {
      id,
      version,
      partnerId,
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};
