import React, { FC, useReducer, useEffect, useRef } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import { useQuery } from "react-query";

// styles
import {
  Form,
  FieldsContainer,
  NumbersContainer,
  SelectorsContainer,
  Selector,
  SelectorCell,
  ZipCodeWrapper,
  ZipCodesContainer,
  FetchingSpinnerContainer,
  ContainerZipCode,
  ErrorZipCode,
} from "./PersonalData2.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";

// components
import TextDataInput from "../ui/TextDataInput";
import NumberDataInput from "../ui/NumberDataInput";
import SelectorTab from "../ui/DocumentTab";
import ContinueBtn from "../ui/PrimaryBtn";
import SelectorInput from "../ui/SelectorInput";
import SmallSpinner from "../ui/SmallSpinner";
import ZipCodeResults from "../ui/ZipCodeResults";

// utils
import {
  validateNonEmpty,
  validateStreetNums,
  validateZIPCode,
  validateCouldBeEmpty,
  getZipCodeData,
} from "./PersonalData2.utils";
import { validationReducer, defaultState } from "./validation.reducer";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// hooks
import useOutsideClick from "../../hooks/useOutsideClick";

const NationalityOptions = [
  { text: "Mexicana", value: "1" },
  { text: "Extranjera", value: "13" },
];

const PersonalData2: FC<RouteComponentProps> = ({ match }) => {
  const zipRef = useRef<null | HTMLInputElement>(null);
  const previewCodesRef = useRef<null | HTMLDivElement>(null);
  const history = useHistory();
  const {
    step,
    nationality,
    street,
    numExt,
    numInt,
    zipCod,
    col,
    mun,
    state,
    city,
    student,
    finance,
    isCPSelected,
  } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const [
    {
      cityValidation,
      colValidation,
      munValidation,
      numExtValidation,
      numIntValidation,
      stateValidation,
      streetValidation,
      zipCodeValidation,
      zipCodePreviews,
      isError,
    },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);
  const { data, isFetching, refetch } = useQuery(
    ["getZipCodeData", zipCod],
    getZipCodeData,
    {
      keepPreviousData: true,
      enabled: false,
      retry: 1,
      onSuccess: () => {
        validationDispatcher({ type: "SET_IS_ERROR", value: false });
      },
      onError: (error) => {
        validationDispatcher({ type: "SET_IS_ERROR", value: true });
      },
    }
  );
  const { codigosPostales } = data || { codigosPostales: [] };

  useEffect(() => {
    if (step < 0.65) {
      history.goBack();
    }
  }, [step, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.65 });
  }, [mainFormDispatcher]);

  useEffect(() => {
    if (zipCod.length === 5 && document.activeElement === zipRef.current) {
      if (!zipCodePreviews) {
        validationDispatcher({ type: "TOGGLE_ZIPCODE_PREVIEWS" });
      }
      refetch();
    }
  }, [zipCod, refetch, zipCodePreviews]);

  useEffect(() => {
    if (zipCod && state && mun && col && city) {
      const cityVal = validateNonEmpty(city);
      const colVal = validateNonEmpty(col);
      const munVal = validateNonEmpty(mun);

      const stateVal = validateNonEmpty(state);

      const zipVal = validateZIPCode(zipCod);
      validationDispatcher({
        type: "VALIDATE_CP_SELECTION",
        cityVal,
        colVal,
        munVal,
        stateVal,
        zipVal,
      });
    }
  }, [zipCod, state, mun, col, city]);

  useEffect(() => {
    let cityVal = { validated: false };
    let colVal = { validated: false };
    let munVal = { validated: false };
    let nacVal = { validated: false };
    let numExtVal = { validated: false };
    let numIntVal = { validated: false };
    let stateVal = { validated: false };
    let strVal = { validated: false };
    let zipVal = { validated: false };

    if (city) {
      cityVal = validateNonEmpty(city);
    }
    if (col) {
      colVal = validateNonEmpty(col);
    }
    if (mun) {
      munVal = validateNonEmpty(mun);
    }
    if (nationality) {
      nacVal = validateNonEmpty(nationality);
    }
    if (numExt) {
      numExtVal = validateStreetNums(numExt);
    }
    if (numInt) {
      numIntVal = validateCouldBeEmpty(numInt);
    }
    if (state) {
      stateVal = validateNonEmpty(state);
    }
    if (street) {
      strVal = validateNonEmpty(street);
    }
    if (zipCod) {
      zipVal = validateZIPCode(zipCod);
    }

    validationDispatcher({
      type: "INIT_ALL_VALIDATIONS",
      cityVal,
      colVal,
      munVal,
      nacVal,
      numExtVal,
      numIntVal,
      stateVal,
      strVal,
      zipVal,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.675 });
    history.push(match.url.replace("/2", "/confirmar"));
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.toUpperCase();
    mainFormDispatcher({
      type: "SET_FIRST_PERSONAL_DATA",
      field: e.target.id,
      value: value,
    });
  };

  const handleNatChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    mainFormDispatcher({
      type: "SET_NATIONALITY_TYPE",
      value: e.target.value,
    });
  };

  const handleChangeOnlyText = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.toUpperCase();
    if (value && !/[a-z. ]/gi.test(value.substr(-1))) return;
    mainFormDispatcher({
      type: "SET_FIRST_PERSONAL_DATA",
      field: e.target.id,
      value: value,
    });
  };

  const handleChangeOnlyNums = (e: React.ChangeEvent<HTMLInputElement>) => {
    const field = e.target.id;
    const value = e.target.value;
    if (value.length > 5 || (value && !/[0-9]/gi.test(value.substr(-1))))
      return;
    mainFormDispatcher({
      type: "SET_FIRST_PERSONAL_DATA",
      field,
      value,
    });
  };

  const handleFocusZip = (e: React.FocusEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (value.length === 5) {
      if (!zipCodePreviews) {
        validationDispatcher({ type: "TOGGLE_ZIPCODE_PREVIEWS" });
      }
      refetch();
    }
  };

  const handleChangeStreetNums = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.toUpperCase();
    if (value && !/[0-9 a-z-]/gi.test(value.substr(-1))) return;
    mainFormDispatcher({
      type: "SET_FIRST_PERSONAL_DATA",
      field: e.target.id,
      value: value,
    });
  };

  const handleBlurNonEmpty = (
    action:
      | "SET_STREET_VALIDATION"
      | "SET_COL_VALIDATION"
      | "SET_MUN_VALIDATION"
      | "SET_STATE_VALIDATION"
      | "SET_CITY_VALIDATION",
    field: string
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    e.preventDefault();
    const validation = validateNonEmpty(field);
    validationDispatcher({ type: action, validation });
  };

  const handleBlurCouldBeEmpty = (
    action: "SET_NUMINT_VALIDATION",
    field: string
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    e.preventDefault();
    const validation = validateCouldBeEmpty(field);
    validationDispatcher({ type: action, validation });
  };

  const handleBlurStreetNums = (
    action: "SET_NUMEXT_VALIDATION",
    field: string
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    e.preventDefault();
    const validation = validateStreetNums(field);
    validationDispatcher({ type: action, validation });
  };

  const handleBlurZIPCode = (
    action: "SET_ZIPCODE_VALIDATION",
    field: string
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    e.preventDefault();
    if (zipCodeValidation.validated && !zipCodeValidation.valid) return;
    const validation = validateZIPCode(field);
    validationDispatcher({ type: action, validation });
  };

  const handleStudySelection = (student: number) => (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_STUDENT", student });
  };

  const handleFinanceSelection = (finance: number) => (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_FINANCE", finance });
  };

  const closeZipPreview = () => {
    if (zipCodePreviews) {
      validationDispatcher({ type: "TOGGLE_ZIPCODE_PREVIEWS" });
    }
  };

  useOutsideClick(previewCodesRef, zipCodePreviews, closeZipPreview);

  return (
    <Form autoComplete="off" onSubmit={(e) => e.preventDefault()}>
      <input type="password" style={{ display: "none" }} readOnly />
      <FormHeader>
        <FormTitle bolder style={{ marginTop: 0 }}>
          Captura rpida
        </FormTitle>
      </FormHeader>
      <FormSubtitle>
        Texto para instrucciones - texto para instrucciones
      </FormSubtitle>
      <FieldsContainer>
        <SelectorInput
          labelTxt="Nacionalidad"
          first
          value={nationality}
          options={NationalityOptions}
          onChange={handleNatChange}
          id="nationality"
        />

        <TextDataInput
          labelTxt="Calle"
          first
          onChange={handleChange}
          onBlur={handleBlurNonEmpty("SET_STREET_VALIDATION", street)}
          value={street}
          id={"street"}
          validation={streetValidation}
        />

        <NumbersContainer>
          <TextDataInput
            labelTxt="Nm. Exterior"
            first
            onChange={handleChangeStreetNums}
            onBlur={handleBlurStreetNums("SET_NUMEXT_VALIDATION", numExt)}
            value={numExt}
            id={"numExt"}
            validation={numExtValidation}
          />

          <TextDataInput
            labelTxt="Nm. Interior"
            first
            onChange={handleChangeStreetNums}
            onBlur={handleBlurCouldBeEmpty("SET_NUMINT_VALIDATION", numInt)}
            value={numInt}
            id={"numInt"}
            validation={numIntValidation}
          />
          <ZipCodeWrapper>
            <NumberDataInput
              labelTxt="Cdigo Postal"
              first
              onChange={handleChangeOnlyNums}
              onBlur={handleBlurZIPCode("SET_ZIPCODE_VALIDATION", zipCod)}
              value={zipCod}
              id={"zipCod"}
              validation={zipCodeValidation}
              onFocus={handleFocusZip}
              ref={zipRef}
            />
            {zipCodePreviews && (
              <ZipCodesContainer>
                {isError ? (
                  <ContainerZipCode>
                    <ErrorZipCode>
                      Algo sali mal, puede que el cdigo postal no sea vlido.
                    </ErrorZipCode>
                  </ContainerZipCode>
                ) : isFetching ? (
                  <FetchingSpinnerContainer>
                    <SmallSpinner />
                  </FetchingSpinnerContainer>
                ) : (
                  codigosPostales.length && (
                    <ZipCodeResults
                      codigosPostales={codigosPostales}
                      ref={previewCodesRef}
                      closeZipPreview={closeZipPreview}
                    />
                  )
                )}
              </ZipCodesContainer>
            )}
          </ZipCodeWrapper>
        </NumbersContainer>

        <TextDataInput
          labelTxt="Colonia"
          first
          onChange={handleChange}
          onBlur={handleBlurNonEmpty("SET_COL_VALIDATION", col)}
          value={col}
          id={"col"}
          validation={colValidation}
        />

        <TextDataInput
          labelTxt="Municipio/Delegacin"
          first
          onChange={handleChange}
          onBlur={handleBlurNonEmpty("SET_MUN_VALIDATION", mun)}
          value={mun}
          id={"mun"}
          validation={munValidation}
          disabled={isCPSelected}
        />

        <TextDataInput
          labelTxt="Estado"
          first
          onChange={handleChangeOnlyText}
          onBlur={handleBlurNonEmpty("SET_STATE_VALIDATION", state)}
          value={state}
          id={"state"}
          validation={stateValidation}
          disabled={isCPSelected}
        />

        <TextDataInput
          labelTxt="Ciudad"
          first
          onChange={handleChangeOnlyText}
          onBlur={handleBlurNonEmpty("SET_CITY_VALIDATION", city)}
          value={city}
          id={"city"}
          validation={cityValidation}
          disabled={isCPSelected}
        />
      </FieldsContainer>
      <SelectorsContainer>
        <SelectorCell>
          <h3>Estudias actualmente?</h3>
          <Selector>
            <SelectorTab
              selected={student === 1}
              onClick={handleStudySelection(1)}
            >
              S
            </SelectorTab>
            <SelectorTab
              selected={student === 0}
              onClick={handleStudySelection(0)}
            >
              No
            </SelectorTab>
          </Selector>
        </SelectorCell>
        <SelectorCell>
          <h3>Fuente de ingreso</h3>
          <Selector className="finance">
            <SelectorTab
              selected={finance === 1}
              onClick={handleFinanceSelection(1)}
            >
              Propio
            </SelectorTab>
            <SelectorTab
              selected={finance === 2}
              onClick={handleFinanceSelection(2)}
            >
              Padres
            </SelectorTab>
            <SelectorTab
              selected={finance === 3}
              onClick={handleFinanceSelection(3)}
            >
              Conyuge
            </SelectorTab>
          </Selector>
        </SelectorCell>
      </SelectorsContainer>
      <ContinueBtn
        disabled={
          isFetching ||
          !streetValidation.validated ||
          !streetValidation.valid ||
          !numExtValidation.validated ||
          !numExtValidation.valid ||
          !zipCodeValidation.validated ||
          !zipCodeValidation.valid ||
          !colValidation.validated ||
          !colValidation.valid ||
          !munValidation.validated ||
          !munValidation.valid ||
          !stateValidation.validated ||
          !stateValidation.valid ||
          !cityValidation.validated ||
          !cityValidation.valid ||
          student === null ||
          finance === null ||
          (numIntValidation.validated && !numIntValidation.valid)
        }
        onClick={handleContinue}
      >
        Continuar
        <ContinueIconWrapper>
          <ContinueIcon />
        </ContinueIconWrapper>
      </ContinueBtn>
    </Form>
  );
};

export default PersonalData2;
