import React, { FC } from "react";
import { useParams, Redirect, useHistory } from "react-router-dom";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { useQuery } from "react-query";

// sytles
import {
  Container,
  MainTitle,
  DescTitle,
  DescTable,
  TableCell,
  NewTransaction,
  Footer,
  MobileBackDecoration,
  MainTitleContainer,
  RightSideTitle,
  MainWrapper,
  FetchingSpinnerContainer,
} from "./Partner.styled";
import { CornerDecoration, PurpleLine } from "../Home/Home.styled";
import { theme } from "../../styles/theme";

// icons
import { ReactComponent as NewIcon } from "../../assets/icons/NewTrans.svg";

// hooks
import useMediaQuery from "../../hooks/useMedia";

// Context
import { useMasterFormDispatch } from "../../Context/MainForm";

// constants
import { partnerData } from "../../constants/partnerData";

// utils
import { flasher } from "../../utils/Flasher";
import { requestNewFolio } from "./Partner.utils";

// components
import SmallSpinner from "../../components/ui/SmallSpinner";

const { REACT_APP_ROUTER_PREFIX } = process.env;

const Partner: FC = () => {
  const history = useHistory();
  const { socio }: { socio: string } = useParams();
  const isMidHeiht = useMediaQuery(theme.mediaquerys.heightMed);
  const isMobile = useMediaQuery(theme.mediaquerys.mobile);
  const mainFormDispatcher = useMasterFormDispatch();
  const { refetch, isFetching } = useQuery(
    ["requestNewFolio", partnerData[socio].id.toString()],
    requestNewFolio,
    {
      enabled: false,
      onError: (error) => {
        console.error(error);
        flasher(
          "Algo sali mal al iniciar el proceso de tramite, intente de nuevo ms tarde",
          "warning"
        );
      },
      onSuccess: (folioRes) => {
        if (folioRes) {
          const { folio, date } = folioRes;
          mainFormDispatcher({
            type: "INIT_PROCESS",
            partner: partnerData[socio].id.toString(),
            idProdType: partnerData[socio].idProdType.toString(),
            requestDate: date,
            requestNumber: folio,
          });
          history.push(
            REACT_APP_ROUTER_PREFIX?.length === 1
              ? `/new/${socio}`
              : `${REACT_APP_ROUTER_PREFIX}/new/${socio}`
          );
        }
      },
    }
  );

  const handleInitProcess = (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    refetch();
  };

  if (!partnerData[socio]) {
    return <Redirect to="/" />;
  }

  return (
    <MainWrapper>
      <Container>
        {isMobile && <MobileBackDecoration />}
        <MainTitleContainer>
          {!isMobile && (
            <LazyLoadImage
              alt={partnerData[socio].alt}
              src={partnerData[socio].url}
              height={isMidHeiht ? "160px" : "210px"}
              style={{ zIndex: 2 }}
            />
          )}
          <RightSideTitle>
            <MainTitle>
              Tramtala! <br />
              {partnerData[socio].title}
            </MainTitle>
            {!isMobile &&
              (isFetching ? (
                <FetchingSpinnerContainer>
                  <SmallSpinner />
                </FetchingSpinnerContainer>
              ) : (
                <NewTransaction onClick={handleInitProcess}>
                  <span className="circle">
                    <NewIcon />
                  </span>
                  <strong>Nuevo Trmite</strong>
                </NewTransaction>
              ))}
          </RightSideTitle>
          {isMobile && (
            <>
              <LazyLoadImage
                alt={partnerData[socio].alt}
                src={partnerData[socio].url}
                height={"170px"}
                style={{ zIndex: 2 }}
              />
              {isFetching ? (
                <FetchingSpinnerContainer>
                  <SmallSpinner />
                </FetchingSpinnerContainer>
              ) : (
                <NewTransaction onClick={handleInitProcess}>
                  <span className="circle">
                    <NewIcon />
                  </span>
                  <strong>Nuevo Trmite</strong>
                </NewTransaction>
              )}
            </>
          )}
        </MainTitleContainer>
        <DescTitle>Beneficios exclusivos</DescTitle>
        <DescTable>
          {partnerData[socio].benefits.map((b) => (
            <TableCell key={b.id}>
              <LazyLoadImage
                alt="beneficio"
                src={b.icon}
                style={{ marginRight: 5 }}
                width={"42px"}
              />
              <p>{b.text}</p>
            </TableCell>
          ))}
        </DescTable>
        <Footer>
          <span>Sujeto a aprobacin de crdito.</span>
          <span>
            Tarjetas emitidas y operadas por Bradescard Mxico S. de R.L.
          </span>
        </Footer>
      </Container>
      {!isMobile && (
        <>
          <CornerDecoration
            style={{
              zIndex: -1,
              bottom: "-500px",
              backgroundColor: "#F8FAFB",
            }}
          />
          <PurpleLine right="-200px" bottom="20%" style={{ zIndex: -1 }} />
          <PurpleLine right="300px" bottom="-30px" style={{ zIndex: -1 }} />
        </>
      )}
    </MainWrapper>
  );
};

export default Partner;
