import React, { FC } from "react";

// styles
import {
  PhoneInput,
  PhoneNumWrapper,
  ValidationIconWrapper,
} from "./ClientNumInput.styled";
import { theme } from "../../../styles/theme";

// components
import ValidationMsg from "../ValidationErrorMsg";

// icons
import { ReactComponent as CheckIcon } from "../../../assets/icons/CheckMark.svg";
import { ReactComponent as CrossIcon } from "../../../assets/icons/Cross.svg";

// types
import { ValidationObject } from "../../../Types/validationTypes";

// hooks
import useMediaQuery from "../../../hooks/useMedia";

interface ClientNumInputProps {
  validationObj: ValidationObject;
  pfxChangeFn?: (e: React.ChangeEvent<HTMLInputElement>) => void;
  changeFn: (e: React.ChangeEvent<HTMLInputElement>) => void;
  blurFn: (e: React.FocusEvent<HTMLInputElement>) => void;
  prefix: string;
  value: string;
  placeholder: string;
  disabled?: boolean;
}

const ClientNumInput: FC<ClientNumInputProps> = ({
  validationObj,
  pfxChangeFn,
  changeFn,
  blurFn,
  prefix,
  value,
  placeholder,
  disabled,
}) => {
  const isMobile = useMediaQuery(theme.mediaquerys.mobile);

  return (
    <>
      <PhoneNumWrapper
        invalid={validationObj.validated && !validationObj.valid}
        validated={validationObj.validated && validationObj.valid}
      >
        <span>+</span>
        <PhoneInput
          type="tel"
          maxLength={2}
          size={2}
          onChange={pfxChangeFn}
          value={prefix}
          inputWidth={isMobile ? 30 : 50}
          disabled
          isPrefix
        />
        <PhoneInput
          type="tel"
          maxLength={10}
          onChange={changeFn}
          value={value}
          placeholder={placeholder}
          onBlur={blurFn}
          invalid={validationObj.validated && !validationObj.valid}
          disabled={disabled}
        />
        {validationObj.validated && (
          <ValidationIconWrapper invalid={!validationObj.valid}>
            {validationObj.valid ? <CheckIcon /> : <CrossIcon />}
          </ValidationIconWrapper>
        )}
      </PhoneNumWrapper>
      {validationObj.validated && !validationObj.valid && (
        <ValidationMsg>{validationObj.msg}</ValidationMsg>
      )}
    </>
  );
};

export default ClientNumInput;
