import styled, { keyframes } from "styled-components";

const cardAnimation = keyframes`
  0% {
    transform: translateY(-100%);
  }
  30%, 50% {
    transform: translateY(-50%);
  }
  100% {
    transform: translateY(0%);
  }
`;

const blingAnimation = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;

export const AnimationWrapper = styled.div`
  margin-top: 60px;
  margin-bottom: 30px;
  transform: translateX(-20px);

  #Cross1 {
    opacity: 0;
    animation: ${blingAnimation} 2000ms ease infinite alternate;
  }
  #Cross2 {
    opacity: 0;
    animation: ${blingAnimation} 2000ms ease 400ms infinite alternate;
  }
  #Cross3 {
    opacity: 0;
    animation: ${blingAnimation} 2000ms ease 800ms infinite alternate;
  }
  #Cross4 {
    opacity: 0;
    animation: ${blingAnimation} 2000ms ease 1200ms infinite alternate;
  }
  #Card {
    transform: translateY(-100%);
    animation: ${cardAnimation} 3000ms ease 1200ms infinite;
  }
`;
