import React, { FC } from "react";

// styles
import { Checkbox, Container } from "./StyledCheckbox.styled";

interface StyledCheckboxProps {
  toogleFn: (e: React.ChangeEvent<HTMLInputElement>) => void;
  checked: boolean;
  id: string;
}

const StyledCheckbox: FC<StyledCheckboxProps> = ({ toogleFn, checked, id }) => {
  return (
    <Container>
      <label htmlFor={id}>
        <input id={id} type="checkbox" onChange={toogleFn} checked={checked} />
        <Checkbox className="checkmark" />
      </label>
    </Container>
  );
};

export default StyledCheckbox;
