// types
import { ValidationObject } from "../../Types/validationTypes";

export const validateNameField = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  if (!field.trim().length || field.trim().length < 2) {
    validation.valid = false;
    validation.msg = "Ingrese un valor correcto";
  }

  return validation;
};

export const validateSecondname = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  if (field.trim().length && field.trim().length < 2) {
    validation.valid = false;
    validation.msg = "Ingrese un valor correcto";
  }

  return validation;
};

export const validateSurnames = (field: string, otherField: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (!field.trim().length && !otherField.trim().length) {
    validation.valid = false;
    validation.msg = "Debes ingresar almenos un apellido";
  } else if (field.trim().length && field.trim().length < 2) {
    validation.valid = false;
    validation.msg = "Ingrese un valor correcto";
  }

  return validation;
};

export const validateBirthDate = (day: string, month: string, year: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  const today = new Date();
  const localYear = today.getFullYear();
  const localMonth = today.getMonth() + 1;
  const localDay = today.getDate();
  if (!day || !month || !year) {
    validation.valid = false;
    validation.msg = "Ingresa una fecha vlida";
  } else if (
    +year === localYear - 18 &&
    (+month > localMonth || (+month === localMonth && +day > localDay))
  ) {
    validation.valid = false;
    validation.msg = "Debes ser mayor de 18 aos";
  }

  return validation;
};
