// types
import { defaultSelectorOption } from "../../Types/selectorOption";

type Action =
  | {
      type: "SET_EMPLOY_TYPES";
      value: defaultSelectorOption[];
    }
  | {
      type: "SET_EMPLOY_AREAS";
      value: defaultSelectorOption[];
    }
  | {
      type: "SET_PAY_DAYS";
      value: defaultSelectorOption[];
    };
type State = {
  employTypeOptions: defaultSelectorOption[];
  employAreaOptions: defaultSelectorOption[];
  payDateOptions: defaultSelectorOption[];
};

export const completeDefaultState: State = {
  employTypeOptions: [{ text: "Empleado", value: "Empleado" }],
  employAreaOptions: [{ text: "Empleado", value: "Empleado" }],
  payDateOptions: [
    { text: "Da 1 de cada mes", value: "1delMes" },
    { text: "Da 15 de cada mes", value: "15delMes" },
    { text: "Da 30 de cada mes", value: "30delMes" },
  ],
};

export const completeDataReducer = (
  state: State = completeDefaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_EMPLOY_TYPES":
      return { ...state, employTypeOptions: action.value };
    case "SET_EMPLOY_AREAS":
      return { ...state, employAreaOptions: action.value };
    case "SET_PAY_DAYS":
      return { ...state, payDateOptions: action.value };
    default:
      return state;
  }
};
