import React, { FC, useEffect, useCallback, useState } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import { useQuery } from "react-query";
import ReactModal from "react-modal";

// styles
import { AnimationWrapper } from "./ProcesandoAinm2.styled";
import { Container, Title } from "../ProcesandoAnim/ProcesandoAnim.styled";
import { FormSubtitle } from "../FormClientPhone/FormClientPhone.styled";

// Icons
import { ReactComponent as Truck } from "../../assets/icons/TruckAnimation.svg";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// utils
import {
  calculateRfc,
  startModel,
  saveDocuments,
} from "./ProcesandoAnim2.utils";
import { flasher } from "../../utils/Flasher";
import { closeDataModel } from "../../utils/closeModel";

// components
import RejectionMessage from "../RejectionMensage";

ReactModal.setAppElement("body");

const ProcesandoAnim2: FC<RouteComponentProps> = ({ match }) => {
  const [{ isOpen, rejectionMessage }, setModalState] = useState({
    isOpen: false,
    rejectionMessage: "",
  });
  const [savingDocs, setSavingDocs] = useState(false);
  const mainFormDispatcher = useMasterFormDispatch();
  const history = useHistory();
  const {
    name,
    secondName,
    fatherSurname,
    motherSurname,
    birthDay,
    birthMonth,
    birthYear,
    rfc,
    street,
    numExt,
    zipCod,
    col,
    mun,
    city,
    state,
    partner,
    idSelected,
    student,
    finance,
    otpValue,
    ctePhone,
    nationality,
    genre,
    requestNumber,
    requestDate,
    startModelResponse,
    numInt,
    ineAnverso,
    ineReverso,
    residenteAnverso,
    currentStep,
  } = useMasterForm();

  const { refetch: saveDocs } = useQuery(
    [
      "saveDocuments",
      startModelResponse,
      street,
      numExt,
      numInt,
      zipCod,
      col,
      mun,
      state,
      city,
      idSelected,
      ineAnverso,
      ineReverso,
      residenteAnverso,
    ],
    saveDocuments,
    {
      onSuccess: (savDocRes) => {
        console.log(savDocRes);
        goToCapturaCompleta();
      },
      onError: () => {
        flasher(
          "Algo sali mal guardando tus documentos, intentalo de nuevo ms tarde",
          "warning"
        );
        history.push("/");
      },
      enabled: false,
    }
  );
  useQuery(
    [
      "calculateRfc",
      name,
      secondName,
      fatherSurname,
      motherSurname,
      birthDay,
      birthMonth,
      birthYear,
      rfc,
    ],
    calculateRfc,
    {
      onSuccess: (rfcRes) => {
        if (rfcRes) {
          const rfcCorto = rfcRes.rfcCorto;
          mainFormDispatcher({ type: "SET_RFC", rfc: rfcCorto });
        }
      },
      onError: () => {
        flasher(
          "Algo sali mal calculando el RFC intentalo de nuevo ms tarde",
          "warning"
        );
        history.goBack();
      },
      keepPreviousData: true,
    }
  );
  const { refetch } = useQuery(
    [
      "startModel",
      name,
      secondName,
      fatherSurname,
      motherSurname,
      birthDay,
      birthMonth,
      birthYear,
      genre,
      nationality,
      rfc,
      street,
      numExt,
      zipCod,
      col,
      mun,
      city,
      state,
      partner,
      idSelected,
      student,
      finance,
      otpValue,
      ctePhone,
      requestNumber,
      requestDate,
      currentStep,
    ],
    startModel,
    {
      onError: () => {
        flasher(
          "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
          "warning"
        );
        history.push("/");
      },
      onSettled: (modelRes) => {
        console.log(modelRes);
        if (modelRes === "return") {
          history.push("/");
          return;
        } else if (modelRes && modelRes.name) {
          switch (modelRes.name) {
            case "ShowNotification":
              closeDataModel(modelRes.id, modelRes.version, +partner);
              setModalState({
                isOpen: true,
                rejectionMessage: `estatus ${modelRes.notificationStatus}, ${modelRes.notificationReason}`,
              });
              return;
            case "FullCapture":
              mainFormDispatcher({
                type: "SET_STARTMODEL_RES",
                response: modelRes,
              });
              return setSavingDocs(true);
            case "Multiplecoincidences":
            case "ShowCustomerCoincidence":
              mainFormDispatcher({
                type: "SET_STARTMODEL_RES",
                response: modelRes,
              });
              return goToCoincidences();
            default:
              flasher(
                "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
                "warning"
              );
              history.push("/");
          }
        } else {
          flasher(
            "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
            "warning"
          );
          history.push("/");
        }
      },
      retry: false,
      enabled: false,
    }
  );

  useEffect(() => {
    if (startModelResponse && savingDocs) {
      saveDocs();
    }
  }, [startModelResponse, saveDocs, savingDocs]);

  useEffect(() => {
    if (currentStep < 0.7) {
      history.goBack();
    } else if (currentStep === 0.2) {
      history.push("/");
    }
  }, [currentStep, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.7 });
  }, [mainFormDispatcher]);

  const goToCapturaCompleta = useCallback(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.75 });
    history.push(match.url.replace("/procesando", "/captura-completa"));
  }, [mainFormDispatcher, match.url, history]);

  const goToCoincidences = useCallback(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.725 });
    history.push(match.url.replace("/procesando", "/valida-domicilio"));
  }, [mainFormDispatcher, match.url, history]);

  useEffect(() => {
    if (rfc) {
      refetch();
    }
    // eslint-disable-next-line
  }, [rfc]);

  return (
    <>
      <ReactModal
        isOpen={isOpen}
        className="modal-content"
        overlayClassName="modal-overlay"
        closeTimeoutMS={300}
      >
        <RejectionMessage msg={rejectionMessage} />
      </ReactModal>
      <Container>
        <AnimationWrapper>
          <Truck />
        </AnimationWrapper>
        <Title>Ya casi en tus manos!</Title>
        <FormSubtitle style={{ marginTop: 10, marginBottom: 5 }}>
          Estamos validando tus datos.
        </FormSubtitle>
      </Container>
    </>
  );
};

export default ProcesandoAnim2;
