import React, { FC, useEffect, Dispatch } from "react";

// styles
import { Container, SelectStyled } from "./BirthDate2.styled";
import { StyledLabel } from "../TextDataInput/TextDataInput.styled";

// utils
import { isLeapYear } from "../../../utils/dateUtils";

// types
import { ValidationObject } from "../../../Types/validationTypes";

// components
import ValidationMsg from "../ValidationErrorMsg";

// types
import { Action } from "../../ConfirmPersonalData/validation.reducer";

interface BirthDateProps {
  validation: ValidationObject;
  validationFn: () => void;
  birthDay: string;
  birthMonth: string;
  birthYear: string;
  dispatcher: Dispatch<Action>;
}

const days = Array.from(Array(31).keys());
const months = [
  "Enero",
  "Febrero",
  "Marzo",
  "Abril",
  "Mayo",
  "Junio",
  "Julio",
  "Agosto",
  "Septiembre",
  "Octubre",
  "Noviembre",
  "Diciembre",
];
const year = new Date().getFullYear() - 100;
const years = Array.from({ length: 83 }, (_, i) => i + year).reverse();

const BirthDate2: FC<BirthDateProps> = ({
  validation,
  validationFn,
  birthDay,
  birthMonth,
  birthYear,
  dispatcher,
}) => {
  useEffect(() => {
    if (birthDay && birthMonth && birthYear) {
      validationFn();
    }
  }, [birthDay, birthMonth, birthYear, validationFn]);

  const handleDayChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    let day = e.target.value;

    // edge cases
    if (day && birthMonth) {
      switch (birthMonth) {
        case "2":
          if (+day > 28) {
            if (birthYear) {
              isLeapYear(+birthYear) ? (day = "29") : (day = "28");
            } else {
              day = "29";
            }
          }
          break;
        case "4":
        case "6":
        case "9":
        case "11":
          if (+day > 30) day = "30";
          break;
      }
    }

    dispatcher({ type: "SET_BIRTH_DAY", day });
  };

  const handleMonthChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const month = e.target.value;
    // edge cases
    if (birthDay && month) {
      switch (month) {
        case "2":
          if (+birthDay > 28) {
            if (birthYear) {
              isLeapYear(+birthYear)
                ? dispatcher({
                    type: "SET_BIRTH_DAY_MONTH",
                    day: "29",
                    month,
                  })
                : dispatcher({
                    type: "SET_BIRTH_DAY_MONTH",
                    day: "28",
                    month,
                  });
            } else {
              dispatcher({
                type: "SET_BIRTH_DAY_MONTH",
                day: "29",
                month,
              });
            }
            return;
          }
          break;
        case "4":
        case "6":
        case "9":
        case "11":
          if (+birthDay > 30) {
            dispatcher({
              type: "SET_BIRTH_DAY_MONTH",
              day: "30",
              month,
            });
            return;
          }
          break;
      }
    }

    dispatcher({ type: "SET_BIRTH_MONTH", month });
  };

  const handleYearChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const year = e.target.value;
    // edge cases
    if (birthDay && birthMonth && year) {
      switch (birthMonth) {
        case "2":
          if (+birthDay > 28) {
            if (!isLeapYear(+year)) {
              dispatcher({
                type: "SET_BIRTH_DAY_YEAR",
                day: "28",
                year,
              });
              return;
            }
          }
          break;
      }
    }

    dispatcher({ type: "SET_BIRTH_YEAR", year });
  };

  return (
    <>
      <StyledLabel>Fecha de Nacimiento</StyledLabel>
      <Container>
        <SelectStyled
          value={birthDay}
          onChange={handleDayChange}
          validated={validation.validated}
          valid={validation.valid}
        >
          <option value="">Da</option>
          {days.map((n) => (
            <option key={n} value={n + 1}>
              {n + 1}
            </option>
          ))}
        </SelectStyled>
        <SelectStyled
          value={birthMonth}
          onChange={handleMonthChange}
          validated={validation.validated}
          valid={validation.valid}
        >
          <option value="">Mes</option>
          {months.map((m, i) => (
            <option key={m} value={i + 1}>
              {m}
            </option>
          ))}
        </SelectStyled>
        <SelectStyled
          value={birthYear}
          onChange={handleYearChange}
          validated={validation.validated}
          valid={validation.valid}
        >
          <option value="">Ao</option>
          {years.map((y) => (
            <option key={y} value={y}>
              {y}
            </option>
          ))}
        </SelectStyled>
        <br />
        {validation.validated && !validation.valid && (
          <ValidationMsg>{validation.msg}</ValidationMsg>
        )}
      </Container>
    </>
  );
};

export default BirthDate2;
