import React, { FC, useEffect, useState, useCallback } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import { useQuery } from "react-query";
import ReactModal from "react-modal";

// styles
import { AnimationWrapper } from "./ProcesandoAnim3.styled";
import { Container, Title } from "../ProcesandoAnim/ProcesandoAnim.styled";
import { FormSubtitle } from "../FormClientPhone/FormClientPhone.styled";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// icons
import { ReactComponent as WalletIcon } from "../../assets/icons/Wallet.svg";

// utils
import { completeDataModel } from "./ProcesandoAnim3.utils";
import { flasher } from "../../utils/Flasher";
import { closeDataModel } from "../../utils/closeModel";

// components
import RejectionMessage from "../RejectionMensage";

ReactModal.setAppElement("body");

const ProcesandoAnim3: FC<RouteComponentProps> = ({ match }) => {
  const [{ isOpen, rejectionMessage }, setModalState] = useState({
    isOpen: false,
    rejectionMessage: "",
  });
  const mainFormDispatcher = useMasterFormDispatch();
  const {
    startModelResponse,
    idSelected,
    rfc,
    numInt,
    finance,
    idNum,
    mail,
    mailDomain,
    bornCountry,
    natCountry,
    livingPlace,
    timeResidence,
    employArea,
    employType,
    payDate,
    beneCont,
    partner,
    currentStep,
  } = useMasterForm();
  const history = useHistory();
  const { refetch } = useQuery(
    [
      "completeDataModel",
      startModelResponse,
      idSelected,
      rfc,
      numInt,
      finance,
      idNum,
      mail,
      mailDomain,
      bornCountry,
      natCountry,
      timeResidence,
      livingPlace,
      employArea,
      employType,
      payDate,
      beneCont,
      partner,
      currentStep,
    ],
    completeDataModel,
    {
      onError: () => {
        flasher(
          "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
          "warning"
        );
        history.push("/");
      },
      onSettled: (modelRes) => {
        console.log(modelRes);
        if (modelRes === "return") {
          history.push("/");
          return;
        } else if (modelRes && modelRes.name) {
          switch (modelRes.name) {
            case "ShowNotification":
              closeDataModel(modelRes.id, modelRes.version, +partner);
              setModalState({
                isOpen: true,
                rejectionMessage: `estatus ${modelRes.notificationStatus}, ${modelRes.notificationReason}`,
              });
              return;
            case "TelephoneCapture":
              mainFormDispatcher({
                type: "SET_STARTMODEL_RES",
                response: modelRes,
              });
              return goToReferences();
            default:
              flasher(
                "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
                "warning"
              );
              history.push("/");
          }
        } else {
          flasher(
            "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
            "warning"
          );
          history.push("/");
        }
      },
      retry: false,
      enabled: false,
    }
  );

  useEffect(() => {
    refetch();
  }, [refetch]);

  useEffect(() => {
    if (currentStep < 0.85) {
      history.goBack();
    } else if (currentStep === 0.2) {
      history.push("/");
    }
  }, [currentStep, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.85 });
  }, [mainFormDispatcher]);

  const goToReferences = useCallback(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.9 });
    history.push(
      match.url.replace("/captura-completa/procesando", "/referencias")
    );
  }, [mainFormDispatcher, match.url, history]);

  return (
    <>
      <ReactModal
        isOpen={isOpen}
        className="modal-content"
        overlayClassName="modal-overlay"
        closeTimeoutMS={300}
      >
        <RejectionMessage msg={rejectionMessage} />
      </ReactModal>
      <Container>
        <AnimationWrapper>
          <WalletIcon />
        </AnimationWrapper>
        <Title>Ests a un clic!</Title>
        <FormSubtitle style={{ marginTop: 10, marginBottom: 5 }}>
          Solamente unos datos ms y la tarjeta ser tuya.
        </FormSubtitle>
      </Container>
    </>
  );
};

export default ProcesandoAnim3;
