import React, { FC, useEffect, useReducer, useCallback } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";

// styles
import { Form } from "../PersonalData1/PersonalData1.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// components
import ContinueBtn from "../ui/PrimaryBtn";
import TextDataInput from "../ui/TextDataInput2";
import BirthDate from "../ui/BirthDate2";

// utils
import {
  validateBirthDate,
  validateEqualField,
} from "./ConfirmPersonalData.utils";
import { validationReducer, defaultState } from "./validation.reducer";

const ConfirmPersonalData: FC<RouteComponentProps> = ({ match }) => {
  const history = useHistory();
  const {
    currentStep,
    step,
    name,
    secondName,
    fatherSurname,
    motherSurname,
    birthDay,
    birthMonth,
    birthYear,
  } = useMasterForm();
  const [
    {
      valFathSur,
      valMothSur,
      valName,
      valSecName,
      dayVal,
      monthVal,
      yearVal,
      nameValidation,
      secondNameValidation,
      fatherSurnameValidation,
      motherSurnameValidation,
      birthdayValidation,
    },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);
  const mainFormDispatcher = useMasterFormDispatch();

  useEffect(() => {
    if (step < 0.675) {
      history.goBack();
    }
  }, [step, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.675 });
  }, [mainFormDispatcher]);

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.7 });
    history.push(match.url.replace("/confirmar", "/procesando"));
  };

  useEffect(() => {
    if (currentStep > 0.675) {
      const nameVal = validateEqualField(valName, name);
      const secondNameVal = validateEqualField(valSecName, secondName);
      const fSurnameVal = validateEqualField(valFathSur, fatherSurname);
      const mSurnameVal = validateEqualField(valMothSur, motherSurname);
      const birthVal = validateBirthDate(
        `${dayVal}-${monthVal}-${yearVal}`,
        `${birthDay}-${birthMonth}-${birthYear}`
      );
      validationDispatcher({
        type: "INIT_ALL_VALIDATIONS",
        nameVal,
        secondNameVal,
        fSurnameVal,
        mSurnameVal,
        birthVal,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleChange = (
    action:
      | "SET_NAME"
      | "SET_SECOND_NAME"
      | "SET_FATHER_SURNAME"
      | "SET_MOTHER_SURNAME"
  ) => (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.toUpperCase();
    if (value && !/[a-z ,.]/gi.test(value.substr(-1))) return;
    validationDispatcher({
      type: action,
      value,
    });
  };

  const handleFieldBlur = (
    action:
      | "SET_NAME_VALIDATION"
      | "SET_SECONDNAME_VALIDATION"
      | "SET_FATHERSURNAME_VALIDATION"
      | "SET_MOTHERSURNAME_VALIDATION"
  ) => (e: React.FocusEvent<HTMLInputElement>) => {
    let validation = { validated: false };

    switch (action) {
      case "SET_NAME_VALIDATION":
        validation = validateEqualField(valName, name);
        break;
      case "SET_SECONDNAME_VALIDATION":
        validation = validateEqualField(valSecName, secondName);
        break;
      case "SET_FATHERSURNAME_VALIDATION":
        validation = validateEqualField(valFathSur, fatherSurname);
        break;
      case "SET_MOTHERSURNAME_VALIDATION":
        validation = validateEqualField(valMothSur, motherSurname);
        break;
    }

    validationDispatcher({ type: action, validation });
  };

  const handleBirthDateValidation = useCallback(() => {
    const validation = validateBirthDate(
      `${dayVal}-${monthVal}-${yearVal}`,
      `${birthDay}-${birthMonth}-${birthYear}`
    );
    validationDispatcher({ type: "SET_BIRTHDATE_VALIDATION", validation });
  }, [dayVal, monthVal, yearVal, birthDay, birthMonth, birthYear]);

  return (
    <Form onSubmit={(e) => e.preventDefault()}>
      <FormHeader>
        <FormTitle bolder style={{ marginTop: 0 }}>
          Confirma tus datos
        </FormTitle>
      </FormHeader>
      <FormSubtitle>
        Texto para instrucciones - texto para instrucciones
      </FormSubtitle>
      <TextDataInput
        labelTxt="Primer nombre"
        first
        onChange={handleChange("SET_NAME")}
        onBlur={handleFieldBlur("SET_NAME_VALIDATION")}
        value={valName}
        id={"valName"}
        validation={nameValidation}
      />
      {secondName && (
        <TextDataInput
          labelTxt="Segundo nombre"
          onChange={handleChange("SET_SECOND_NAME")}
          onBlur={handleFieldBlur("SET_SECONDNAME_VALIDATION")}
          value={valSecName}
          id={"valSecName"}
          validation={secondNameValidation}
        />
      )}
      {fatherSurname && (
        <TextDataInput
          labelTxt="Primer apellido"
          onChange={handleChange("SET_FATHER_SURNAME")}
          onBlur={handleFieldBlur("SET_FATHERSURNAME_VALIDATION")}
          value={valFathSur}
          id={"valFathSur"}
          validation={fatherSurnameValidation}
        />
      )}

      {motherSurname && (
        <TextDataInput
          labelTxt="Segundo apellido"
          onChange={handleChange("SET_MOTHER_SURNAME")}
          onBlur={handleFieldBlur("SET_MOTHERSURNAME_VALIDATION")}
          value={valMothSur}
          id={"valMothSur"}
          validation={motherSurnameValidation}
        />
      )}
      <BirthDate
        validation={birthdayValidation}
        validationFn={handleBirthDateValidation}
        birthDay={dayVal}
        birthMonth={monthVal}
        birthYear={yearVal}
        dispatcher={validationDispatcher}
      />
      <ContinueBtn
        disabled={
          !nameValidation.validated ||
          !nameValidation.valid ||
          (secondName &&
            (!secondNameValidation.validated || !secondNameValidation.valid)) ||
          (fatherSurname &&
            (!fatherSurnameValidation.validated ||
              !fatherSurnameValidation.valid)) ||
          (motherSurname &&
            (!motherSurnameValidation.validated ||
              !motherSurnameValidation.valid)) ||
          !birthdayValidation.validated ||
          !birthdayValidation.valid
        }
        onClick={handleContinue}
      >
        Continuar
        <ContinueIconWrapper>
          <ContinueIcon />
        </ContinueIconWrapper>
      </ContinueBtn>
    </Form>
  );
};

export default ConfirmPersonalData;
