import React, { FC, useEffect, useReducer, useCallback } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import ReactModal from "react-modal";

// styles
import { Form, GenreSelection } from "./PersonalData1.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";
import { theme } from "../../styles/theme";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// components
import ContinueBtn from "../ui/PrimaryBtn";
import TextDataInput from "../ui/TextDataInput";
import BirthDate from "../ui/BirthDate";
import GenreTab from "../ui/DocumentTab";
import NameConfirmationModal from "../ui/NameConfirmationModal";

// utils
import {
  validateNameField,
  validateSecondname,
  validateBirthDate,
  validateSurnames,
} from "./PersonalData1.utils";
import { validationReducer, defaultState } from "./validation.reducer";

// hooks
import mediaQuery from "../../hooks/useMedia";

ReactModal.setAppElement("body");

const PersonalData1: FC<RouteComponentProps> = ({ match }) => {
  const history = useHistory();
  const isMobile = mediaQuery(theme.mediaquerys.mobile);
  const {
    step,
    genre,
    name,
    secondName,
    fatherSurname,
    motherSurname,
    birthDay,
    birthMonth,
    birthYear,
  } = useMasterForm();
  const [
    {
      nameValidation,
      secondnameValidation,
      fatherSurnameValidation,
      motherSurnameValidation,
      birthDateValidation,
      isModalOpen,
    },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);
  const mainFormDispatcher = useMasterFormDispatch();

  useEffect(() => {
    if (step < 0.6) {
      history.goBack();
    }
  }, [step, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.6 });
  }, [mainFormDispatcher]);

  useEffect(() => {
    if (name) {
      const validation = validateNameField(name);
      validationDispatcher({ type: "SET_NAME_VALIDATION", validation });
    }
    if (secondName) {
      const validation = validateSecondname(secondName);
      validationDispatcher({ type: "SET_SECONDNAME_VALIDATION", validation });
    }
    if (fatherSurname) {
      const validation = validateSurnames(fatherSurname, motherSurname);
      validationDispatcher({
        type: "SET_FATHER_SURNAME_VALIDATION",
        validation,
      });
      if (fatherSurname.length) {
        const validationMotherSurname = validateSurnames(
          motherSurname,
          fatherSurname
        );
        validationDispatcher({
          type: "SET_MOTHER_SURNAME_VALIDATION",
          validation: validationMotherSurname,
        });
      }
    }
    if (motherSurname) {
      const validation = validateSurnames(motherSurname, fatherSurname);
      validationDispatcher({
        type: "SET_MOTHER_SURNAME_VALIDATION",
        validation,
      });
      if (motherSurname.length) {
        const validationFatherSurname = validateSurnames(
          fatherSurname,
          motherSurname
        );
        validationDispatcher({
          type: "SET_FATHER_SURNAME_VALIDATION",
          validation: validationFatherSurname,
        });
      }
    }
    if (birthDay || birthMonth || birthYear) {
      const validation = validateBirthDate(birthDay, birthMonth, birthYear);
      validationDispatcher({ type: "SET_BIRTHDATE_VALIDATION", validation });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.65 });
    history.push(`${match.url}/2`);
  };

  const handleGenreSelection = (genre: string) => (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_GENRE", genre: genre });
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.toUpperCase();
    if (value && !/[a-z. ]/gi.test(value.substr(-1))) return;
    mainFormDispatcher({
      type: "SET_FIRST_PERSONAL_DATA",
      field: e.target.id,
      value: value,
    });
  };

  const handleNameBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateNameField(name);
    validationDispatcher({ type: "SET_NAME_VALIDATION", validation });
  };
  const handleSecondnameBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateSecondname(secondName);
    validationDispatcher({ type: "SET_SECONDNAME_VALIDATION", validation });
  };
  const handleFatherSurnameBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateSurnames(fatherSurname, motherSurname);
    validationDispatcher({ type: "SET_FATHER_SURNAME_VALIDATION", validation });
    if (fatherSurname.length) {
      const validationMotherSurname = validateSurnames(
        motherSurname,
        fatherSurname
      );
      validationDispatcher({
        type: "SET_MOTHER_SURNAME_VALIDATION",
        validation: validationMotherSurname,
      });
    }
  };
  const handleMotherSurnameBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateSurnames(motherSurname, fatherSurname);
    validationDispatcher({ type: "SET_MOTHER_SURNAME_VALIDATION", validation });
    if (motherSurname.length) {
      const validationFatherSurname = validateSurnames(
        fatherSurname,
        motherSurname
      );
      validationDispatcher({
        type: "SET_FATHER_SURNAME_VALIDATION",
        validation: validationFatherSurname,
      });
    }
  };
  const handleBirthDateValidation = useCallback(() => {
    const validation = validateBirthDate(birthDay, birthMonth, birthYear);
    validationDispatcher({ type: "SET_BIRTHDATE_VALIDATION", validation });
  }, [birthDay, birthMonth, birthYear]);

  const toggleModal = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    if (!secondName || !fatherSurname || !motherSurname) {
      validationDispatcher({ type: "TOGGLE_MODAL" });
    } else {
      mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.65 });
      history.push(`${match.url}/2`);
    }
  };

  const checkButtonDisabled = () => {
    if (
      !nameValidation.validated ||
      !nameValidation.valid ||
      !genre ||
      !birthDateValidation.validated ||
      !birthDateValidation.valid
    ) {
      return true;
    } else if (
      secondName.length &&
      (!secondnameValidation.validated || !secondnameValidation.valid)
    ) {
      return true;
    } else if (
      fatherSurname.length &&
      (!fatherSurnameValidation.validated || !fatherSurnameValidation.valid)
    ) {
      return true;
    } else if (
      motherSurname.length &&
      (!motherSurnameValidation.validated || !motherSurnameValidation.valid)
    ) {
      return true;
    } else if (!fatherSurname.length && !motherSurname.length) {
      return true;
    }

    return false;
  };

  return (
    <>
      <ReactModal
        isOpen={isModalOpen}
        className="modal-content"
        overlayClassName="modal-overlay"
        closeTimeoutMS={300}
        style={
          isMobile
            ? { content: { padding: 20, height: "100vh", width: "100vw" } }
            : { content: { padding: 20, height: 270 } }
        }
      >
        <NameConfirmationModal
          toggleModal={toggleModal}
          handleContinue={handleContinue}
          secondName={secondName}
          fatherSurname={fatherSurname}
          motherSurname={motherSurname}
        />
      </ReactModal>
      <Form onSubmit={(e) => e.preventDefault()}>
        <FormHeader>
          <FormTitle bolder style={{ marginTop: 0 }}>
            Captura rpida
          </FormTitle>
        </FormHeader>
        <FormSubtitle>
          Texto para instrucciones - texto para instrucciones
        </FormSubtitle>
        <TextDataInput
          labelTxt="Primer nombre"
          first
          onChange={handleChange}
          onBlur={handleNameBlur}
          value={name}
          id={"name"}
          validation={nameValidation}
        />
        <TextDataInput
          labelTxt="Segundo nombre"
          onChange={handleChange}
          onBlur={handleSecondnameBlur}
          value={secondName}
          id={"secondName"}
          validation={secondnameValidation}
        />
        <TextDataInput
          labelTxt="Primer apellido"
          onChange={handleChange}
          onBlur={handleFatherSurnameBlur}
          value={fatherSurname}
          id={"fatherSurname"}
          validation={fatherSurnameValidation}
        />
        <TextDataInput
          labelTxt="Segundo apellido"
          onChange={handleChange}
          onBlur={handleMotherSurnameBlur}
          value={motherSurname}
          id={"motherSurname"}
          validation={motherSurnameValidation}
        />
        <BirthDate
          validation={birthDateValidation}
          validationFn={handleBirthDateValidation}
        />
        <GenreSelection>
          <GenreTab
            selected={genre === "2"}
            onClick={handleGenreSelection("2")}
          >
            Femenino
          </GenreTab>
          <GenreTab
            selected={genre === "1"}
            onClick={handleGenreSelection("1")}
          >
            Masculino
          </GenreTab>
        </GenreSelection>
        <ContinueBtn disabled={checkButtonDisabled()} onClick={toggleModal}>
          Continuar
          <ContinueIconWrapper>
            <ContinueIcon />
          </ContinueIconWrapper>
        </ContinueBtn>
      </Form>
    </>
  );
};

export default PersonalData1;
