import React, { FC, useState } from "react";
import { useQuery } from "react-query";

// styles
import { StyledSelect } from "./EmailDomainSelect.styled";

// utils
import { getEmailDomains } from "./EmailDomainSelect.utils";
interface EmailDomainSelectProps {
  value: string;
  onChange: (e: React.ChangeEvent<HTMLSelectElement>) => void;
}

const EmailDomainSelect: FC<EmailDomainSelectProps> = ({ value, onChange }) => {
  const [mailDomains, setMailDomains] = useState([
    "@gmail.com",
    "@hotmail.com",
    "@yahoo.com",
  ]);
  useQuery("getEmailDomains", getEmailDomains, {
    keepPreviousData: true,
    onSuccess: (emailsRes) => {
      setMailDomains(
        emailsRes[0].content
          .map((email: { key: string }) => `@${email.key}`)
          .filter((email: string) => email.length > 1)
      );
    },
  });

  return (
    <StyledSelect value={value} onChange={onChange}>
      {mailDomains.map((domain) => (
        <option key={domain} value={domain}>
          {domain}
        </option>
      ))}
    </StyledSelect>
  );
};

export default EmailDomainSelect;
