// types
import { ValidationObject } from "../../Types/validationTypes";

export const validateCtePhone = (phone: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  const repeatRegex = /([0-9])\1{4}/g;
  const numToCheck = phone.slice(2);

  if (phone.length < 10 || repeatRegex.test(numToCheck)) {
    validation.msg = "Ingrese un nmero de telfono vlido";
    validation.valid = false;
  }

  return validation;
};

export const validateCtePhoneConf = (phone: string, phoneConf: string) => {
  const validation: ValidationObject = { validated: true, valid: true };
  if (phone !== phoneConf) {
    validation.msg = "Los nmeros de telfono no coinciden";
    validation.valid = false;
  }

  return validation;
};
