import React, { FC, useReducer, useEffect } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";

// styles
import {
  Form,
  SelectorsContainer,
  Selector,
  SelectorCell,
  InputWrapper,
} from "./PersonalData3.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";

// components
import ContinueBtn from "../ui/PrimaryBtn";
import CountrySelector from "../ui/CountrySelector";
import IdEmitSelector from "../ui/IdEmitDate";
import IdValSelector from "../ui/IdValDate";
import SelectorTab from "../ui/DocumentTab";
import TextDataInput from "../ui/TextDataInput";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// utils
import { validateCouldBeEmpty, validateNonEmpty } from "./PersonalData3.utils";
import { validationReducer, defaultState } from "./validation.reducer";

const PersonalData3: FC<RouteComponentProps> = ({ match }) => {
  const history = useHistory();
  const {
    step,
    currentStep,
    countryId,
    moreCitizens,
    citizens,
    idEmitDay,
    idEmitMonth,
    idEmityear,
    idValDay,
    idValMonth,
    idValyear,
  } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const [{ citizensVal }, validationDispatcher] = useReducer(
    validationReducer,
    defaultState
  );

  useEffect(() => {
    if (step < 0.82) {
      history.goBack();
    }
  }, [step, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.82 });
  }, [mainFormDispatcher]);

  useEffect(() => {
    if (currentStep > 0.66 && moreCitizens) {
      const validation = validateNonEmpty(citizens);
      validationDispatcher({ type: "SET_CITIZENS_VALIDATION", validation });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.84 });
    history.push(match.url.replace("/3", "/4"));
  };

  const handleChangeCountryId = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const { value } = e.target;
    mainFormDispatcher({
      type: "SET_COUNTRY_ID",
      countryId: value,
    });
  };

  const handleCitizenSelector = (value: boolean) => (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({
      type: "SET_MORECITIZENS",
      value,
    });
    if (!value) {
      validationDispatcher({ type: "RESET_CITIZENS_VALIDATION" });
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    mainFormDispatcher({
      type: "SET_CITIZENS",
      value: e.target.value.toUpperCase(),
    });
  };

  const handleBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation =
      moreCitizens === true
        ? validateNonEmpty(citizens)
        : validateCouldBeEmpty(citizens);
    validationDispatcher({ type: "SET_CITIZENS_VALIDATION", validation });
  };

  return (
    <Form onSubmit={(e) => e.preventDefault()}>
      <FormHeader>
        <FormTitle bolder style={{ marginTop: 0 }}>
          Captura extranjeros
        </FormTitle>
      </FormHeader>
      <FormSubtitle>
        Texto para instrucciones - texto para instrucciones
      </FormSubtitle>
      <CountrySelector
        labelTxt="Pas de Nacionalidad"
        first
        value={countryId}
        onChange={handleChangeCountryId}
      />
      <IdEmitSelector />
      <IdValSelector />
      <SelectorsContainer>
        <SelectorCell>
          <h3>Cuentas con otras ciudadanas?</h3>
          <Selector>
            <SelectorTab
              selected={moreCitizens === true}
              onClick={handleCitizenSelector(true)}
            >
              S
            </SelectorTab>
            <SelectorTab
              selected={moreCitizens === false}
              onClick={handleCitizenSelector(false)}
            >
              No
            </SelectorTab>
          </Selector>
        </SelectorCell>
        <InputWrapper>
          <TextDataInput
            labelTxt="Ciudadanas"
            first
            onChange={handleChange}
            onBlur={handleBlur}
            value={citizens}
            id={"citizens"}
            validation={citizensVal}
            disabled={moreCitizens !== true}
          />
        </InputWrapper>
      </SelectorsContainer>
      <ContinueBtn
        disabled={
          !idEmitDay ||
          !idEmitMonth ||
          !idEmityear ||
          !idValDay ||
          !idValMonth ||
          !idValyear ||
          moreCitizens === null ||
          (moreCitizens && (!citizensVal.validated || !citizensVal.valid))
        }
        onClick={handleContinue}
      >
        Continuar
        <ContinueIconWrapper>
          <ContinueIcon />
        </ContinueIconWrapper>
      </ContinueBtn>
    </Form>
  );
};

export default PersonalData3;
