import React, { FC, useEffect, useReducer } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import { useQuery } from "react-query";

// styles
import {
  Form,
  FieldsContainer,
  EmailFieldWrapper,
} from "./CompleteData.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";
import { theme } from "../../styles/theme";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// hooks
import mediaQuery from "../../hooks/useMedia";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// components
import TextDataInput from "../ui/TextDataInput";
import ContinueBtn from "../ui/PrimaryBtn";
import SelectorInput from "../ui/SelectorInput";
import CountrySelector from "../ui/CountrySelector";
import EmailDomains from "../ui/EmailDomainSelect";

// utils
import { validationReducer, defaultState } from "./validation.reducer";
import {
  completeDataReducer,
  completeDefaultState,
} from "./CompleteData.reducer";
import {
  validateEmail,
  validateConfirmation,
  validateIdNum,
  validateRfc,
  getTypeHouse,
  getTimeResidence,
  getIdTypes,
} from "./CompleteData.utils";

const CompleteData: FC<RouteComponentProps> = ({ match }) => {
  const history = useHistory();
  const isMobile = mediaQuery(theme.mediaquerys.mobile);
  const {
    rfc,
    currentStep,
    step,
    livingPlace,
    timeResidence,
    idSelected,
    bornCountry,
    natCountry,
    mail,
    mailDomain,
    idNum,
  } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const [
    {
      mailValidation,
      confirmationValidation,
      mailConfirmation,
      idNumValidation,
      rfcValidation,
    },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);
  const [
    { timeResidenceOptons, livingPlaceOptons, idTypesOptions },
    completeDataDispatcher,
  ] = useReducer(completeDataReducer, completeDefaultState);

  useQuery("getTypeHouse", getTypeHouse, {
    keepPreviousData: true,
    onSuccess: (houseRes) => {
      completeDataDispatcher({
        type: "SET_RESIDENCE_TYPES",
        value: houseRes[0].content.map(
          (house: { key: string; value: string }) => ({
            value: house.key,
            text: house.value,
          })
        ),
      });
    },
  });
  useQuery("getTimeResidence", getTimeResidence, {
    keepPreviousData: true,
    onSuccess: (timeRes) => {
      completeDataDispatcher({
        type: "SET_RESIDEMCE_TIMES",
        value: timeRes[0].content.map(
          (time: { key: string; value: string }) => ({
            value: time.key,
            text: time.value,
          })
        ),
      });
    },
  });
  useQuery("getIdTypes", getIdTypes, {
    keepPreviousData: true,
    onSuccess: (idTypesRes) => {
      completeDataDispatcher({
        type: "SET_ID_TYPES",
        value: idTypesRes[0].content.map(
          (time: { key: string; value: string }) => ({
            value: time.key,
            text: time.value,
          })
        ),
      });
    },
  });

  useEffect(() => {
    if (step < 0.65) {
      history.goBack();
    }
  }, [step, history]);

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    history.push(`${match.url}/2`);
  };

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.75 });
  }, [mainFormDispatcher]);

  useEffect(() => {
    if (currentStep > 0.75) {
      const mailValidation = validateEmail(mail, mailConfirmation);
      const idNumValidation = validateIdNum(idNum);
      const rfcValidation = validateRfc(rfc);
      validationDispatcher({
        type: "INIT_ALL_VALIDATIONS",
        mailValidation,
        idNumValidation,
        rfcValidation,
      });
    } else {
      if (rfc) {
        const rfcValidation = validateRfc(rfc);
        validationDispatcher({
          type: "SET_RFC_VALIDATION",
          validation: rfcValidation,
        });
      }
      if (idNum) {
        const idNumValidation = validateIdNum(idNum);
        validationDispatcher({
          type: "SET_IDNUM_VALIDATION",
          validation: idNumValidation,
        });
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleLivingPlaceSelect = (e: React.ChangeEvent<HTMLSelectElement>) => {
    e.preventDefault();
    mainFormDispatcher({
      type: "SET_LIVING_PLACE",
      livingPlace: e.target.value,
    });
  };

  const handleTimeResidenceSelect = (
    e: React.ChangeEvent<HTMLSelectElement>
  ) => {
    e.preventDefault();
    mainFormDispatcher({
      type: "SET_TIME_RESIDENCE",
      timeResidence: e.target.value,
    });
  };

  const handleIdTypeSelect = (e: React.ChangeEvent<HTMLSelectElement>) => {
    e.preventDefault();
    mainFormDispatcher({
      type: "SET_SELECTED_ID",
      idSelected: e.target.value,
    });
  };

  const handleNumIdChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    mainFormDispatcher({
      type: "SET_ID_NUM",
      idNum: e.target.value.toUpperCase(),
    });
  };

  const handleRfcChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    mainFormDispatcher({
      type: "SET_RFC",
      rfc: e.target.value.toUpperCase(),
    });
  };

  const handleChangeBorn = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const { value } = e.target;
    mainFormDispatcher({
      type: "SET_BORN_COUNTRY",
      bornCountry: value,
    });
  };

  const handleChangeNat = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const { value } = e.target;
    mainFormDispatcher({
      type: "SET_NATIONALITY_COUNTRY",
      natCountry: value,
    });
  };

  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const prevChar = mail.substr(-1);
    const lastChar = e.target.value.substr(-1);
    if (
      lastChar === " " ||
      lastChar === "@" ||
      (lastChar === "." && !mail.length) ||
      (prevChar === "." && lastChar === ".")
    )
      return;
    mainFormDispatcher({
      type: "SET_MAIL",
      mail: e.target.value.toLowerCase(),
    });
  };

  const handleConfirmationChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const prevChar = mail.substr(-1);
    const lastChar = e.target.value.substr(-1);
    if (
      lastChar === " " ||
      lastChar === "@" ||
      (lastChar === "." && !mail.length) ||
      (prevChar === "." && lastChar === ".")
    )
      return;
    validationDispatcher({
      type: "SET_CONFIRMATION_MAIL",
      mailConfirmation: e.target.value.toLowerCase(),
    });
  };

  const handleEmailBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateEmail(mail, mailConfirmation);
    validationDispatcher({ type: "SET_MAIL_VALIDATION", validation });
  };

  const handleConfirmationBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateConfirmation(mailConfirmation, mail);
    validationDispatcher({ type: "SET_CONFIRMATION_VALIDATION", validation });
  };

  const handleEmailDomainChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    mainFormDispatcher({ type: "SET_MAIL_DOMAIN", mailDomain: e.target.value });
  };

  const handleIdNumBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateIdNum(idNum);
    validationDispatcher({ type: "SET_IDNUM_VALIDATION", validation });
  };

  const handleRfcBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const validation = validateRfc(rfc);
    validationDispatcher({ type: "SET_RFC_VALIDATION", validation });
  };

  return (
    <Form onSubmit={(e) => e.preventDefault()}>
      <FormHeader>
        <FormTitle bolder style={{ marginTop: 0 }}>
          Captura completa
        </FormTitle>
      </FormHeader>
      <FormSubtitle>
        Texto para instrucciones - texto para instrucciones
      </FormSubtitle>
      <FieldsContainer>
        <TextDataInput
          labelTxt="RFC"
          first
          value={rfc}
          id={"rfc"}
          validation={rfcValidation}
          onChange={handleRfcChange}
          onBlur={handleRfcBlur}
          disabled
        />
        {!isMobile && <br />}
        <CountrySelector
          labelTxt="Pas de Nacimiento"
          first
          value={bornCountry}
          onChange={handleChangeBorn}
        />
        <CountrySelector
          labelTxt="Pas de Nacionalidad"
          first
          value={natCountry}
          onChange={handleChangeNat}
        />
        <SelectorInput
          labelTxt="Tipo de vivienda"
          first
          value={livingPlace}
          options={livingPlaceOptons}
          defaultOption={"Seleccione una opcin"}
          onChange={handleLivingPlaceSelect}
          id="livingPlace"
        />
        <SelectorInput
          labelTxt="Tiempo de residencia"
          first
          value={timeResidence}
          options={timeResidenceOptons}
          defaultOption={"Seleccione una opcin"}
          onChange={handleTimeResidenceSelect}
          id="timeResidence"
        />
        <SelectorInput
          labelTxt="Tipo de ID"
          first
          value={idSelected}
          options={idTypesOptions}
          defaultOption={"Seleccione una opcin"}
          onChange={handleIdTypeSelect}
          id="idSelected"
        />
        <TextDataInput
          labelTxt="Nmero de ID"
          first
          // TODO connect the API end points to calculate ID num
          value={idNum}
          id={"idNum"}
          validation={idNumValidation}
          onChange={handleNumIdChange}
          onBlur={handleIdNumBlur}
        />
        <EmailFieldWrapper>
          <TextDataInput
            labelTxt="Correo electrnico"
            first
            onChange={handleEmailChange}
            onBlur={handleEmailBlur}
            value={mail}
            id={"mail"}
            validation={mailValidation}
          />
          <EmailDomains value={mailDomain} onChange={handleEmailDomainChange} />
        </EmailFieldWrapper>
        <EmailFieldWrapper>
          <TextDataInput
            labelTxt="Confirmar correo electrnico"
            first
            onChange={handleConfirmationChange}
            onBlur={handleConfirmationBlur}
            value={mailConfirmation}
            id={"mail-confirmation"}
            validation={confirmationValidation}
            disabled={!mailValidation.validated || !mailValidation.valid}
          />
          <EmailDomains value={mailDomain} onChange={handleEmailDomainChange} />
        </EmailFieldWrapper>
      </FieldsContainer>

      <ContinueBtn
        disabled={
          // TODO add RFC validation & ID num validation
          !mailValidation.validated ||
          !mailValidation.valid ||
          !confirmationValidation.validated ||
          !confirmationValidation.valid ||
          !timeResidence ||
          !livingPlace ||
          !idNumValidation.validated ||
          !idNumValidation.valid ||
          !rfcValidation.validated ||
          !rfcValidation.valid ||
          idSelected === "OTRO" ||
          idSelected === ""
        }
        onClick={handleContinue}
      >
        Continuar
        <ContinueIconWrapper>
          <ContinueIcon />
        </ContinueIconWrapper>
      </ContinueBtn>
    </Form>
  );
};

export default CompleteData;
