import React, { FC, Dispatch, useState } from "react";

// styles
import { TabsContainer, Tab } from "./INEFormMobile.styled";
import { Container } from "../ResidenteForm/ResidenteForm.styled";

// components
import DocumentForm from "../DocumentForm";

// Context
import { useMasterForm } from "../../Context/MainForm";

// types
import { ValidationObject } from "../../Types/validationTypes";
import { Action } from "../FormId/validation.reducer";

interface INEFormMobilePops {
  validations: {
    adverseIneValidation: ValidationObject;
    reverseIneValidation: ValidationObject;
  };
  validationDispatcher: Dispatch<Action>;
}

const INEFormMobile: FC<INEFormMobilePops> = ({
  validations: { adverseIneValidation, reverseIneValidation },
  validationDispatcher,
}) => {
  const {
    ineAnverso,
    ineReverso,
    ineAnversoFilename,
    ineReversoFilename,
  } = useMasterForm();
  const [anversoForm, setAnversoForm] = useState(true);

  return (
    <Container>
      <TabsContainer>
        <Tab selected={anversoForm} onClick={() => setAnversoForm(true)}>
          Foto Anverso
        </Tab>
        <Tab selected={!anversoForm} onClick={() => setAnversoForm(false)}>
          Foto Reverso
        </Tab>
      </TabsContainer>
      <DocumentForm
        hide={!anversoForm}
        title=""
        containerWidth="100%"
        actionType="SET_INE_ANVERSO"
        validationType="SET_INE_ADVERSE_VALIDATION"
        document={ineAnverso}
        filename={ineAnversoFilename}
        validation={adverseIneValidation}
        validationDispatcher={validationDispatcher}
        reset="RESET_INE_ANVERSO"
        resetValidation="RESET_INE_ADVERSE_VALIDATION"
      />
      <DocumentForm
        hide={anversoForm}
        title=""
        actionType="SET_INE_REVERSO"
        validationType="SET_INE_REVERSE_VALIDATION"
        document={ineReverso}
        filename={ineReversoFilename}
        validation={reverseIneValidation}
        validationDispatcher={validationDispatcher}
        reset="RESET_INE_REVERSO"
        resetValidation="RESET_INE_REVERSE_VALIDATION"
      />
    </Container>
  );
};

export default INEFormMobile;
