import React, { FC } from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { Link } from "react-router-dom";

// styles
import {
  Container,
  CardTitle,
  HeaderDecoration,
  StyledButtonPrimary,
} from "./ProductCard.styled";

interface ProductCardProps {
  url: string;
  title: string;
  link: string;
  alt: string;
}

const ProductCard: FC<ProductCardProps> = ({ url, title, link, alt }) => {
  return (
    <Container>
      <HeaderDecoration />
      <LazyLoadImage width={"77%"} alt={alt} src={url} style={{ zIndex: 2 }} />
      <CardTitle>{title}</CardTitle>
      <Link to={link}>
        <StyledButtonPrimary>Tramitar aqu</StyledButtonPrimary>
      </Link>
    </Container>
  );
};

export default ProductCard;
