interface FechaPagoContext {
  queryKey: string[];
}

export const getFechasPago = async (ctx: FechaPagoContext) => {
  let token = localStorage.getItem("auth_token");
  const { queryKey } = ctx;

  const response = await axios.post(
    // Get email domains selector end point
    `${REACT_APP_API_PREFIX}/Middleware/BusinessWorks/FechaDePagoPreaprobados`,
    {
      idProducto: +queryKey[2],
      idSocio: +queryKey[1],
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};
