import styled from "styled-components";

interface StyledLabelProps {
  validated: boolean;
  valid: boolean | undefined;
  first?: boolean;
}

interface StyledInputProps {
  invalid: boolean | undefined;
}

export const StyledLabel = styled.label<StyledLabelProps>`
  background-color: ${({ theme: { colors } }) => colors.almostWhite};
  border: ${({ theme: { colors }, valid, validated }) =>
    validated
      ? valid
        ? `1px solid ${colors.greenCheck}`
        : `1px solid ${colors.errorRed}`
      : 0};
  border-radius: 7px;
  color: ${({ theme: { colors } }) => colors.gray};
  font-size: 13px;
  font-weight: 500;
  line-height: 18px;
  margin-top: 28px;
  padding: 5px 18px 2px 18px;
  position: relative;
  width: 95%;

  ${({ theme: { up, breakpoints } }) => up(breakpoints.desktop)} {
    margin-top: ${({ first }) => (first ? "0" : "28px")};
  }
`;

export const InputContainer = styled.div`
  height: 35px;
  padding-top: 7px;
  position: relative;
`;

export const StyledInput = styled.input<StyledInputProps>`
  background-color: ${({ theme: { colors } }) => colors.almostWhite};
  border: 0;
  color: ${({ theme: { colors }, invalid }) =>
    invalid ? colors.errorRed : colors.bradescardBlue};
  font-size: 16px;
  font-weight: 600;
  height: 23px;
  width: 88%;

  &:focus {
    outline: none;
  }
`;
