import React, { FC, useRef } from "react";
import { useHistory } from "react-router-dom";
import { exportComponentAsJPEG } from "react-component-export-image";

// styles
import { Container, Title, Message, BtnText } from "./ApproveMessage.styled";

// icons
import { ReactComponent as AcceptIcon } from "../../assets/icons/Accept.svg";

// Context
import { useMasterFormDispatch } from "../../Context/MainForm";

// components
import Finish from "../ui/SecondaryBtn";
interface RejectionMesajeProps {
  msg: string;
  fullName: string;
  caseId: string;
  requestId: string;
}

const { REACT_APP_ROUTER_PREFIX } = process.env;

const RejeceitonMensaje: FC<RejectionMesajeProps> = ({
  msg,
  caseId,
  requestId,
  fullName,
}) => {
  const history = useHistory();
  const mainFormDispatcher = useMasterFormDispatch();
  const modalRef = useRef<HTMLElement | null>(null);

  const finishProcess = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "RESET_FORM" });
    history.push(REACT_APP_ROUTER_PREFIX || "/");
  };

  const saveImg = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    if (modalRef) {
      exportComponentAsJPEG(modalRef, {
        fileName: "datos_tarjeta_preaprobada.jpg",
      });
    }
  };

  return (
    <Container ref={modalRef}>
      <AcceptIcon />
      <Title>
        Felicidades!
        <br />
        Tu tarjeta est pre-aprobada.
      </Title>
      <Message>{msg}</Message>
      <Message>
        {`Tu nmero de caso es: `}
        <strong>{caseId}</strong>
      </Message>
      <Message>
        {`Tu nmero de solicitud es: `}
        <strong>{requestId}</strong>
      </Message>
      <Message>
        <strong>{fullName}</strong>
      </Message>
      <Message>
        {
          "Documentacin requerida: INE, en caso de ser estudiante, presentar comprobante de estudios."
        }
      </Message>
      <br />
      <Finish onClick={saveImg}>
        <BtnText>Guardar</BtnText>
      </Finish>
      <Finish onClick={finishProcess}>
        <BtnText>Finalizar</BtnText>
      </Finish>
    </Container>
  );
};

export default RejeceitonMensaje;
