// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_MAIL_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_CONFIRMATION_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_IDNUM_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_RFC_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_CONFIRMATION_MAIL";
      mailConfirmation: string;
    }
  | {
      type: "INIT_ALL_VALIDATIONS";
      mailValidation: ValidationObject;
      idNumValidation: ValidationObject;
      rfcValidation: ValidationObject;
    };
type State = {
  mailValidation: ValidationObject;
  confirmationValidation: ValidationObject;
  idNumValidation: ValidationObject;
  rfcValidation: ValidationObject;
  mailConfirmation: string;
};

export const defaultState: State = {
  mailValidation: { validated: false },
  confirmationValidation: { validated: false },
  idNumValidation: { validated: false },
  rfcValidation: { validated: false },
  mailConfirmation: "",
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_MAIL_VALIDATION":
      return { ...state, mailValidation: action.validation };
    case "SET_CONFIRMATION_VALIDATION":
      return { ...state, confirmationValidation: action.validation };
    case "SET_CONFIRMATION_MAIL":
      return { ...state, mailConfirmation: action.mailConfirmation };
    case "SET_IDNUM_VALIDATION":
      return { ...state, idNumValidation: action.validation };
    case "SET_RFC_VALIDATION":
      return { ...state, rfcValidation: action.validation };
    case "INIT_ALL_VALIDATIONS":
      return {
        ...state,
        mailValidation: action.mailValidation,
        idNumValidation: action.idNumValidation,
        rfcValidation: action.rfcValidation,
      };
    default:
      return state;
  }
};
