import axios from "axios";

// types
import { ValidationObject } from "../../Types/validationTypes";

const { REACT_APP_API_PREFIX } = process.env;

export const validatePhoneNonEmpty = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (!field.trim().length || field.trim().length < 10) {
    validation.valid = false;
    validation.msg = "Ingresa un nmero a 10 digitos vlido";
  }

  return validation;
};

export const validateName = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (!field.trim().length || field.trim().length < 4) {
    validation.valid = false;
    validation.msg = "Ingresa un nombre completo vlido";
  }

  return validation;
};

export const validateOTP = (field: string) => {
  const validation: ValidationObject = { validated: true, valid: true };

  if (field.trim().length < 6) {
    validation.valid = false;
    validation.msg = "Ingresa un OTP vlido";
  }

  return validation;
};

interface ContinueModelCtx {
  queryKey: [string, string, string, string, ValidationObject];
}

export const continueModel = async (ctx: ContinueModelCtx) => {
  const { queryKey } = ctx;
  const telValidation = queryKey[4];
  const byPhone = telValidation.validated && telValidation.valid;
  const otp = byPhone ? "" : queryKey[1];
  const fullName = byPhone ? "" : queryKey[2];
  const phoneNumber = byPhone ? queryKey[3] : "";

  let token = localStorage.getItem("auth_token");

  console.log({
    fullName,
    otp,
    phoneNumber,
  });

  const response = await axios.post(
    // TOKEN ENDPOINT
    `${REACT_APP_API_PREFIX}/Middleware/Amx/AmxGateway/recovery`,
    {
      fullName,
      otp,
      phoneNumber,
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};
