import axios from "axios";

const { REACT_APP_API_PREFIX } = process.env;

interface queryNewFolioCtx {
  queryKey: [string, string];
}

export const requestNewFolio = async (ctx: queryNewFolioCtx) => {
  const { queryKey } = ctx;

  let token = localStorage.getItem("auth_token");

  const response = await axios.post(
    // CATALOGS END POINT
    `${REACT_APP_API_PREFIX}/Middleware/ContentManagement/Documents/createFolio`,
    // JSON BODY
    {
      idSocio: queryKey[1],
    },
    // JSON CONFIG
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  return response.data.result;
};
