import React, { FC, useEffect } from "react";

// styles
import { Container, SelectStyled } from "./BirthDate.styled";
import { StyledLabel } from "../TextDataInput/TextDataInput.styled";

// Context
import {
  useMasterFormDispatch,
  useMasterForm,
} from "../../../Context/MainForm";

// utils
import { isLeapYear } from "../../../utils/dateUtils";

// types
import { ValidationObject } from "../../../Types/validationTypes";

// components
import ValidationMsg from "../ValidationErrorMsg";

interface BirthDateProps {
  validation: ValidationObject;
  validationFn: () => void;
}

const days = Array.from(Array(31).keys());
const months = [
  "Enero",
  "Febrero",
  "Marzo",
  "Abril",
  "Mayo",
  "Junio",
  "Julio",
  "Agosto",
  "Septiembre",
  "Octubre",
  "Noviembre",
  "Diciembre",
];
const year = new Date().getFullYear() - 100;
const years = Array.from({ length: 83 }, (_, i) => i + year).reverse();

const BirthDate: FC<BirthDateProps> = ({ validation, validationFn }) => {
  const { birthDay, birthMonth, birthYear } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();

  useEffect(() => {
    if (birthDay && birthMonth && birthYear) {
      validationFn();
    }
  }, [birthDay, birthMonth, birthYear, validationFn]);

  const handleDayChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    let day = e.target.value;

    // edge cases
    if (day && birthMonth) {
      switch (birthMonth) {
        case "2":
          if (+day > 28) {
            if (birthYear) {
              isLeapYear(+birthYear) ? (day = "29") : (day = "28");
            } else {
              day = "29";
            }
          }
          break;
        case "4":
        case "6":
        case "9":
        case "11":
          if (+day > 30) day = "30";
          break;
      }
    }

    mainFormDispatcher({ type: "SET_BDAY", day });
  };

  const handleMonthChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const month = e.target.value;
    // edge cases
    if (birthDay && month) {
      switch (month) {
        case "2":
          if (+birthDay > 28) {
            if (birthYear) {
              isLeapYear(+birthYear)
                ? mainFormDispatcher({
                    type: "SET_BYDAY_BMONTH",
                    day: "29",
                    month,
                  })
                : mainFormDispatcher({
                    type: "SET_BYDAY_BMONTH",
                    day: "28",
                    month,
                  });
            } else {
              mainFormDispatcher({
                type: "SET_BYDAY_BMONTH",
                day: "29",
                month,
              });
            }
            return;
          }
          break;
        case "4":
        case "6":
        case "9":
        case "11":
          if (+birthDay > 30) {
            mainFormDispatcher({
              type: "SET_BYDAY_BMONTH",
              day: "30",
              month,
            });
            return;
          }
          break;
      }
    }

    mainFormDispatcher({ type: "SET_BMONTH", month });
  };

  const handleYearChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const year = e.target.value;
    // edge cases
    if (birthDay && birthMonth && year) {
      switch (birthMonth) {
        case "2":
          if (+birthDay > 28) {
            if (!isLeapYear(+year)) {
              mainFormDispatcher({
                type: "SET_BYDAY_BYEAR",
                day: "28",
                year,
              });
              return;
            }
          }
          break;
      }
    }

    mainFormDispatcher({ type: "SET_BYEAR", year });
  };

  return (
    <>
      <StyledLabel>Fecha de Nacimiento</StyledLabel>
      <Container>
        <SelectStyled value={birthDay} onChange={handleDayChange}>
          <option value="">Da</option>
          {days.map((n) => (
            <option key={n} value={n + 1}>
              {n + 1}
            </option>
          ))}
        </SelectStyled>
        <SelectStyled value={birthMonth} onChange={handleMonthChange}>
          <option value="">Mes</option>
          {months.map((m, i) => (
            <option key={m} value={i + 1}>
              {m}
            </option>
          ))}
        </SelectStyled>
        <SelectStyled value={birthYear} onChange={handleYearChange}>
          <option value="">Ao</option>
          {years.map((y) => (
            <option key={y} value={y}>
              {y}
            </option>
          ))}
        </SelectStyled>
        <br />
        {validation.validated && !validation.valid && (
          <ValidationMsg>{validation.msg}</ValidationMsg>
        )}
      </Container>
    </>
  );
};

export default BirthDate;
