import React, { FC, Dispatch } from "react";

// styles
import { Container } from "./ResidenteForm.styled";
import { theme } from "../../styles/theme";
import { TabsContainer, Tab } from "../INEFormMobile/INEFormMobile.styled";

// components
import DocumentForm from "../DocumentForm";

// Context
import { useMasterForm } from "../../Context/MainForm";

// types
import { ValidationObject } from "../../Types/validationTypes";
import { Action } from "../FormId/validation.reducer";

// hooks
import useMediaQuery from "../../hooks/useMedia";

interface ResidenteFormPops {
  validations: ValidationObject;
  validationDispatcher: Dispatch<Action>;
}

const ResidenteForm: FC<ResidenteFormPops> = ({
  validations,
  validationDispatcher,
}) => {
  const { residenteAnverso, residenteFilename } = useMasterForm();
  const isMobile = useMediaQuery(theme.mediaquerys.mobile);

  return (
    <Container>
      {isMobile && (
        <TabsContainer>
          <Tab selected={true}>Foto Anverso</Tab>
        </TabsContainer>
      )}
      <DocumentForm
        title={isMobile ? "" : "Foto Anverso"}
        containerWidth="100%"
        actionType="SET_RESIDENT_ANVERSO"
        validationType="SET_RESIDENTE_ADVERSE_VALIDATION"
        document={residenteAnverso}
        filename={residenteFilename}
        validation={validations}
        validationDispatcher={validationDispatcher}
        reset="RESET_RESIDENT_ANVERSO"
        resetValidation="RESET_RESIDENTE_ADVERSE_VALIDATION"
      />
    </Container>
  );
};

export default ResidenteForm;
