// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_PHONE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_HOMEPHONE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_WORKPHONE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_REF1NAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_REF2NAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_REF1PHONE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_REF1PHONEHOME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_REF2PHONE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_REF2PHONEHOME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "INIT_ALL_VALIDATIONS";
      name1Validation: ValidationObject;
      ref1PhoneValidation: ValidationObject;
    }
  | {
      type: "SET_PHONE_VALIDATIONS";
      ref1PhoneValidation: ValidationObject;
      ref2PhoneValidation: ValidationObject;
    }
  | {
      type: "TOGGLE_MODAL";
      msg: string;
      rejection: boolean;
    };
type State = {
  phoneValidation: ValidationObject;
  phoneHomeValidation: ValidationObject;
  phoneWorkValidation: ValidationObject;
  name1Validation: ValidationObject;
  name2Validation: ValidationObject;
  ref1PhoneValidation: ValidationObject;
  ref1PhoneHomeValidation: ValidationObject;
  ref2PhoneValidation: ValidationObject;
  ref2PhoneHomeValidation: ValidationObject;
  finalModalOpen: boolean;
  message: string;
  rejection: boolean;
};

export const defaultState: State = {
  phoneValidation: { validated: false },
  phoneHomeValidation: { validated: false },
  phoneWorkValidation: { validated: false },
  name1Validation: { validated: false },
  name2Validation: { validated: false },
  ref1PhoneValidation: { validated: false },
  ref1PhoneHomeValidation: { validated: false },
  ref2PhoneValidation: { validated: false },
  ref2PhoneHomeValidation: { validated: false },
  finalModalOpen: false,
  message: "",
  rejection: false,
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_PHONE_VALIDATION":
      return { ...state, phoneValidation: action.validation };
    case "SET_HOMEPHONE_VALIDATION":
      return { ...state, phoneHomeValidation: action.validation };
    case "SET_WORKPHONE_VALIDATION":
      return { ...state, phoneWorkValidation: action.validation };
    case "SET_REF1NAME_VALIDATION":
      return { ...state, name1Validation: action.validation };
    case "SET_REF2NAME_VALIDATION":
      return { ...state, name2Validation: action.validation };
    case "SET_REF1PHONE_VALIDATION":
      return { ...state, ref1PhoneValidation: action.validation };
    case "SET_REF1PHONEHOME_VALIDATION":
      return { ...state, ref1PhoneHomeValidation: action.validation };
    case "SET_REF2PHONE_VALIDATION":
      return { ...state, ref2PhoneValidation: action.validation };
    case "SET_REF2PHONEHOME_VALIDATION":
      return { ...state, ref2PhoneHomeValidation: action.validation };
    case "TOGGLE_MODAL":
      return {
        ...state,
        finalModalOpen: !state.finalModalOpen,
        message: action.msg,
        rejection: action.rejection,
      };
    case "INIT_ALL_VALIDATIONS":
      return {
        ...state,
        name1Validation: action.name1Validation,
        ref1PhoneValidation: action.ref1PhoneValidation,
      };
    case "SET_PHONE_VALIDATIONS":
      return {
        ...state,
        ref1PhoneValidation: action.ref1PhoneValidation,
        ref2PhoneValidation: action.ref2PhoneValidation,
      };
    default:
      return state;
  }
};
