// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_RESPERM_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "RESET_RESPERM_VALIDATION";
    }
  | {
      type: "SET_RESFIS_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "RESET_RESFIS_VALIDATION";
    }
  | {
      type: "SET_WAITNUM_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "RESET_WAITNUM_VALIDATION";
    }
  | {
      type: "SET_QUITED_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "RESET_QUITED_VALIDATION";
    }
  | {
      type: "INIT_ALL_VALIDATIONS";
      resVal: ValidationObject;
      fisVal: ValidationObject;
      waitVal: ValidationObject;
      quitVal: ValidationObject;
    };

type State = {
  resPermVal: ValidationObject;
  resFisVal: ValidationObject;
  waitNumVal: ValidationObject;
  quitedVal: ValidationObject;
};

export const defaultState: State = {
  resPermVal: { validated: false },
  resFisVal: { validated: false },
  waitNumVal: { validated: false },
  quitedVal: { validated: false },
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_RESPERM_VALIDATION":
      return { ...state, resPermVal: action.validation };
    case "RESET_RESPERM_VALIDATION":
      return { ...state, resPermVal: { validated: false } };
    case "SET_RESFIS_VALIDATION":
      return { ...state, resFisVal: action.validation };
    case "RESET_RESFIS_VALIDATION":
      return { ...state, resFisVal: { validated: false } };
    case "SET_WAITNUM_VALIDATION":
      return { ...state, waitNumVal: action.validation };
    case "RESET_WAITNUM_VALIDATION":
      return { ...state, waitNumVal: { validated: false } };
    case "SET_QUITED_VALIDATION":
      return { ...state, quitedVal: action.validation };
    case "RESET_QUITED_VALIDATION":
      return { ...state, quitedVal: { validated: false } };
    case "INIT_ALL_VALIDATIONS":
      return {
        ...state,
        resPermVal: action.resVal,
        resFisVal: action.fisVal,
        waitNumVal: action.waitVal,
        quitedVal: action.quitVal,
      };
    default:
      return state;
  }
};
