const masterFormReducer = (state: State = defaultFormData, action: Action) => {
  switch (action.type) {
    case "SET_STARTMODEL_RES":
      return { ...state, startModelResponse: action.response };
    case "INIT_PROCESS":
      return {
        ...defaultFormData,
        partner: action.partner,
        idProdType: action.idProdType,
        requestNumber: action.requestNumber,
        requestDate: action.requestDate,
      };
    case "SET_OTP":
      return { ...state, otpValue: action.value };
    case "SET_CURRENT_STEP":
      return {
        ...state,
        step: action.step,
        currentStep: Math.max(state.currentStep, action.step),
      };
    case "SET_CTE_PHONEPREFIX":
      return { ...state, ctePhonePrefix: action.ctePhonePrefix };
    case "SET_CTE_PHONE":
      return { ...state, ctePhone: action.value };
    case "SET_CTEPHONE_HOME":
      return { ...state, ctePhoneHome: action.value };
    case "SET_CTEPHONE_WORK":
      return { ...state, ctePhoneWork: action.value };
    case "SET_REF1_PHONE":
      return { ...state, ref1Phone: action.value };
    case "SET_REF1_PHONEHOME":
      return { ...state, ref1PhoneHome: action.value };
    case "SET_REF2_PHONE":
      return { ...state, ref2Phone: action.value };
    case "SET_REF2_PHONEHOME":
      return { ...state, ref2PhoneHome: action.value };
    case "SET_SELECTED_ID":
      return {
        ...state,
        idSelected: action.idSelected,
      };
    case "SET_NATIONALITY_TYPE":
      return { ...state, nationality: action.value };
    case "SET_INE_ANVERSO":
      return {
        ...state,
        ineAnverso: action.document,
        ineAnversoFilename: action.name,
      };
    case "SET_INE_REVERSO":
      return {
        ...state,
        ineReverso: action.document,
        ineReversoFilename: action.name,
      };
    case "SET_RESIDENT_ANVERSO":
      return {
        ...state,
        residenteAnverso: action.document,
        residenteFilename: action.name,
      };
    case "RESET_INE_ANVERSO":
      return {
        ...state,
        ineAnverso: null,
        ineAnversoFilename: "",
      };
    case "RESET_INE_REVERSO":
      return {
        ...state,
        ineReverso: null,
        ineReversoFilename: "",
      };
    case "RESET_RESIDENT_ANVERSO":
      return {
        ...state,
        residenteAnverso: null,
        residenteFilename: "",
      };
    case "SET_GENRE":
      return { ...state, genre: action.genre };
    case "SET_BDAY":
      return { ...state, birthDay: action.day };
    case "SET_BMONTH":
      return { ...state, birthMonth: action.month };
    case "SET_BYEAR":
      return { ...state, birthYear: action.year };
    case "SET_BYDAY_BMONTH":
      return { ...state, birthDay: action.day, birthMonth: action.month };
    case "SET_BYDAY_BYEAR":
      return { ...state, birthDay: action.day, birthYear: action.year };
    case "SET_FIRST_PERSONAL_DATA":
      return { ...state, [action.field]: action.value };
    case "SET_STUDENT":
      return { ...state, student: action.student };
    case "SET_FINANCE":
      return { ...state, finance: action.finance };
    case "SET_RFC":
      return { ...state, rfc: action.rfc };
    case "SET_LIVING_PLACE":
      return { ...state, livingPlace: action.livingPlace };
    case "SET_TIME_RESIDENCE":
      return { ...state, timeResidence: action.timeResidence };
    case "SET_ID_NUM":
      return { ...state, idNum: action.idNum };
    case "SET_BORN_COUNTRY":
      return { ...state, bornCountry: action.bornCountry };
    case "SET_NATIONALITY_COUNTRY":
      return { ...state, natCountry: action.natCountry };
    case "SET_MAIL":
      return { ...state, mail: action.mail };
    case "SET_MAIL_DOMAIN":
      return { ...state, mailDomain: action.mailDomain };
    case "SET_EMPLOY_AREA":
      return { ...state, employArea: action.employArea };
    case "SET_EMPLOY_TYPE":
      return { ...state, employType: action.employType };
    case "SET_PAY_DATE":
      return { ...state, payDate: action.payDate };
    case "SET_BENE_CONT":
      return { ...state, beneCont: action.beneCont };
    case "SET_REF1_PAR":
      return { ...state, reference1Par: action.value };
    case "SET_REF2_PAR":
      return { ...state, reference2Par: action.value };
    case "SET_REF1_NAME":
      return { ...state, reference1Name: action.value };
    case "SET_REF2_NAME":
      return { ...state, reference2Name: action.value };
    case "SET_COUNTRY_ID":
      return { ...state, countryId: action.countryId };
    case "SET_IDEMIT_DAY":
      return { ...state, idEmitDay: action.day };
    case "SET_IDEMIT_DAY_MONTH":
      return { ...state, idEmitDay: action.day, idEmitMonth: action.month };
    case "SET_IDEMIT_MONTH":
      return { ...state, idEmitMonth: action.month };
    case "SET_IDEMIT_YEAR":
      return { ...state, idEmityear: action.year };
    case "SET_IDEMIT_YEAR_DAY":
      return { ...state, idEmitDay: action.day, idEmityear: action.year };
    case "SET_IDVAL_DAY":
      return { ...state, idValDay: action.day };
    case "SET_IDVAL_DAY_MONTH":
      return { ...state, idValDay: action.day, idValMonth: action.month };
    case "SET_IDVAL_MONTH":
      return { ...state, idValMonth: action.month };
    case "SET_IDVAL_YEAR":
      return { ...state, idValyear: action.year };
    case "SET_IDVAL_YEAR_DAY":
      return { ...state, idValDay: action.day, idValyear: action.year };
    case "SET_MORECITIZENS":
      return {
        ...state,
        moreCitizens: action.value,
        citizens: action.value ? state.citizens : "",
      };
    case "SET_CITIZENS":
      return { ...state, citizens: action.value };
    case "SET_VISARES_PER":
      return {
        ...state,
        visaResPer: action.value,
        resPer: action.value ? state.resPer : "",
      };
    case "SET_RES_PERM":
      return { ...state, resPer: action.value };
    case "SET_FIS_OTHER":
      return {
        ...state,
        resFisOther: action.value,
        resFis: action.value ? state.resFis : "",
      };
    case "SET_RES_FIS":
      return { ...state, resFis: action.value };
    case "SET_WAITNUM_CONT":
      return {
        ...state,
        waitNumCont: action.value,
        waitNum: action.value ? state.waitNum : "",
      };
    case "SET_NUM_CONT":
      return { ...state, waitNum: action.value };
    case "SET_QUITED_CITIZEN":
      return {
        ...state,
        quitedCiti: action.value,
        quitedCitizens: action.value ? state.quitedCitizens : "",
      };
    case "SET_QUITED":
      return { ...state, quitedCitizens: action.value };
    case "SET_PRESENTED_DOCS":
      return { ...state, presentedDocs: action.value };
    case "TOGGLE_ADDRESS_MATCH":
      return { ...state, addressValNotMatch: !state.addressValNotMatch };
    case "SET_RECOVERY_FULLCAPTURE_RES":
      return {
        ...state,
        startModelResponse: action.response,
        rfc: action.rfc,
        nationality: action.nationality,
        partner: action.partner,
        ctePhone: action.ctePhone,
        idSelected: action.idSelected,
        idProdType: action.idProdType,
        finance: action.finance,
        fullName: action.fullName,
        productType: action.productType,
      };
    case "SET_RECOVERY_TELCAPTURE_RES":
      return {
        ...state,
        startModelResponse: action.response,
        partner: action.partner,
        ctePhone: action.ctePhone,
        fullName: action.fullName,
        productType: action.productType,
      };
    case "SET_CP_RESULT":
      return {
        ...state,
        zipCod: action.zipCod,
        col: action.col,
        mun: action.mun,
        state: action.state,
        city: action.city,
        isCPSelected: true,
      };
    case "SET_OCR_RESULT":
      return {
        ...state,
        name: action.primerNombre,
        secondName: action.segundoNombre,
        fatherSurname: action.apellidoPaterno,
        motherSurname: action.apellidoMaterno,
        street: action.calle,
        zipCod: action.codigoPostal,
        col: action.colonia,
        mun: action.municipio,
        state: action.estado,
        idNum: action.id,
        birthDay: action.birthDay,
        birthMonth: action.birthMonth,
        birthYear: action.birthYear,
      };
    case "RESET_FORM":
      return defaultFormData;
    default:
      return state;
  }
};

const MasterFormContext = createContext<State | undefined>(undefined);
const MasterFormDispatchContext = createContext<Dispatch | undefined>(
  undefined
);

const MasterFormProvider = ({ children }: MasterFormProviderProps) => {
  const [state, dispatch] = useReducer(masterFormReducer, defaultFormData);

  return (
    <MasterFormContext.Provider value={state}>
      <MasterFormDispatchContext.Provider value={dispatch}>
        {children}
      </MasterFormDispatchContext.Provider>
    </MasterFormContext.Provider>
  );
};

const useMasterForm = () => {
  const context = useContext(MasterFormContext);
  if (context === undefined) {
    throw new Error("useMasterForm must be used within a MasterFormProvider");
  }
  return context;
};

const useMasterFormDispatch = () => {
  const context = useContext(MasterFormDispatchContext);
  if (context === undefined) {
    throw new Error(
      "useMasterFormDispath must be used within a MasterFormProvider"
    );
  }
  return context;
};

export { MasterFormProvider, useMasterForm, useMasterFormDispatch };
