import React, { FC } from "react";

// styles
import {
  Container,
  Title,
  Message,
  BtnText,
} from "./NameConfirmationModal.styled";

// components
import OptButton from "../SecondaryBtn";

interface NameConfirmationProps {
  toggleModal: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
  handleContinue: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
  secondName: string;
  fatherSurname: string;
  motherSurname: string;
}

const NameConfirmationModal: FC<NameConfirmationProps> = ({
  toggleModal,
  handleContinue,
  secondName,
  fatherSurname,
  motherSurname,
}) => {
  return (
    <Container>
      <Title>Confirmacin de datos.</Title>
      <Message>{`Estas dejando ${secondName.length ? "" : "Segundo nombre,"}${
        fatherSurname.length ? "" : " Primer apellido,"
      }${
        motherSurname.length ? "" : " Segundo apellido,"
      } como vacio.`}</Message>
      <Message>{"Es esto correcto ?."}</Message>
      <br />
      <div style={{ textAlign: "center" }}>
        <OptButton onClick={toggleModal}>
          <BtnText>Cancelar</BtnText>
        </OptButton>
        <OptButton onClick={handleContinue}>
          <BtnText>Aceptar</BtnText>
        </OptButton>
      </div>
    </Container>
  );
};

export default NameConfirmationModal;
