// types
import { ValidationObject } from "../../Types/validationTypes";

export type Action =
  | {
      type: "SET_NAME";
      value: string;
    }
  | {
      type: "SET_SECOND_NAME";
      value: string;
    }
  | {
      type: "SET_FATHER_SURNAME";
      value: string;
    }
  | {
      type: "SET_MOTHER_SURNAME";
      value: string;
    }
  | {
      type: "SET_BIRTH_DAY";
      day: string;
    }
  | {
      type: "SET_BIRTH_DAY_MONTH";
      day: string;
      month: string;
    }
  | {
      type: "SET_BIRTH_DAY_YEAR";
      day: string;
      year: string;
    }
  | {
      type: "SET_BIRTH_MONTH";
      month: string;
    }
  | {
      type: "SET_BIRTH_YEAR";
      year: string;
    }
  | {
      type: "SET_NAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_SECONDNAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_FATHERSURNAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_MOTHERSURNAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_BIRTHDATE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "INIT_ALL_VALIDATIONS";
      nameVal: ValidationObject;
      secondNameVal: ValidationObject;
      fSurnameVal: ValidationObject;
      mSurnameVal: ValidationObject;
      birthVal: ValidationObject;
    };

type State = {
  valName: string;
  valSecName: string;
  valFathSur: string;
  valMothSur: string;
  dayVal: string;
  monthVal: string;
  yearVal: string;
  nameValidation: ValidationObject;
  secondNameValidation: ValidationObject;
  fatherSurnameValidation: ValidationObject;
  motherSurnameValidation: ValidationObject;
  birthdayValidation: ValidationObject;
};

export const defaultState: State = {
  valName: "",
  valSecName: "",
  valFathSur: "",
  valMothSur: "",
  dayVal: "",
  monthVal: "",
  yearVal: "",
  nameValidation: { validated: false },
  secondNameValidation: { validated: false },
  fatherSurnameValidation: { validated: false },
  motherSurnameValidation: { validated: false },
  birthdayValidation: { validated: false },
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_NAME":
      return { ...state, valName: action.value };
    case "SET_SECOND_NAME":
      return { ...state, valSecName: action.value };
    case "SET_FATHER_SURNAME":
      return { ...state, valFathSur: action.value };
    case "SET_MOTHER_SURNAME":
      return { ...state, valMothSur: action.value };
    case "SET_BIRTH_DAY":
      return { ...state, dayVal: action.day };
    case "SET_BIRTH_DAY_MONTH":
      return { ...state, dayVal: action.day, monthVal: action.month };
    case "SET_BIRTH_DAY_YEAR":
      return { ...state, dayVal: action.day, yearVal: action.year };
    case "SET_BIRTH_MONTH":
      return { ...state, monthVal: action.month };
    case "SET_BIRTH_YEAR":
      return { ...state, yearVal: action.year };
    case "SET_NAME_VALIDATION":
      return { ...state, nameValidation: action.validation };
    case "SET_SECONDNAME_VALIDATION":
      return { ...state, secondNameValidation: action.validation };
    case "SET_FATHERSURNAME_VALIDATION":
      return { ...state, fatherSurnameValidation: action.validation };
    case "SET_MOTHERSURNAME_VALIDATION":
      return { ...state, motherSurnameValidation: action.validation };
    case "SET_BIRTHDATE_VALIDATION":
      return { ...state, birthdayValidation: action.validation };
    case "INIT_ALL_VALIDATIONS":
      return {
        ...state,
        nameValidation: action.nameVal,
        secondNameValidation: action.secondNameVal,
        fatherSurnameValidation: action.fSurnameVal,
        motherSurnameValidation: action.mSurnameVal,
        birthdayValidation: action.birthVal,
      };
    default:
      return state;
  }
};
