import React, { FC } from "react";
import { useHistory } from "react-router-dom";

// styles
import { Container, Title, Message, BtnText } from "./RejectionMensaje.styled";

// icons
import { ReactComponent as RejectIcon } from "../../assets/icons/RejectIcon.svg";

// Context
import { useMasterFormDispatch } from "../../Context/MainForm";

// components
import Finish from "../ui/SecondaryBtn";
interface RejectionMesajeProps {
  msg: string;
  restart?: boolean;
}

const { REACT_APP_ROUTER_PREFIX } = process.env;

const RejeceitonMensaje: FC<RejectionMesajeProps> = ({ msg, restart }) => {
  const history = useHistory();
  const mainFormDispatcher = useMasterFormDispatch();

  const finishProcess = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "RESET_FORM" });
    history.push(REACT_APP_ROUTER_PREFIX || "/");
  };

  return (
    <Container>
      {!restart && <RejectIcon />}
      <Title>
        {restart ? (
          "Upsss..."
        ) : (
          <>
            Lo sentimos!
            <br />
            Tu trmite ha sido rechazado.
          </>
        )}
      </Title>
      <Message>{msg}</Message>
      <Finish onClick={finishProcess}>
        <BtnText>{restart ? "Reiniciar trmite" : "Finalizar"}</BtnText>
      </Finish>
    </Container>
  );
};

export default RejeceitonMensaje;
