// types
import { ValidationObject } from "../../Types/validationTypes";

export type Action =
  | {
      type: "SET_INE_ADVERSE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_INE_REVERSE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_RESIDENTE_ADVERSE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "RESET_INE_ADVERSE_VALIDATION";
    }
  | {
      type: "RESET_INE_REVERSE_VALIDATION";
    }
  | {
      type: "RESET_RESIDENTE_ADVERSE_VALIDATION";
    };
type State = {
  adverseIneValidation: ValidationObject;
  reverseIneValidation: ValidationObject;
  adverseResidenteValidation: ValidationObject;
};

export const defaultState: State = {
  adverseIneValidation: { validated: false },
  reverseIneValidation: { validated: false },
  adverseResidenteValidation: { validated: false },
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_INE_ADVERSE_VALIDATION":
      return { ...state, adverseIneValidation: action.validation };
    case "SET_INE_REVERSE_VALIDATION":
      return { ...state, reverseIneValidation: action.validation };
    case "SET_RESIDENTE_ADVERSE_VALIDATION":
      return { ...state, adverseResidenteValidation: action.validation };
    case "RESET_INE_ADVERSE_VALIDATION":
      return { ...state, adverseIneValidation: { validated: false } };
    case "RESET_INE_REVERSE_VALIDATION":
      return { ...state, reverseIneValidation: { validated: false } };
    case "RESET_RESIDENTE_ADVERSE_VALIDATION":
      return { ...state, adverseResidenteValidation: { validated: false } };
    default:
      return state;
  }
};
