import React, { FC } from "react";
import { useLocation, useHistory } from "react-router-dom";

// styles
import {
  BannerContainer,
  MainTitle,
  LittleWhiteLine,
  WhiteLine,
  BannerHomeContainer,
  HomeBannerHeader,
  LogoWrapperHome,
  ContentBannerHome,
  BannerImg,
  DesktopBannerUp,
  DesktopBannerDown,
  Triangle,
  SkewedStripe,
  LogoSingleWrapper,
  TitleBanner2,
  MainDesktopHeader,
  HeaderContainer,
  HeaderDecoration,
  HeaderMobile,
} from "./MainBanner.styled";
import { PurpleLine } from "../../pages/Home/Home.styled";
import { theme } from "../../styles/theme";

// icons
import { ReactComponent as Logo } from "../../assets/icons/Logotipo.svg";
import { ReactComponent as LogoSingle } from "../../assets/icons/LogoBradesSingle.svg";
import { ReactComponent as ArrowIcon } from "../../assets/icons/Arrow.svg";

// compoenents
import BannerLink from "../ui/BannerLink";
import ArrowButton from "../ui/ArrowButton";

// hooks
import mediaQuery from "../../hooks/useMedia";

// Context
import { useMasterForm } from "../../Context/MainForm";

// utils
import { calculateImgBanner, calculateTitle } from "./MainBanner.utils";

const { REACT_APP_ROUTER_PREFIX } = process.env;

const MainBanner: FC = () => {
  const { pathname } = useLocation();
  const pathnameToCheck =
    pathname.length < 2 ? pathname : pathname.replace(/\/$/, "");
  const isHome = pathnameToCheck === REACT_APP_ROUTER_PREFIX;
  const isMobile = mediaQuery(theme.mediaquerys.mobile);
  const isDesktopLg = mediaQuery(theme.mediaquerys.desktopLg);
  const history = useHistory();
  const { step, idSelected } = useMasterForm();
  const imgURL = calculateImgBanner(step, idSelected, pathname);
  const titleText = calculateTitle(step, idSelected, pathname);

  const alternativeTitle = step >= 0.3 && step < 0.75;

  const navigateBack = () => {
    history.goBack();
  };

  return (
    <>
      {isHome ? (
        <BannerHomeContainer>
          <HomeBannerHeader>
            <LogoWrapperHome>
              <Logo />
            </LogoWrapperHome>
            {!isMobile && (
              <>
                <BannerLink href="https://www.bradescard.com.mx/">
                  Ir a bradescard.com.mx
                </BannerLink>
                <WhiteLine top={"0"} right={"0"} lineWidth={"70px"} />
              </>
            )}
          </HomeBannerHeader>
          <ContentBannerHome>
            <BannerImg />
            <WhiteLine
              top={isMobile ? "0" : "20px"}
              right={isMobile ? "-70px" : "0"}
            />
            <WhiteLine
              bottom={isMobile ? "80px" : "40px"}
              right={isMobile ? "70%" : "80px"}
              lineWidth={"200px"}
            />
            {!isMobile && (
              <WhiteLine bottom={"40px"} right={"500px"} lineWidth={"200px"} />
            )}
            <MainTitle>
              <p className="first">Bienvenido a</p>
              <p className="middle">Tramitacin</p>
              <p className="last">en Lnea</p>
              <LittleWhiteLine />
            </MainTitle>
          </ContentBannerHome>
        </BannerHomeContainer>
      ) : (
        <>
          {isDesktopLg && step > 0.1 && (
            <MainDesktopHeader>
              <HeaderContainer>
                <HeaderDecoration />
                <PurpleLine left="-180px;" bottom="-40px" />
              </HeaderContainer>
            </MainDesktopHeader>
          )}
          <BannerContainer>
            {!isDesktopLg ? (
              <HeaderMobile>
                <ArrowButton onClick={navigateBack}>
                  <ArrowIcon />
                </ArrowButton>
                <LogoWrapperHome>
                  <Logo />
                </LogoWrapperHome>
              </HeaderMobile>
            ) : (
              <>
                <DesktopBannerUp imgURL={imgURL}>
                  <WhiteLine top={"20px"} right={"-50px"} />
                  <WhiteLine bottom={"40%"} left={"0"} lineWidth={"70px"} />
                  <Triangle
                    top={"-40px"}
                    left={"-100px"}
                    color={"redToPurpleLinearGradient3"}
                  />
                  <LogoSingleWrapper>
                    <LogoSingle />
                  </LogoSingleWrapper>
                  <TitleBanner2 alternativeTitle={alternativeTitle}>
                    {titleText.main}
                    <br /> <strong className="weak">{titleText.sub}</strong>
                  </TitleBanner2>
                </DesktopBannerUp>
                <DesktopBannerDown>
                  <Triangle top={"-100px"} left={"-130px"} />
                </DesktopBannerDown>
                <SkewedStripe
                  width={"90px"}
                  height={"20px"}
                  bottom={"35%"}
                  left={"0"}
                />
                <SkewedStripe
                  width={"250px"}
                  height={"30px"}
                  bottom={"11%"}
                  right={"0"}
                />
              </>
            )}
          </BannerContainer>
        </>
      )}
    </>
  );
};

export default MainBanner;
