const { REACT_APP_PUBLIC_URL, REACT_APP_ROUTER_PREFIX } = process.env;

export const calculateImgBanner = (
  step: number,
  idSelected: string,
  path: string
) => {
  if (
    (idSelected !== "1" && step >= 0.6 && step < 0.75) ||
    path ===
      (REACT_APP_ROUTER_PREFIX?.length === 1
        ? "/new/retomar/tramite"
        : `${REACT_APP_ROUTER_PREFIX}/new/retomar/tramite`) ||
    path.split("/").pop() === "valida-domicilio"
  ) {
    return `${REACT_APP_PUBLIC_URL}images/banner_img_5.jpg`;
  } else if (step >= 0.3 && step < 0.75) {
    return `${REACT_APP_PUBLIC_URL}images/banner_img_3.jpg`;
  } else if (step >= 0.75) {
    return `${REACT_APP_PUBLIC_URL}images/banner_img_4.jpg`;
  }
  return `${REACT_APP_PUBLIC_URL}images/banner_img_2.jpg`;
};

export const calculateTitle = (
  step: number,
  idSelected: string,
  path: string
) => {
  if (
    (idSelected !== "1" && step >= 0.6 && step < 0.75) ||
    path ===
      (REACT_APP_ROUTER_PREFIX?.length === 1
        ? "/new/retomar/tramite"
        : `${REACT_APP_ROUTER_PREFIX}/new/retomar/tramite`) ||
    path.split("/").pop() === "valida-domicilio"
  ) {
    return { main: "S como Julieta,", sub: "ya tiene su tarjeta." };
  } else if (step >= 0.3 && step < 0.75) {
    return { main: "S como Mauro y Javi,", sub: "ya tienen su tarjeta." };
  } else if (step >= 0.75) {
    return { main: "S como Ana,", sub: "ya tiene su tarjeta." };
  }
  return { main: "S como Jessi,", sub: "ya tiene su tarjeta." };
};
