import React, { FC } from "react";

// styles
import {
  Container,
  StyledProgress,
  CheckMark,
  StyledBar,
} from "./ProgressBar.styled";

// icons
import { ReactComponent as CheckIcon } from "../../../assets/icons/CheckMark.svg";

interface ProgressBarProps {
  value: number;
}

const ProgressBar: FC<ProgressBarProps> = ({ value }) => {
  return (
    <Container>
      <StyledBar>
        <StyledProgress value={value * 100} />
      </StyledBar>
      <CheckMark value={value * 100}>
        <CheckIcon />
      </CheckMark>
    </Container>
  );
};

export default ProgressBar;
