import React, { FC, useReducer, useEffect, Dispatch, useRef } from "react";
import Webcam from "react-webcam";

// styles
import {
  Container,
  Title,
  ButtonsWrapper,
  FileShowcase,
  DocumentPreview,
  StyledFileSelector,
  CrossWrapper,
  ValidationIconWrapper,
  CameraWrapper,
  CameraView,
  CameraHeader,
  TakePicWrapper,
  PreviewImgWrapper,
  CfmButtonsWrapper,
  ConfirmPreview,
  CancelPreview,
  FileNameText,
} from "./DocumentForm.styled";
import { LogoWrapperHome } from "../MainBanner/MainBanner.styled";

// components
import ActionButton from "../ui/SecondaryBtn";
import ArrowButton from "../ui/ArrowButton";

// icons
import { ReactComponent as CameraIcon } from "../../assets/icons/Camera.svg";
import { ReactComponent as ClipIcon } from "../../assets/icons/Clip.svg";
import { ReactComponent as IdIcon } from "../../assets/icons/IdIcon.svg";
import { ReactComponent as CrossIcon } from "../../assets/icons/CrossId.svg";
import { ReactComponent as CheckIcon } from "../../assets/icons/CheckMark.svg";
import { ReactComponent as InvalidIcon } from "../../assets/icons/Cross.svg";
import { ReactComponent as Logo } from "../../assets/icons/Logotipo.svg";
import { ReactComponent as ArrowIcon } from "../../assets/icons/Arrow.svg";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// types
import { ValidationObject } from "../../Types/validationTypes";
import { Action } from "../FormId/validation.reducer";
import { documentFormReducer, defaultState } from "./DocumentForm.reducer";

//utils
import { validateDocument } from "./DocumentForm.urils";

interface DocumentFormProps {
  hide?: boolean;
  title?: string | undefined;
  filename: string;
  containerWidth?: string | undefined;
  document: File | null;
  actionType: "SET_INE_ANVERSO" | "SET_INE_REVERSO" | "SET_RESIDENT_ANVERSO";
  reset: "RESET_INE_ANVERSO" | "RESET_INE_REVERSO" | "RESET_RESIDENT_ANVERSO";
  validationType:
    | "SET_INE_ADVERSE_VALIDATION"
    | "SET_INE_REVERSE_VALIDATION"
    | "SET_RESIDENTE_ADVERSE_VALIDATION";
  resetValidation:
    | "RESET_INE_ADVERSE_VALIDATION"
    | "RESET_INE_REVERSE_VALIDATION"
    | "RESET_RESIDENTE_ADVERSE_VALIDATION";
  validation: ValidationObject;
  validationDispatcher: Dispatch<Action>;
}

const DocumentForm: FC<DocumentFormProps> = ({
  hide,
  title,
  filename,
  containerWidth,
  document,
  actionType,
  validationType,
  reset,
  resetValidation,
  validation,
  validationDispatcher,
}) => {
  const { currentStep } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const fileRef = useRef<HTMLInputElement | null>(null);
  const [
    { previewImg, cameraOpen, capturedPreview },
    dcmFormDispatcher,
  ] = useReducer(documentFormReducer, defaultState);
  const webcamRef = useRef<Webcam | null>(null);

  useEffect(() => {
    if (!document) return;
    const objectURL = URL.createObjectURL(document);
    dcmFormDispatcher({ type: "SET_PREVIEW_IMG", previewImg: objectURL });

    return () => {
      URL.revokeObjectURL(objectURL);
    };
  }, [document]);

  useEffect(() => {
    const validateDoc = async (doc: File) => {
      const validation = await validateDocument(doc);
      validationDispatcher({ type: validationType, validation });
    };

    if (currentStep > 0.4 && document) {
      validateDoc(document);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleFileSelection = async (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    const files = e.target.files;
    if (files && files.length) {
      mainFormDispatcher({
        type: actionType,
        document: files[0],
        name: files[0].name,
      });
      const validation = await validateDocument(files[0]);
      validationDispatcher({ type: validationType, validation });
    }
  };

  const clearForm = () => {
    URL.revokeObjectURL(previewImg);
    dcmFormDispatcher({ type: "SET_PREVIEW_IMG", previewImg: "" });
    validationDispatcher({ type: resetValidation });
    mainFormDispatcher({ type: reset });
    if (fileRef.current) {
      fileRef.current.value = "";
    }
  };

  const toggleCamera = () => {
    dcmFormDispatcher({ type: "TOGGLE_CAMERA" });
  };

  const confirmPicture = React.useCallback(async () => {
    if (capturedPreview) {
      const res = await fetch(capturedPreview);
      const buffer = await res.arrayBuffer();
      const file = new File([buffer], "capture.jpg", { type: "image/jpeg" });
      mainFormDispatcher({
        type: actionType,
        document: file,
        name: file.name,
      });
      toggleCamera();
      dcmFormDispatcher({ type: "CANCEL_PREVIEW_CAPTURE" });
      const validation = await validateDocument(file);
      validationDispatcher({ type: validationType, validation });
    }
  }, [
    capturedPreview,
    mainFormDispatcher,
    actionType,
    validationDispatcher,
    validationType,
  ]);

  const takePicture = React.useCallback(() => {
    const imageSrc = webcamRef.current?.getScreenshot();
    if (imageSrc) {
      dcmFormDispatcher({
        type: "SET_PREVIEW_CAPTURE",
        capture: imageSrc,
      });
    }
  }, [webcamRef]);

  const cancelPreview = () => {
    dcmFormDispatcher({ type: "CANCEL_PREVIEW_CAPTURE" });
  };

  return (
    <>
      <>
        {cameraOpen && (
          <CameraWrapper>
            <CameraHeader>
              <ArrowButton onClick={toggleCamera}>
                <ArrowIcon />
              </ArrowButton>
              <LogoWrapperHome>
                <Logo />
              </LogoWrapperHome>
            </CameraHeader>
            {capturedPreview ? (
              <PreviewImgWrapper>
                <img src={capturedPreview} alt="vista previa del documento" />
                <CfmButtonsWrapper>
                  <CancelPreview onClick={cancelPreview}>
                    <InvalidIcon />
                  </CancelPreview>
                  <ConfirmPreview onClick={confirmPicture}>
                    <CheckIcon />
                  </ConfirmPreview>
                </CfmButtonsWrapper>
              </PreviewImgWrapper>
            ) : (
              <>
                {" "}
                <CameraView>
                  <Webcam
                    audio={false}
                    ref={webcamRef}
                    screenshotFormat="image/jpeg"
                  />
                </CameraView>
                <TakePicWrapper onClick={takePicture}>
                  <CameraIcon />
                </TakePicWrapper>
              </>
            )}
          </CameraWrapper>
        )}
      </>
      <Container
        containerWidth={containerWidth}
        style={hide ? { display: "none" } : {}}
      >
        {title && <Title>{title}</Title>}
        <ButtonsWrapper>
          <ActionButton slim onClick={toggleCamera}>
            <CameraIcon style={{ height: 15 }} />
            Capturar
          </ActionButton>
          <StyledFileSelector htmlFor={actionType}>
            <ActionButton slim>
              <ClipIcon style={{ height: 15 }} />
              Adjuntar
            </ActionButton>
          </StyledFileSelector>
        </ButtonsWrapper>
        <input
          type="file"
          id={actionType}
          accept="image/x-png, image/jpeg"
          capture="camera"
          onChange={handleFileSelection}
          ref={fileRef}
        />
        <FileShowcase filename={filename}>
          {filename ? (
            <>
              <FileNameText>{filename}</FileNameText>
              <CrossWrapper onClick={clearForm}>
                <CrossIcon />
              </CrossWrapper>
            </>
          ) : (
            "Archivos adjuntos"
          )}
        </FileShowcase>
        <DocumentPreview
          validated={validation.validated}
          valid={validation.valid}
        >
          {previewImg ? (
            <img alt="documento de identificacin" src={previewImg} />
          ) : (
            <IdIcon />
          )}
          {validation.validated && (
            <ValidationIconWrapper invalid={!validation.valid}>
              {validation.valid ? <CheckIcon /> : <InvalidIcon />}
            </ValidationIconWrapper>
          )}
        </DocumentPreview>
      </Container>
    </>
  );
};

export default DocumentForm;
