// types
import { defaultSelectorOption } from "../../Types/selectorOption";

type Action = {
  type: "SET_RELATIVES_OPTIONS";
  value: defaultSelectorOption[];
};
type State = {
  referenceParOptons: defaultSelectorOption[];
};

export const referenceDefaultState: State = {
  referenceParOptons: [
    { text: "Esposo/a", value: "1" },
    { text: "Padres", value: "2" },
    { text: "Hijos", value: "3" },
    { text: "Hermanos", value: "4" },
    { text: "Amistad", value: "5" },
  ],
};

export const referenceReducer = (
  state: State = referenceDefaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_RELATIVES_OPTIONS":
      return { ...state, referenceParOptons: action.value };
    default:
      return state;
  }
};
