import React, { forwardRef } from "react";

// components
import {
  Container,
  ZipCode,
  ZipIconContainer,
  ZipCodeDetail,
} from "./ZipCodeResults.styled";

// icons
import { ReactComponent as PinIcon } from "../../../assets/icons/Pin.svg";

// types
import { zipCode } from "../../../Types/api";

// Context
import { useMasterFormDispatch } from "../../../Context/MainForm";

interface ZipCodeResultsProps {
  codigosPostales: zipCode[];
  closeZipPreview: () => void;
}

const ZipCodeResults = forwardRef<HTMLDivElement | null, ZipCodeResultsProps>(
  ({ codigosPostales, closeZipPreview }, ref) => {
    const mainFormDispatcher = useMasterFormDispatch();

    const handleZipCodeSelection = (cp: zipCode) => (
      e: React.MouseEvent<HTMLDivElement, MouseEvent>
    ) => {
      mainFormDispatcher({
        type: "SET_CP_RESULT",
        zipCod: cp.codigoPostal,
        city: cp.ciudad,
        state: cp.estado,
        col: cp.colonia || "",
        mun: cp.municipio || "",
      });
      closeZipPreview();
    };

    return (
      <Container>
        {codigosPostales.map((cp, i) => (
          <ZipCode
            key={`cp-${i}`}
            onClick={handleZipCodeSelection(cp)}
            ref={ref}
          >
            <ZipIconContainer>
              <PinIcon />
            </ZipIconContainer>
            <ZipCodeDetail>
              <span>{cp.codigoPostal}</span> {cp.colonia}
            </ZipCodeDetail>
          </ZipCode>
        ))}
      </Container>
    );
  }
);

export default ZipCodeResults;
