import styled from "styled-components";

interface PhoneInputProps {
  inputWidth?: number;
  invalid?: boolean;
  isPrefix?: boolean;
}

interface PhoneNumWrapperProps {
  invalid?: boolean;
  validated?: boolean;
}

interface ValidationIconWrapperProps {
  invalid: boolean;
}

export const PhoneNumWrapper = styled.div<PhoneNumWrapperProps>`
  align-items: flex-end;
  border-bottom: 1px solid
    ${({ theme: { colors }, invalid, validated }) =>
      validated
        ? colors.greenCheck
        : invalid
        ? colors.errorRed
        : colors.homeTriangle};
  color: ${({ theme: { colors } }) => colors.gray};
  display: flex;
  font-size: 16px;
  font-weight: 600;
  height: 40px;
  margin-top: 25px;
  padding: 5px 10px;
  position: relative;
  width: 100%;

  ${({ theme: { up, breakpoints } }) => up(breakpoints.desktop)} {
    font-size: 23px;
    margin-top: 20px;
    padding: 5px 12px;
    width: 368px;
  }
`;

export const PhoneInput = styled.input<PhoneInputProps>`
  border: 0;
  color: ${({ theme: { colors }, invalid, isPrefix }) =>
    invalid ? colors.errorRed : isPrefix ? colors.gray : colors.bradescardBlue};
  font-size: 16px;
  font-weight: 600;
  width: ${({ inputWidth }) => (inputWidth ? `${inputWidth}px` : "95%")};

  &:focus {
    outline: none;
  }

  ${({ theme: { up, breakpoints } }) => up(breakpoints.desktop)} {
    font-size: 22px;
  }

  ${({ theme: { downHeight, breakpoints } }) =>
    downHeight(breakpoints.midHeight)} {
    font-size: 17px;
  }
`;

export const ValidationIconWrapper = styled.span<ValidationIconWrapperProps>`
  align-items: center;
  border: 1px solid
    ${({ theme: { colors }, invalid }) =>
      invalid ? colors.errorRed : colors.greenCheck};
  border-radius: 50%;
  bottom: 50%;
  display: flex;
  height: 20px;
  justify-content: center;
  position: absolute;
  right: 10px;
  width: 20px;
  transform: translateY(50%);

  svg {
    width: 9px;
  }
`;
