import React, { FC, useEffect, useReducer } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import { useQuery } from "react-query";

// styles
import {
  Form,
  DocumentSelectionTabs,
  ImgMsg,
  ContinueWrapper,
  FetchingSpinnerContainer,
} from "./FormId.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";
import { theme } from "../../styles/theme";

// components
import DocumentTab from "../ui/DocumentTab";
import INEForm from "../INEForm";
import ResidenteForm from "../ResidenteForm";
import ContinueBtn from "../ui/PrimaryBtn";
import INEFormMobile from "../INEFormMobile";
import SmallSpinner from "../ui/SmallSpinner";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// hooks
import useMediaQuery from "../../hooks/useMedia";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// utils
import { defaultState, validationReducer } from "./validation.reducer";
import { getDataFromINE } from "./FormId.utils";
import { flasher } from "../../utils/Flasher";
import { toFullTear } from "../../utils/dateUtils";

// types
import { ocrResponse } from "../../Types/api";

const FormId: FC<RouteComponentProps> = ({ match }) => {
  const { idSelected, step, ineAnverso, ineReverso } = useMasterForm();
  const mainFormDispatcher = useMasterFormDispatch();
  const [
    { adverseIneValidation, reverseIneValidation, adverseResidenteValidation },
    validationDispatcher,
  ] = useReducer(validationReducer, defaultState);
  const isMobile = useMediaQuery(theme.mediaquerys.mobile);
  const isTablet = useMediaQuery(theme.mediaquerys.tablet);
  const history = useHistory();
  const { refetch: getDataOCR, isLoading } = useQuery(
    ["getDataFromINE", ineAnverso, ineReverso],
    getDataFromINE,
    {
      enabled: false,
      onError: (error) => {
        console.error(error);
        flasher(
          "No se pudo obtener datos desde la imagen proporcionada",
          "warning"
        );
      },
      onSuccess: ({
        apellidoMaterno,
        apellidoPaterno,
        calle,
        codigoPostal,
        colonia,
        estado,
        fechaDeNacimiento,
        id,
        municipio,
        primerNombre,
        segundoNombre,
      }: ocrResponse) => {
        let day = "";
        let month = "";
        let year = "";
        if (segundoNombre?.includes("error")) {
          flasher(
            "No se pudo obtener datos de las imagenes proporcionadas",
            "warning"
          );
          return;
        }
        if (fechaDeNacimiento) {
          const splited = fechaDeNacimiento.split("/");
          day = splited[0][0] === "0" ? splited[0][1] : splited[0];
          month = splited[1][0] === "0" ? splited[1][1] : splited[1];
          year = toFullTear(+splited[2]);
        }

        mainFormDispatcher({
          type: "SET_OCR_RESULT",
          apellidoMaterno: apellidoMaterno || "",
          apellidoPaterno: apellidoPaterno || "",
          calle: calle || "",
          codigoPostal: codigoPostal || "",
          colonia: colonia || "",
          estado: estado || "",
          id: id || "",
          municipio: municipio || "",
          primerNombre: primerNombre || "",
          segundoNombre: segundoNombre || "",
          birthDay: day,
          birthMonth: month,
          birthYear: year,
        });
      },
    }
  );

  useEffect(() => {
    if (ineAnverso && ineReverso) {
      getDataOCR();
    }
  }, [ineAnverso, ineReverso, getDataOCR]);

  useEffect(() => {
    if (step < 0.4) {
      history.goBack();
    }
  }, [step, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.4 });
  }, [mainFormDispatcher]);

  const goToFlyAnimation = () => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.5 });
    history.push(`${match.url}/procesando`);
  };

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    goToFlyAnimation();
  };

  const handleDocumentSelection = (document: string) => (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    mainFormDispatcher({ type: "SET_SELECTED_ID", idSelected: document });
  };

  const checkDisabled = () => {
    if (idSelected === "1") {
      return (
        !adverseIneValidation.validated ||
        !adverseIneValidation.valid ||
        !reverseIneValidation.validated ||
        !reverseIneValidation.valid
      );
    } else {
      return (
        !adverseResidenteValidation.validated ||
        !adverseResidenteValidation.valid
      );
    }
  };

  return (
    <Form onSubmit={(e) => e.preventDefault()}>
      <FormHeader>
        <FormTitle bolder style={{ marginTop: 0 }}>
          Valida tu identificacin
        </FormTitle>
      </FormHeader>
      <FormSubtitle
        style={
          isMobile
            ? { padding: "0 30px", textAlign: "center" }
            : { marginBottom: 0 }
        }
      >
        Qu identificacin deseas capturar?
      </FormSubtitle>
      <DocumentSelectionTabs>
        <DocumentTab
          selected={idSelected === "1"}
          onClick={handleDocumentSelection("1")}
        >
          INE/IFE
        </DocumentTab>
        <DocumentTab
          selected={idSelected !== "1"}
          onClick={handleDocumentSelection("2")}
        >
          Otros
        </DocumentTab>
      </DocumentSelectionTabs>
      <ImgMsg>
        Es necesario tu foto anverso{idSelected === "1" && " y reverso"}.
        (Tamao ideal 2542 x 1944 px - 250 ppp)
      </ImgMsg>
      {idSelected === "1" ? (
        isMobile || isTablet ? (
          <INEFormMobile
            validations={{ adverseIneValidation, reverseIneValidation }}
            validationDispatcher={validationDispatcher}
          />
        ) : (
          <INEForm
            validations={{ adverseIneValidation, reverseIneValidation }}
            validationDispatcher={validationDispatcher}
          />
        )
      ) : (
        <ResidenteForm
          validations={adverseResidenteValidation}
          validationDispatcher={validationDispatcher}
        />
      )}
      {isLoading ? (
        <FetchingSpinnerContainer>
          <SmallSpinner />
        </FetchingSpinnerContainer>
      ) : (
        <ContinueWrapper>
          <ContinueBtn disabled={checkDisabled()} onClick={handleContinue}>
            Continuar
            <ContinueIconWrapper>
              <ContinueIcon />
            </ContinueIconWrapper>
          </ContinueBtn>
        </ContinueWrapper>
      )}
    </Form>
  );
};

export default FormId;
