// types
import { ValidationObject } from "../../Types/validationTypes";

type Action =
  | {
      type: "SET_REF2NAME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_REF2PHONE_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_REF2PHONEHOME_VALIDATION";
      validation: ValidationObject;
    }
  | {
      type: "SET_PHONE_VALIDATIONS";
      ref1PhoneValidation: ValidationObject;
      ref2PhoneValidation: ValidationObject;
    }
  | {
      type: "TOGGLE_MODAL";
      msg: string;
      rejection: boolean;
    };
type State = {
  name2Validation: ValidationObject;
  ref2PhoneValidation: ValidationObject;
  ref2PhoneHomeValidation: ValidationObject;
  finalModalOpen: boolean;
  message: string;
  rejection: boolean;
};

export const defaultState: State = {
  name2Validation: { validated: false },
  ref2PhoneValidation: { validated: false },
  ref2PhoneHomeValidation: { validated: false },
  finalModalOpen: false,
  message: "",
  rejection: false,
};

export const validationReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_REF2NAME_VALIDATION":
      return { ...state, name2Validation: action.validation };
    case "SET_REF2PHONE_VALIDATION":
      return { ...state, ref2PhoneValidation: action.validation };
    case "SET_REF2PHONEHOME_VALIDATION":
      return { ...state, ref2PhoneHomeValidation: action.validation };
    case "TOGGLE_MODAL":
      return {
        ...state,
        finalModalOpen: !state.finalModalOpen,
        message: action.msg,
        rejection: action.rejection,
      };
    case "SET_PHONE_VALIDATIONS":
      return {
        ...state,
        ref1PhoneValidation: action.ref1PhoneValidation,
        ref2PhoneValidation: action.ref2PhoneValidation,
      };
    default:
      return state;
  }
};
