import React, { useEffect, useReducer, FC, useState } from "react";
import { RouteComponentProps, useHistory } from "react-router-dom";
import { useQuery } from "react-query";
import ReactModal from "react-modal";

// styles
import {
  Form,
  TableHeader,
  TableFooter,
  FetchingSpinnerContainer,
} from "./AddressValidation.styled";
import {
  FormHeader,
  FormTitle,
  FormSubtitle,
  ContinueIconWrapper,
} from "../FormClientPhone/FormClientPhone.styled";

// components
import ContinueBtn from "../ui/PrimaryBtn";
import AddressTableRow from "../AddressTableRow";
import NotMatchSelector from "../ui/DocumentTab";
import RejectionMessage from "../RejectionMensage";
import SmallSpinner from "../ui/SmallSpinner";

// icons
import { ReactComponent as ContinueIcon } from "../../assets/icons/ArrowContinue.svg";

// Context
import { useMasterFormDispatch, useMasterForm } from "../../Context/MainForm";

// utils
import { validationReducer, defaultState } from "./AddressValidation.reducer";
import { completeDataModel } from "./AddressValidation.utils";
import { flasher } from "../../utils/Flasher";
import { closeDataModel } from "../../utils/closeModel";

// types
import { personObject } from "../../Types/api";

ReactModal.setAppElement("body");

const AddressValidation: FC<RouteComponentProps> = ({ match }) => {
  const [{ isOpen, rejectionMessage }, setModalState] = useState({
    isOpen: false,
    rejectionMessage: "",
  });
  const history = useHistory();
  const {
    addressValNotMatch,
    step,
    startModelResponse,
    partner,
  } = useMasterForm();
  const person = startModelResponse?.person || [];
  const mainFormDispatcher = useMasterFormDispatch();
  const [{ selectedAddress }, validationDispatcher] = useReducer(
    validationReducer,
    defaultState
  );
  const { refetch, isLoading } = useQuery(
    ["completeDataModel", startModelResponse, person, selectedAddress, partner],
    completeDataModel,
    {
      enabled: false,
      onError: () => {
        flasher(
          "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
          "warning"
        );
        history.goBack();
      },
      onSettled: (modelRes) => {
        console.log(modelRes);
        if (modelRes && modelRes.name) {
          switch (modelRes.name) {
            case "ShowNotification":
              closeDataModel(modelRes.id, modelRes.version, +partner);
              setModalState({
                isOpen: true,
                rejectionMessage: `estatus ${modelRes.notificationStatus}, ${modelRes.notificationReason}`,
              });
              return;
            case "FullCapture":
              return goToFullCapture();
            default:
              flasher(
                "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
                "warning"
              );
          }
        } else {
          flasher(
            "Algo sali mal verificando tus datos intentalo de nuevo ms tarde",
            "warning"
          );
          history.goBack();
        }
      },
      retry: 1,
      refetchOnMount: true,
    }
  );

  const handleContinue = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    refetch();
  };

  const goToFullCapture = () => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.75 });
    history.push(match.url.replace("/valida-domicilio", "/captura-completa"));
  };

  useEffect(() => {
    if (step < 0.725) {
      history.goBack();
    }
  }, [step, history]);

  useEffect(() => {
    mainFormDispatcher({ type: "SET_CURRENT_STEP", step: 0.725 });
  }, [mainFormDispatcher]);

  const handleCheck = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (addressValNotMatch) {
      mainFormDispatcher({ type: "TOGGLE_ADDRESS_MATCH" });
    }
    validationDispatcher({
      type: "SET_SELECTED_ADDRESS_INDEX",
      value: +e.target.id,
    });
  };

  const handleNotMatchSelector = (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    if (!addressValNotMatch) {
      validationDispatcher({
        type: "SET_SELECTED_ADDRESS_INDEX",
        value: null,
      });
    }
    mainFormDispatcher({ type: "TOGGLE_ADDRESS_MATCH" });
  };

  return (
    <>
      <ReactModal
        isOpen={isOpen}
        className="modal-content"
        overlayClassName="modal-overlay"
        closeTimeoutMS={300}
      >
        <RejectionMessage msg={rejectionMessage} />
      </ReactModal>
      <Form onSubmit={(e) => e.preventDefault()}>
        <FormHeader>
          <FormTitle bolder style={{ marginTop: 0 }}>
            Valida tu domicilio
          </FormTitle>
        </FormHeader>
        <FormSubtitle>
          Vives o has vivido en alguna de estas direcciones?
        </FormSubtitle>
        <TableHeader>
          <h3>Estado</h3>
          <h3>Domicilio</h3>
          <h3>Coincide</h3>
        </TableHeader>
        {person?.slice(0, 5).map((add: personObject, i: number) => (
          <AddressTableRow
            key={i}
            first={i === 0}
            address={add}
            selected={selectedAddress === i}
            keyId={i}
            toggleFn={handleCheck}
          />
        ))}
        <TableFooter>
          <NotMatchSelector
            selected={!!addressValNotMatch}
            onClick={handleNotMatchSelector}
          >
            Domicilio No Coincide
          </NotMatchSelector>
        </TableFooter>
        {isLoading ? (
          <FetchingSpinnerContainer>
            <SmallSpinner />
          </FetchingSpinnerContainer>
        ) : (
          <ContinueBtn
            disabled={addressValNotMatch !== true && selectedAddress === null}
            onClick={handleContinue}
            style={{ marginTop: 10 }}
          >
            Continuar
            <ContinueIconWrapper>
              <ContinueIcon />
            </ContinueIconWrapper>
          </ContinueBtn>
        )}
      </Form>
    </>
  );
};

export default AddressValidation;
