type Action =
  | {
      type: "SET_PREVIEW_IMG";
      previewImg: string;
    }
  | {
      type: "TOGGLE_CAMERA";
    }
  | {
      type: "SET_PREVIEW_CAPTURE";
      capture: string;
    }
  | {
      type: "CANCEL_PREVIEW_CAPTURE";
    };

type State = {
  previewImg: string;
  cameraOpen: boolean;
  capturedPreview: string;
};

export const defaultState: State = {
  previewImg: "",
  cameraOpen: false,
  capturedPreview: "",
};

export const documentFormReducer = (
  state: State = defaultState,
  action: Action
) => {
  switch (action.type) {
    case "SET_PREVIEW_IMG":
      return { ...state, previewImg: action.previewImg };
    case "TOGGLE_CAMERA":
      return { ...state, cameraOpen: !state.cameraOpen };
    case "SET_PREVIEW_CAPTURE":
      return { ...state, capturedPreview: action.capture };
    case "CANCEL_PREVIEW_CAPTURE":
      return { ...state, capturedPreview: "" };
    default:
      return state;
  }
};
